import axios, { AxiosPromise } from 'axios';
import { ErrorEnum, errorParser } from './Errors';
import { ACT_JWT_TESTRESULT_CHANGED } from './reducers/appSettings';
import {store} from './index';
export const ACT_TABLEROWS_REQUEST = 'ACT_TABLEROWS_REQUEST';
export const ACT_TABLEROWS_RECEIVE = 'ACT_TABLEROWS_RECEIVE';
export const ACT_TABLEROWS_ERROR = 'ACT_TABLEROWS_ERROR';
export const ACT_SETCURRENTROW_INDEX = 'ACT_SETCURRENTROW_INDEX';
export const ACT_ROW_ADD = 'ACT_ROW_ADD';
export const ACT_ROW_UPDATE = 'ACT_ROW_UPDATE';
export const ACT_ROW_DELETE = 'ACT_ROW_DELETE';



export function GetRows(tblName: string, dispatch: any) {
    let result = (dispatch: any) => {
        if (dispatch) {
            dispatch({
                type: ACT_TABLEROWS_REQUEST,
                payload: { tableName: tblName }
            });
        }
        let prefix = "/api/" + tblName;
        //let apiUrl = prefix + tblName + "GetRows";
        let apiUrl = prefix;
        return axios({
            url: apiUrl,
            method: "GET",
            headers: { Authorization: "Bearer " + localStorage.lastJWT },
            //data: filter
        })
            .then(response => {
                return response.data;
            })
            .then((dataObj) => {
                if (dispatch) {
                    dispatch({
                        type: ACT_TABLEROWS_RECEIVE,
                        payload: { tableName: tblName, rows: dataObj, error: null }
                    });
                }
                let result: responseResult = { data: dataObj, success: true }
                return result;
            })
            .catch(err => {
                return commonErrHandling(err,tblName,()=>{
                    return GetRows(tblName,dispatch);
                });
            });
    }
    return doRequest(result, dispatch);
}

function commonErrHandling(err:any,tblName:string,repeatFunc:any){
    let parsedErr = errorParser(err);
    let result: responseResult = { data: parsedErr, success: false }
    let dispatch = store.dispatch;
    if (parsedErr.parsedErrId && parsedErr.parsedErrId === ErrorEnum.ERR401) {
        return RefreshJWT(dispatch)
            .then((res: any) => {
                if (res.success) {
                    //   refreshToken  ,   
                    return repeatFunc();
                } else {
                    dispatch({
                        type: ACT_TABLEROWS_ERROR,
                        payload: { tableName: tblName, rows: [], error: parsedErr }
                    });
                    return errorParser(result);
                }
            })
    } else {
        dispatch({
            type: ACT_TABLEROWS_ERROR,
            payload: { tableName: tblName, rows: [], error: parsedErr }
        });
        return errorParser(result);
    }    
}


export function RefreshJWT(dispatch: any) {
    if (!localStorage.lastJWT) {
        dispatch({
            type: ACT_JWT_TESTRESULT_CHANGED,
            payload: false
        });
        return returnFailResult();
    }
    let prefix = "/api/"
    let apiUrl = prefix + "account/refresh";
    return axios({
        url: apiUrl,
        method: "POST",
        headers: { Authorization: "Bearer " + localStorage.lastJWT },
    })
        .then((response) => {
            return response.data;
        })
        .then((dataObj) => {
            localStorage.lastJWT = dataObj.accessToken;
            dispatch({
                type: ACT_JWT_TESTRESULT_CHANGED,
                payload: true
            });
            console.log('refresh ok');
            let result: responseResult = { success: true, data: [] };
            return result;
        })
        .catch((err) => {
            dispatch({
                type: ACT_JWT_TESTRESULT_CHANGED,
                payload: false
            });
            let result: responseResult = { success: false, data: [] };
            return result;
        });
}


export function GetLookupRows(tblName: string, dispatch: any) {
    let result = (dispatch: any) => {
        let prefix = "/api/" + tblName;
        let apiUrl = prefix;
        return axios({
            url: apiUrl,
            method: "GET",
            headers: { Authorization: "Bearer " + localStorage.lastJWT },
        })
            .then(response => {
                return response.data;
            })
            .then((dataObj) => {
                if (dispatch) {
                    dispatch({
                        type: ACT_TABLEROWS_RECEIVE,
                        payload: { tableName: tblName, rows: dataObj, error: null }
                    });
                }
                let result: responseResult = { data: dataObj, success: true }
                return result;
            })
            .catch(err => {
                let parsedErr = errorParser(err);
                let result: responseResult = { data: parsedErr, success: false }
                if (dispatch) {
                    dispatch({
                        type: ACT_TABLEROWS_ERROR,
                        payload: { tableName: tblName, rows: [], error: err.toJSON() }
                    });
                }
                return errorParser(result);
            }
            );
    }
    return doRequest(result, dispatch);
}

export function AddRowRange(tblName: string, updatedRow: any, dispatch: any) {
    if (!Array.isArray(updatedRow)) {
        updatedRow = [updatedRow];
    }
    return AddRow(tblName, updatedRow, dispatch);
}

export function AddRow(tblName: string, updatedRow: any, dispatch: any) {
    let result = (dispatch: any) => {
        let prefix = "/api/" + tblName;
        let apiUrl = prefix;
        return axios({
            url: apiUrl,
            method: "POST",
            data: updatedRow,
            headers: { Authorization: "Bearer " + localStorage.lastJWT },
        })
            .then(response => {
                return response.data;
            })
            .then((dataObj) => {
                if (dispatch) {
                    dispatch({
                        type: ACT_ROW_ADD,
                        payload: { tableName: tblName, row: dataObj, error: null }
                    });
                }
                let result: responseResult = { data: dataObj, success: true }
                return result;
            })
            .catch(err => {
                return commonErrHandling(err,tblName,()=>{
                    return AddRow(tblName,updatedRow,dispatch);
                });
            });
    }
    return doRequest(result, dispatch);
}

export function UpdateRow(tblName: string, updatedRow: any, dispatch: any) {
    if (!Array.isArray(updatedRow)) {
        updatedRow = [updatedRow];
    }
    let result = (dispatch: any) => {
        let prefix = "/api/" + tblName;
        let apiUrl = prefix;
        return axios({
            url: apiUrl,
            method: "PUT",
            data: updatedRow,
            headers: { Authorization: "Bearer " + localStorage.lastJWT },
        })
            .then(response => {
                return updatedRow;
            })
            .then((dataObj) => {
                if (dispatch) {
                    dispatch({
                        type: ACT_ROW_UPDATE,
                        payload: { tableName: tblName, row: dataObj, error: null }
                    });
                }
                let result: responseResult = { data: dataObj, success: true }
                return result;
            })
            .catch(err => {
                return commonErrHandling(err,tblName,()=>{
                    return UpdateRow(tblName,updatedRow,dispatch);
                });
            });
    }
    return doRequest(result, dispatch);
}

export interface responseResult {
    data: any,
    success: boolean
}

function doRequest(func: any, dispatch: any) {
    if (dispatch) {
        return dispatch(func);
    }
    return func();
}


export function DeleteRow(tblName: string, rowUID: any, dispatch: any) {
    if (!Array.isArray(rowUID)) {
        rowUID = [rowUID];
    }
    let result = (dispatch: any) => {
        let prefix = "/api/" + tblName;
        let apiUrl = prefix;
        return axios({
            url: apiUrl,
            data: rowUID,
            method: "DELETE",
            headers: { Authorization: "Bearer " + localStorage.lastJWT },
        })
            .then(response => {
                return response.data;
            })
            .then((dataObj) => {
                if (dispatch) {
                    dispatch({
                        type: ACT_ROW_DELETE,
                        payload: { tableName: tblName, rowUid: rowUID, row: dataObj, error: null }
                    });
                }
                let result: responseResult = { data: dataObj, success: true }
                return result;
            })
            .catch(err => {
                return commonErrHandling(err,tblName,()=>{
                    return DeleteRow(tblName,rowUID,dispatch);
                });
            });
    }
    return doRequest(result, dispatch);
}

export function AddUser(userRow: any) {
    let apiUrl = "/api/account/signup";
    let result = () => axios({
        url: apiUrl,
        data: { Username: userRow.username, Password: userRow.password, User: userRow },
        method: "POST",
        headers: { Authorization: "Bearer " + localStorage.lastJWT },
    })
        .then(response => {
            return response.data;
        })
        .then((dataObj) => {
            let result: responseResult = { data: dataObj, success: true }
            return result;
        })
        .catch(err => {
            let parsedErr = errorParser(err);
            let result: responseResult = { data: parsedErr, success: false }
            return errorParser(result);
        });
    return doRequest(result, null);
}


export function returnOkResult() {
    let pr = new Promise((resolve) => {
        let okResult: responseResult = { success: true, data: [] };
        return resolve(okResult);
    });
    return pr.then();
}

export function returnFailResult() {
    let pr = new Promise((resolve) => {
        let result: responseResult = { success: false, data: [] };
        return resolve(result);
    });
    return pr.then();
}