import * as React from 'react';
import { Popup, Position, ToolbarItem } from 'devextreme-react/popup';
import { TextArea } from 'devextreme-react';

interface ConfirmFormProps {
    title: string,
    message: string,
    visible: boolean,
    confirmButtonText: string,
    confirmForm(): void,
    cancelButtonText: string,
    cancelForm(): void
}

export class ConfirmForm extends React.Component<ConfirmFormProps> {
    confirmButtonOptions: { text: string; onClick: () => void; };
    cancelButtonOptions: { text: string; onClick: () => void; };

    constructor(props: ConfirmFormProps) {
        super(props);

        this.confirmFormClick = this.confirmFormClick.bind(this);
        this.cancelFormClick = this.cancelFormClick.bind(this);

        this.confirmButtonOptions = {
            text: this.props.confirmButtonText,
            onClick: this.confirmFormClick,
        };

        this.cancelButtonOptions = {
            text: this.props.cancelButtonText,
            onClick: this.cancelFormClick,
        };
    }

    confirmFormClick() {
        this.props.confirmForm();
    }

    cancelFormClick() {
        this.props.cancelForm();
    }

    render() {
        return (
            <Popup
                visible={this.props.visible}
                dragEnabled={false}
                closeOnOutsideClick={false}
                showCloseButton={false}
                showTitle={true}
                width={500}
                height={300}
                title={this.props.title}
                container=".dx-viewport">
                <Position at="center" my="center" />

                <TextArea
                    height={150}
                    value={this.props.message}
                    readOnly={true}
                />

                <ToolbarItem location="after" toolbar="bottom" widget="dxButton" options={this.confirmButtonOptions}></ToolbarItem>
                <ToolbarItem location="after" toolbar="bottom" widget="dxButton" options={this.cancelButtonOptions}></ToolbarItem>
            </Popup>
        );
    }
}