import * as React from 'react';
import { Popup, Position } from 'devextreme-react/popup';
import axios, { AxiosPromise } from 'axios';
import { connect } from 'react-redux';
import * as dbwrap from '../DbWrapper';
import {ACT_JWT_TESTRESULT_CHANGED,ACT_ENABlEDGUIITEMS_CHANGED} from '../reducers/appSettings';

import Form, {
    Item,
    SimpleItem,
    Label,
} from 'devextreme-react/form';

import Button from 'devextreme-react/button';
import getLocMsg, { LocMsgId } from '../localization';
import { triggerHandler } from 'devextreme/events';
import { MessageType, errorParser, ErrorEnum, getProp } from '../Errors';
import { ErrorMsg, ErrorMsgProps, ErrorMsgFromDict } from '../ReusableComponents/DxStyleSimple';


interface ISessionSettings {
    lastLogin: string,
    lastRoleId: number,
    lastJWT: string;
}

interface ILoginDlgProps {
    JWTTestResult?:boolean
    actions: {
        SetJWTTestResult(newResult?:boolean):void,
        UpdateEnabledGuiItems(items:number[]):void
    }
}

interface ILoginDlgState {
    login: string,
    password: string,
    error: ErrorEnum,
}

const titleTmpl = (p1: any, p2: any) => {
    return (
        <div>
            <span className="dx-icon-key" style={{ fontSize: "xx-large", display: "flex" }} >
                <h3 className="dx-treelist-column-chooser" style={{ marginLeft: "20px" }}>{getLocMsg(LocMsgId.PopupTitle_Login)}</h3>
            </span>
        </div>
    );
}

export class LoginDialogClass extends React.Component<ILoginDlgProps, ILoginDlgState> {
    JWT: string = "";
    constructor(props: any) {
        super(props);
        let lastLogin = "";
        if (localStorage.lastLogin) {
            lastLogin = localStorage.lastLogin;
        }
        this.state = {
            login: lastLogin,
            password: "",
            error:ErrorEnum.NoError
        }

    }

    componentDidMount(){
        if (localStorage.lastJWT) {
            this.JWT = localStorage.lastJWT;
            this.isJWTAlive()
        } else {
            setTimeout(()=>{
                this.props.actions.SetJWTTestResult(false);
            },1000);
        }
    }

    isJWTAlive(){
        dbwrap.GetRows("Cities",null)
        .then((result:dbwrap.responseResult)=>{
            this.props.actions.SetJWTTestResult(result.success);
            return dbwrap.GetRows("VEnabledMenuObjects",null)
        })
        .then((result:dbwrap.responseResult)=>{
            this.setEnabledGuiItems(result);
        });
    }


    handleFieldChange(e: any, fieldName: string) {
        let newObj = {};
        let newFldValue = e.event.target.value;
        //@ts-ignore
        newObj[fieldName] = newFldValue;
        this.setState(newObj);
    }

    handleOkBtnClick() {
        this.setState({error:ErrorEnum.NoError});
        this.Login();
    }

    Login() {
        axios({
            url: "/api/Account/login",
            data: { username: this.state.login, password: this.state.password },
            method: "POST"
        })
            .then(response => {
                return response.data;
            })
            .then((dataObj) => {
                localStorage.lastLogin = this.state.login;
                localStorage.lastJWT = dataObj.accessToken;
                this.props.actions.SetJWTTestResult(true);
                this.setState({ error: ErrorEnum.NoError })
                return dbwrap.GetRows("VEnabledMenuObjects",null)
            })
            .then((result:dbwrap.responseResult)=>{
                this.setEnabledGuiItems(result);
            })
            .catch(err => {
                let parsedErr = errorParser(err);
                if (parsedErr.hasOwnProperty("parsedErrId")) {
                    let errId: ErrorEnum = getProp(parsedErr, "parsedErrId");
                    this.setState({ error: errId })
                    return;
                }            }
            );
    }

    setEnabledGuiItems(result:dbwrap.responseResult){
        if(!result.success){
            //this.setState({ error: result.data.errId })
            return;
        }
        this.props.actions.UpdateEnabledGuiItems(result.data);
    }


    render() {
        let visibility = this.props.JWTTestResult === false || this.props.JWTTestResult === undefined;
        let okBtnDisabled = (!this.state.login || !this.state.password);
        let ph = 220;
        if(this.state.error){
            ph=280;
        }
        return (
            <Popup
                visible={visibility}
                dragEnabled={false}
                closeOnOutsideClick={false}
                showCloseButton={false}
                showTitle={true}
                width={500}
                height={ph}
                titleRender={titleTmpl}
                container=".dx-viewport">
                <Position at="center" my="center"  />
                <Form formData={this.state}>
                    <SimpleItem dataField={"login"} editorType="dxTextBox"
                        editorOptions={{ onInput: (e: any) => this.handleFieldChange(e, "login") }}>
                        <Label text={getLocMsg(LocMsgId.FormField_Login)} />
                    </SimpleItem>
                    <SimpleItem dataField={"password"} editorType="dxTextBox"
                        editorOptions={{ onInput: (e: any) => this.handleFieldChange(e, "password"), mode: "password" }}>
                        <Label text={getLocMsg(LocMsgId.FormField_Password)} />
                    </SimpleItem>
                </Form>
                <div style={{ display: "flex", justifyContent: "flex-end", marginBottom: "10px", marginTop: "10px" }}>
                    <Button text={getLocMsg(LocMsgId.Button_Ok)} disabled={okBtnDisabled} onClick={() => this.handleOkBtnClick()} />
                </div>
                {ErrorMsgFromDict(this.state.error)}
            </Popup>
        );
    }
}


function mapStateToProps(storePar: any, ownProps?: any) {
    let result = { ...storePar.AppSettings };
    return result
}

function mapDispatchToProps(dispatch: any) {
    return {
        actions: {
            SetJWTTestResult(newResult?:boolean){
                dispatch({
                    type: ACT_JWT_TESTRESULT_CHANGED,
                    payload: newResult
                });

            },
            UpdateEnabledGuiItems(items:number[]){
                dispatch({
                    type:ACT_ENABlEDGUIITEMS_CHANGED,
                    payload:items
                });
            }
        }
    }
}


export const LoginDialog = connect(mapStateToProps, mapDispatchToProps)(LoginDialogClass);