import BooksImg from './Images/Books';
import CameraImg from './Images/Camera';
import ReportImg from './Images/report';
import EmptyImg from './Images/FSEmpty';
import DoneImg from './Images/FSDone';
import ProgressImg from './Images/FSProgress';
import ErrorImg from './Images/FSError';

export enum updateState{
    Unknown,
    New,
    InProcess,
    Complete,
    Error
}

export enum screenSizeEnum{
    ExtraSmall=767,
    Small=768,
    Medium=992,
    Large=1200
}

export function ScreenByWidth():screenSizeEnum{
    if(window.innerWidth >= screenSizeEnum.Large) return screenSizeEnum.Large;
    if(window.innerWidth >= screenSizeEnum.Medium) return screenSizeEnum.Medium;
    if(window.innerWidth >= screenSizeEnum.Small) return screenSizeEnum.Small;
    return screenSizeEnum.ExtraSmall;
}

export interface ColCountByScr{
    sm: number,
    md: number,
    lg: number    
}

export function GetCurrentColCountByScr(ccObj:ColCountByScr):number{
    let scrType = ScreenByWidth();
    switch (scrType) {
        case screenSizeEnum.Large:
            return ccObj.lg;
        case screenSizeEnum.Medium:
            return ccObj.md
        default:        
            return ccObj.sm
    }
}

export enum LoadingStage {
    undefined,
    loading,
    error,
    loaded
}

export const SidebarImages =
{
    Dictionary: BooksImg(32),
    Report : ReportImg(32)
}

export function GetUpdateStateImg(state:updateState,color:any) {
    switch (state){
        case updateState.New:
            return EmptyImg(24,color);
        case updateState.InProcess:
            return ProgressImg(24,color);
        case updateState.Complete:
            return DoneImg(24,color);
        case updateState.Error:
            return ErrorImg(24,color);
    }
}

export function GetUpdateTooltipText(state:updateState):string {
    switch (state){
        case updateState.New:
            return Tooltips.UpdateTaskStatus_New;
        case updateState.InProcess:
            return Tooltips.UpdateTaskStatus_InProcess;
        case updateState.Complete:
            return Tooltips.UpdateTaskStatus_Complete;
        case updateState.Error:
            return "";
        default:
            return "";
    }
}


