import React from 'react';
import {LangEnum} from '../localization';
import { Row } from 'react-bootstrap';
import { connect } from 'react-redux';
import { EnumColors } from '../commonTypes';
import {ACT_CHANGE_LANG} from '../reducers/appSettings';
import * as lngImg from '../Images/langImgs';
import { locale } from 'devextreme/localization';

interface SelLangProps {
    Language:LangEnum,
    actions:{
        ChangeLang(context:any):void
    }
} 

type MenuItemState ={
    hovered: boolean;
}

class SelectLng extends React.Component<SelLangProps,MenuItemState> {
    constructor(props:any) {
        super(props);
        this.state = {
            hovered: false
        };
    }

    componentDidMount() {
    }


    handleOnHover(isEnter:boolean) {
        this.setState({ hovered: isEnter });
    }

    getStyle():object {
        return {
            border: this.state.hovered ? "solid 1px" : "solid 1px",
            borderColor: this.state.hovered ? "red" : "white",
            borderRadius: "4px",
            margin: "1px",
            alignItems: "center",
            fontSize: "14px",
            width:"60px",
            height:"34px",
            position:"relative",
            top: "0px"
        };
    }


    renderImg(){
        switch (this.props.Language){
            case LangEnum.ru:
                return lngImg.RUlang(20);
            case LangEnum.kz:
                return lngImg.KZLang(20);
            case LangEnum.en:
                return lngImg.ENLang(20);
        }
    }
 
    render() {
        return (
            <div onMouseEnter={() => this.handleOnHover(true)} onMouseLeave={() => this.handleOnHover(false)} onClick={()=>this.props.actions.ChangeLang(this)}>               
                <Row style={this.getStyle()}>
                    <div style={{ width: "fit-content", marginLeft:"5px" }}>
                        {this.renderImg()}
                    </div>                    
                    <div style={{ textAlign: "center", borderRadius: "5px", margin: "5px" }}>
                        {this.props.Language.toString().toUpperCase()}
                    </div>
                </Row>
            </div>
        );
    }
}


function mapStateToProps(storePar:any,ownProps?:any) {
    let store = storePar;
    let result = store.AppSettings;
    return result
}

function mapDispatchToProps(dispatch:any) {
    return {
        actions: {
            ChangeLang: (jsxContext:any)=>{
                let currLang = jsxContext.props.Language;
                let newLang = LangEnum.ru;
                if(currLang === LangEnum.ru){
                    newLang = LangEnum.kz;
                }
                if(currLang === LangEnum.kz){
                    newLang = LangEnum.en;
                }
                dispatch({
                    type: ACT_CHANGE_LANG,
                    payload: newLang
                });

                locale(newLang);            //    DevExtreme
            }
        }
    }
}

export const SelectLang = connect(mapStateToProps, mapDispatchToProps)(SelectLng);