import React from 'react';
import FileUploader from 'devextreme-react/file-uploader';
import SelectBox from 'devextreme-react/select-box';
import CheckBox from 'devextreme-react/check-box';
import {errorParser,ErrorEnum,MessageType} from '../Errors';
import {ErrorMsg,ErrorMsgYesNo,ErrorMsgProps} from '../ReusableComponents/DxStyleSimple';
import axios, { AxiosPromise } from 'axios';

interface UploadTupPageState {
    selectedFiles:string[],
    errFileAlreadyExists:boolean,
    otherError:string
}

const fileUplControllerUrl = "/files/UpdatePackages";

export class UploadTupPage extends React.Component<any, UploadTupPageState> {
    constructor(props:UploadTupPageState) {
        super(props);
    
        this.state = {
          selectedFiles: [],
          errFileAlreadyExists:false,
          otherError:""
        };
    
        this.onSelectedFilesChanged = this.onSelectedFilesChanged.bind(this);
        this.onUploadError = this.onUploadError.bind(this);
        this.deleteFileFromDatabase = this.deleteFileFromDatabase.bind(this);
    }

    onSelectedFilesChanged(e:any) {
        this.setState({ selectedFiles: e.value,otherError:"" });
    }

    onUploadError(e:any){
        try {
            let perr = errorParser(e.error);
            if(perr.parsedErrObject){
              this.setState({otherError:perr.parsedErrObject.message});
              return;
            }
            let err = JSON.parse(e.error.response);
            perr = errorParser(err);
            if(perr.parsedErrObject && perr.parsedErrId === ErrorEnum.MSSQL_2601){
                this.setState({errFileAlreadyExists:true});
            } 
        }
        catch(ee){

        }
    }

    deleteFileFromDatabase(){
      let file:any = this.state.selectedFiles[0];
      axios({
        url: fileUplControllerUrl,
        params:{fileName:file.name},
        method: "DELETE"
      })
      .then(response=>{
        // ,   
      })
      .catch(err=>{
        let errStr:string = JSON.stringify(err);
        this.setState({otherError:errStr});
      })
    }
    
    renderFileAlreadyExistsError(){
        if(this.state.errFileAlreadyExists){
            let file:any = this.state.selectedFiles[0];
            let err:ErrorMsgProps = {message:" "+file.name+"  , ?",type:MessageType.error};
            return ErrorMsgYesNo(err,(result:boolean)=>{
                if(result){
                  //  ""
                  this.deleteFileFromDatabase(); 
                }
                this.setState({errFileAlreadyExists:false});
            });
        }
    }

    renderOtherError(){
      if(this.state.otherError){
        return ErrorMsg({message:this.state.otherError,type:MessageType.error});
      }
    }
    
    render() {
        return (
          <div>
            <div className="widget-container">
              <FileUploader
                multiple={false}
                accept="*"
                uploadMode="instantly"
                uploadUrl={fileUplControllerUrl}
                onUploadError={this.onUploadError}
                onValueChanged={this.onSelectedFilesChanged} />
              <div className="content" style={{ display: this.state.selectedFiles.length > 0 ? 'block' : 'none' }}>
                <div>
                  <h4> </h4>
                  {
                    this.state.selectedFiles.map((file:any, i:number) => <div key={i}>
                      <span>{`: ${file.name}`}<br /></span>
                      <span>{` ${file.size}`}<br /></span>
                      <span>{` ${file.size}`}<br /></span>
                      <span>{`  : ${file.lastModifiedDate}`}</span>
                    </div>)
                  }
                </div>
              </div>
            </div>
            {this.renderFileAlreadyExistsError()}
            {this.renderOtherError()}
          </div>
        );
      }    

}

