import * as React from 'react';
import { connect } from 'react-redux';
import * as dbwrap from '../../DbWrapper';
import DataGrid, {
    Column,
    FilterRow,
    HeaderFilter,
    SearchPanel,
    Sorting,
    Pager,
    Paging
} from 'devextreme-react/data-grid';
import Button from 'devextreme-react/button';
import { MessagePopup } from '../../components/CommonComponents/MessagePopup';
import TextBox from 'devextreme-react/text-box';
import ResponsiveBox, { Row, Col, Item, Location } from 'devextreme-react/responsive-box';
import { Popup, ToolbarItem } from 'devextreme-react/popup';

import getLabelText from './Labels';

import { Model_ListUser, Model_GroupUser, ModelExt_MailingGroupUserRelation } from './Model';
import { MailingGroupUser } from '../../Models/MailingGroupUser';
import { MailingGroupUserRelation } from '../../Models/MailingGroupUserRelation';
import { IncorrectData } from './IncorrectData';
import { TextArea, Toast } from 'devextreme-react';

interface Props { }

type State = {
    dataGroupUser: Model_GroupUser[],

    currentItem_GroupUser: { model: Model_GroupUser, rowKey: number },                               //       Grid,   ,      
    isNeedToSave: boolean,                                                                           //,      .   True,        .
    isNew: boolean,                                                                                  //   
    prevCurrentItem_GroupUser?: { model: Model_GroupUser, rowKey: number },                          //        Grid,            

    currentItem_ListUser: { model: Model_ListUser, rowKey: number },                                 //       Grid,   ,      
    currentItem_GroupUserRelation: { model: ModelExt_MailingGroupUserRelation, rowKey: number },     //       Grid,   ,      

    listUser: Model_ListUser[],                                                                      //  

    listUsers: Model_ListUser[],                                                                     //           

    //        
    confirmFormVisible: boolean,
    confirmFormTitle: string,
    confirmFormMessage: string,
    confirmFormOkButtonText: string,

    confirmFormOkButton?: any,
    confirmFormCancelButtonText: string,
    confirmFormCancelButton?: any,

    popupTitle: string,                                           //   PopUp ,  
    popupMessage: string,                                         //   PopUp ,   
    popupVisible: boolean,                                        //    PopUp 
    popupVisibleForCriticalError: boolean,                        //    PopUp .    
    hasCriticalError: boolean,                                    //         

    dataIsCorrect: { groupName: IncorrectData },                  //   .         

    isVisibleEditArea: boolean,
    isVisibleButtonAddToGroup: boolean,
    isVisibleButtonRemoveFromGroup: boolean,

    refComponents: any,                                            //    
    isVisibleToastForEdit: boolean                                 //    ,      
}

export class MailingGroupUsersDictClass extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);

        this.state = {
            dataGroupUser: [],

            currentItem_GroupUser: {
                model: { id: 0, name: '', groupUserRelations: [] },
                rowKey: 0
            },
            isNeedToSave: false,
            isNew: false,

            currentItem_ListUser: {
                model: { id: 0, userName: '', isGroupEmail: false },
                rowKey: 0
            },

            currentItem_GroupUserRelation: {
                model: {
                    id: 0,
                    groupUserRelation: { id: 0, userId: 0, groupId: 0 }
                },
                rowKey: 0
            },

            listUser: [],
            listUsers: [],

            confirmFormVisible: false,
            confirmFormTitle: '',
            confirmFormMessage: '',
            confirmFormOkButtonText: '',
            confirmFormCancelButtonText: '',

            popupTitle: "",
            popupMessage: "",
            popupVisible: false,
            popupVisibleForCriticalError: false,
            hasCriticalError: false,

            dataIsCorrect: {
                groupName: { isValid: true }
            },

            isVisibleEditArea: false,

            isVisibleButtonAddToGroup: false,
            isVisibleButtonRemoveFromGroup: false,

            isVisibleToastForEdit: false,

            refComponents: new Map()
        };

        this.onResize = this.onResize.bind(this)

        this.handleButtonSaveClick = this.handleButtonSaveClick.bind(this);
        this.handleButtonRefreshClick = this.handleButtonRefreshClick.bind(this);
        this.handleButtonRevertClick = this.handleButtonRevertClick.bind(this);

        this.closeConfirmSaveFormClick = this.closeConfirmSaveFormClick.bind(this);
        this.confirmSaveFormClick = this.confirmSaveFormClick.bind(this);

        this.closeConfirmDeleteFormClick = this.closeConfirmDeleteFormClick.bind(this);
        this.confirmDeleteFormClick = this.confirmDeleteFormClick.bind(this);

        this.applyChange_GroupUser = this.applyChange_GroupUser.bind(this);
        this.changeCurrentRow_GroupUser = this.changeCurrentRow_GroupUser.bind(this);
        this.onChangeGroupName = this.onChangeGroupName.bind(this);

        this.hidePopup = this.hidePopup.bind(this);
        this.hidePopupForCriticalError = this.hidePopupForCriticalError.bind(this);

        this.handleButtonNewClick = this.handleButtonNewClick.bind(this);
        this.handleButtonDeleteClick = this.handleButtonDeleteClick.bind(this);
        this.onRowClick_GroupUser = this.onRowClick_GroupUser.bind(this);

        this.onFocusedRowChanged_GroupUser = this.onFocusedRowChanged_GroupUser.bind(this);
        this.onContentReady_GroupUser = this.onContentReady_GroupUser.bind(this);
        this.onFocusedRowChanging_GroupUser = this.onFocusedRowChanging_GroupUser.bind(this);

        this.onFocusedRowChanged_ListUser = this.onFocusedRowChanged_ListUser.bind(this);
        this.onContentReady_ListUser = this.onContentReady_ListUser.bind(this);

        this.onFocusedRowChanged_GroupUserRelation = this.onFocusedRowChanged_GroupUserRelation.bind(this);
        this.onContentReady_GroupUserRelation = this.onContentReady_GroupUserRelation.bind(this);

        this.handleButtonAddToGroupClick = this.handleButtonAddToGroupClick.bind(this);
        this.handleButtonRemoveFromGroupClick = this.handleButtonRemoveFromGroupClick.bind(this);

        this.onToastHiding = this.onToastHiding.bind(this)
    }

    //      
    confirmSaveFormVisible() {
        this.setState({
            confirmFormVisible: true,
            confirmFormTitle: getLabelText('Message_Confirm'),
            confirmFormMessage: getLabelText('Message_ConfirmSave'),
            confirmFormOkButtonText: getLabelText('Button_Save'),
            confirmFormOkButton: this.confirmSaveFormClick,
            confirmFormCancelButtonText: getLabelText('Button_Cancel'),
            confirmFormCancelButton: this.closeConfirmSaveFormClick
        })
    }
    //      
    confirmDeleteFormVisible() {
        this.setState({
            confirmFormVisible: true,
            confirmFormTitle: getLabelText('Message_Confirm'),
            confirmFormMessage: getLabelText('Message_ConfirmDelete') + "\n\r" + getLabelText('GroupsName') + ": " + this.state.currentItem_GroupUser.model.name,
            confirmFormOkButtonText: getLabelText('Button_Yes'),
            confirmFormOkButton: this.confirmDeleteFormClick,
            confirmFormCancelButtonText: getLabelText('Button_No'),
            confirmFormCancelButton: this.closeConfirmDeleteFormClick
        })
    }

    onResize() {
        this.setState({ dataGroupUser: this.state.dataGroupUser });         // ,       
    }

    setUserName() {
        if (
            this.state.dataGroupUser == null
        ) { return }

        for (let i = 0; i < this.state.dataGroupUser.length; i++) {
            let group = this.state.dataGroupUser[i]
            group.groupUserRelations = []

            if (group.mailingGroupUserRelations != null) {
                for (let j = 0; j < group.mailingGroupUserRelations.length; j++) {
                    let relation = group.mailingGroupUserRelations[j]

                    let user = this.state.listUsers.find(x => x.id === relation.userId)

                    if (user != null) {
                        group.groupUserRelations.push(
                            {
                                id: relation.userId,                                        //        userId
                                groupUserRelation: relation, userName: user.userName
                            }
                        )
                    }
                }
            }
        }
    }

    componentDidMount() {
        (async () => {
            await dbwrap.GetRows("MailingListUsers", null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.setState({ listUsers: data.data });
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisibleForCriticalError: true
                    });

                    return
                }

                (async () => {
                    await dbwrap.GetRows("MailingGroupUsers", null).then((data: dbwrap.responseResult) => {
                        if (data.success) {

                            this.setState({ dataGroupUser: data.data });

                            this.setUserName();                                     //       

                            window.addEventListener('resize', this.onResize)        //   Window Resize
                            this.render()                                           //  

                        }
                        else {
                            let errorMessage: string = ''

                            if (data.data.parsedErrObject == null) {
                                errorMessage = data.data.request.responseText
                            }
                            else {
                                errorMessage = data.data.parsedErrObject.message
                            }

                            this.setState({
                                popupTitle: getLabelText('PopupTitle_Error'),
                                popupMessage: errorMessage,
                                popupVisibleForCriticalError: true
                            });
                        }
                    });
                })();
            });
        })();
    }

    getListUser(): any {
        let listUsers: Model_ListUser[]

        listUsers = { ...this.state.listUsers }     //  ,  ,         "" 

        listUsers = Object.values(listUsers)

        if (listUsers != null) {
            for (let i = 0; i < this.state.currentItem_GroupUser.model.groupUserRelations.length; i++) {
                let index = listUsers.findIndex(x => x.id === this.state.currentItem_GroupUser.model.groupUserRelations[i].groupUserRelation.userId)

                if (index >= 0) {
                    listUsers.splice(index, 1);
                }
            }
        }

        return listUsers
    }

    getDeepClone_GroupUser(model: Model_GroupUser): Model_GroupUser {
        let newItem = { ...model }

        let newGroupUserRelations: ModelExt_MailingGroupUserRelation[] = []

        for (var i = 0; i < model.groupUserRelations.length; i++) {
            let item = model.groupUserRelations[i]

            newGroupUserRelations.push(
                {
                    id: item.id,
                    groupUserRelation: { ...model.groupUserRelations[i].groupUserRelation },
                    userName: item.userName
                }
            )
        }

        newItem.groupUserRelations = newGroupUserRelations

        return newItem
    }

    changeCurrentRow_GroupUser(e: Model_GroupUser) {
        this.setState({ isNew: false })             //  ,     False  isNew

        if (e == null) { return }

        let dataRow = e

        this.setState({
            currentItem_GroupUser: {
                model: this.getDeepClone_GroupUser(dataRow),
                rowKey: dataRow.id
            },
        })

        this.setState({
            prevCurrentItem_GroupUser: {
                model: this.state.currentItem_GroupUser.model,
                rowKey: this.state.currentItem_GroupUser.rowKey
            },
        })

        this.setState({ isNeedToSave: false })

        this.setState({ listUser: this.getListUser() })

        this.resetDataForCorrect()          //    

        this.clearFilters()                 //     
    }

    clearFilters() {
        if (this.state.refComponents.dgdListUser != null) {
            this.state.refComponents.dgdListUser.instance.clearFilter()
        }

        if (this.state.refComponents.dgdGroupUserRelation != null) {
            this.state.refComponents.dgdGroupUserRelation.instance.clearFilter()
        }
    }

    changeCurrentRow_ListUser(e: Model_ListUser) {
        if (e == null) { return }

        const dataRow = e;

        this.setState({
            currentItem_ListUser: {
                model: { ...dataRow },
                rowKey: dataRow.id
            },
        })
    }
    changeCurrentRow_GroupUserRelation(e: ModelExt_MailingGroupUserRelation) {
        if (e == null) { return }

        const dataRow = e;

        this.setState({
            currentItem_GroupUserRelation: {
                model: { ...dataRow },
                rowKey: dataRow.id
            },
        })
    }

    onFocusedRowChanged_GroupUser(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }

        this.changeCurrentRow_GroupUser(e.row && e.row.data)
    }
    onFocusedRowChanging_GroupUser(e: any) {
        if (this.state.isNeedToSave) {
            e.cancel = true;

            return
        }
    }

    onFocusedRowChanged_ListUser(e: any) {
        this.changeCurrentRow_ListUser(e.row && e.row.data)
    }
    onFocusedRowChanged_GroupUserRelation(e: any) {
        this.changeCurrentRow_GroupUserRelation(e.row && e.row.data)
    }

    onContentReady_GroupUser(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }


        if (
            (this.state.currentItem_GroupUser == null || this.state.currentItem_GroupUser.model.id === 0)
            &&
            (this.state.prevCurrentItem_GroupUser != null && this.state.prevCurrentItem_GroupUser.model.id > 0)
        ) {
            this.setState({ currentItem_GroupUser: { model: this.getDeepClone_GroupUser(this.state.prevCurrentItem_GroupUser.model), rowKey: this.state.prevCurrentItem_GroupUser.model.id } })
        }

        let item = this.state.currentItem_GroupUser.model
        let key = item.id
        let keyExistsInFilteredRows = -1

        this.onFocusedRowChanged_GroupUser(item)                                           //       , ,     
        if (this.state.isNeedToSave) { return }                                            //  isNeedToSave     

        this.setState({ currentItem_GroupUser: { model: this.getDeepClone_GroupUser(item), rowKey: key } })      //      

        let dgdGroupUser = this.state.refComponents.dgdGroupUser

        if (item != null && dgdGroupUser != null) {
            dgdGroupUser.instance.getDataSource().store()                                      //          .
                .load({ filter: dgdGroupUser.instance.getCombinedFilter() })
                .then((result: any) => {
                    let filteredRows = result;                                                 //     filteredRows

                    keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)      //        
                });
        }

        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {                //            Id,    
            if (dgdGroupUser != null) {
                key = dgdGroupUser.instance.getKeyByRowIndex(0)                                // Key    (  -    )
            }
        }

        if (key != null && dgdGroupUser != null) {
            dgdGroupUser.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    let filteredRows = result;

                    item = filteredRows.find((x: any) => x.id === key)
                });
        }

        this.setState({ currentItem_GroupUser: { model: this.getDeepClone_GroupUser(item), rowKey: key } })
        this.setState({ listUser: this.getListUser() })

        //,   ,    
        if (dgdGroupUser != null) {
            dgdGroupUser.instance.getDataSource().store()                                      //          .
                .load({ filter: dgdGroupUser.instance.getCombinedFilter() })
                .then((result: any) => {
                    let filteredRows = result;                                                 //     filteredRows

                    if (filteredRows.length === 0) {
                        this.setState({ isVisibleEditArea: false })
                        this.setState({ listUser: [] })
                        this.setState({
                            currentItem_GroupUser: {
                                model: { id: 0, name: '', groupUserRelations: [] },
                                rowKey: 0
                            }
                        })
                    }
                    else {
                        this.setState({ isVisibleEditArea: true })
                    }
                });
        }
    }
    onContentReady_ListUser(e: any) {
        let item = this.state.currentItem_ListUser.model
        let key = item.id
        let keyExistsInFilteredRows = -1

        this.onFocusedRowChanged_ListUser(item)                                           //       , ,     

        this.setState({ currentItem_ListUser: { model: { ...item }, rowKey: key } })      //      

        let dgdListUser = this.state.refComponents.dgdListUser

        if (item != null && dgdListUser != null) {
            dgdListUser.instance.getDataSource().store()                                     //          .
                .load({ filter: dgdListUser.instance.getCombinedFilter() })
                .then((result: any) => {
                    let filteredRows = result;                                                 //     filteredRows

                    keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)          //        
                });
        }

        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {                //            Id,    
            if (dgdListUser != null) {
                key = dgdListUser.instance.getKeyByRowIndex(0)                                // Key    (  -    )
            }
        }

        if (key != null && dgdListUser != null) {
            dgdListUser.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    let filteredRows = result;

                    item = filteredRows.find((x: any) => x.id === key)
                });
        }

        this.setState({ currentItem_ListUser: { model: { ...item }, rowKey: key } })

        //,   ,      
        if (dgdListUser != null) {
            dgdListUser.instance.getDataSource().store()                                      //          .
                .load({ filter: dgdListUser.instance.getCombinedFilter() })
                .then((result: any) => {
                    let filteredRows = result;                                                 //     filteredRows

                    if (filteredRows.length === 0) {
                        this.setState({ isVisibleButtonAddToGroup: false })
                    }
                    else {
                        this.setState({ isVisibleButtonAddToGroup: true })
                    }
                });
        }
    }
    onContentReady_GroupUserRelation(e: any) {
        if (this.state.currentItem_GroupUserRelation.model == null || this.state.currentItem_GroupUserRelation.model.groupUserRelation == null) { return }

        let item = this.state.currentItem_GroupUserRelation.model
        let key = item.id
        let keyExistsInFilteredRows = -1

        this.onFocusedRowChanged_GroupUserRelation(item)

        this.setState({ currentItem_GroupUserRelation: { model: { ...item }, rowKey: key } })                //      

        let dgdGroupUserRelation = this.state.refComponents.dgdGroupUserRelation

        if (item != null && dgdGroupUserRelation != null) {
            dgdGroupUserRelation.instance.getDataSource().store()                                    //          .
                .load({ filter: dgdGroupUserRelation.instance.getCombinedFilter() })
                .then((result: any) => {
                    let filteredRows = result;                                                                   //     filteredRows

                    keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)                            //        
                });
        }

        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {                              //            Id,    
            if (dgdGroupUserRelation != null) {
                key = dgdGroupUserRelation.instance.getKeyByRowIndex(0)                                      // Key    (  -    )
            }
        }

        if (key != null && dgdGroupUserRelation != null) {
            dgdGroupUserRelation.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    let filteredRows = result;

                    item = filteredRows.find((x: any) => x.id === key)
                });
        }

        this.setState({ currentItem_GroupUserRelation: { model: { ...item }, rowKey: key } })

        //,   ,      
        if (dgdGroupUserRelation != null) {
            dgdGroupUserRelation.instance.getDataSource().store()                                      //          .
                .load({ filter: dgdGroupUserRelation.instance.getCombinedFilter() })
                .then((result: any) => {
                    let filteredRows = result;                                                 //     filteredRows

                    if (filteredRows.length === 0) {
                        this.setState({ isVisibleButtonRemoveFromGroup: false })
                    }
                    else {
                        this.setState({ isVisibleButtonRemoveFromGroup: true })
                    }
                });
        }
    }

    onRowClick_GroupUser(e: any) {
        if (this.state.currentItem_GroupUser.model.id === e.data.id) { return }

        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        };

        if (this.state.currentItem_GroupUser.model.id === 0 || this.state.currentItem_GroupUser.model.id == null) {
            this.changeCurrentRow_GroupUser(e.data)
        }
    }

    equalGroupUsers(newItem: Model_GroupUser, oldItem: Model_GroupUser): boolean {
        if (newItem == null || oldItem == null) { return false }

        let result: boolean = true

        if (newItem.name != oldItem.name) {
            return false
        }

        if (newItem.groupUserRelations.length != oldItem.groupUserRelations.length) {
            return false
        }
        else {
            for (let i = 0; i < oldItem.groupUserRelations.length; i++) {
                if (newItem.groupUserRelations.findIndex(x => x.groupUserRelation.userId === oldItem.groupUserRelations[i].groupUserRelation.userId) === -1) {
                    return false
                }
            }
        }

        return result
    }
    applyChange_GroupUser(target: any) {
        if (this.state.isVisibleEditArea === false) { return }

        if (target == null) {
            target = this.state.currentItem_GroupUser.model
        }

        let item = this.state.dataGroupUser.find(x => x.id === target.id)

        let hasChanged: boolean = true

        if (item != null) {
            hasChanged = !this.equalGroupUsers(target, item)
        }

        this.setState({ currentItem_GroupUser: { model: target, rowKey: this.state.currentItem_GroupUser.rowKey } })       //  

        this.setState({ isNeedToSave: hasChanged })                                                                        //  " "
    }
    onChangeGroupName = (e: any) => {
        var changedItem = this.getDeepClone_GroupUser(this.state.currentItem_GroupUser.model);

        changedItem.name = e.value;

        this.applyChange_GroupUser(changedItem);
    }

    //  
    handleButtonNewClick() {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        };

        if (this.state.currentItem_GroupUser != null && this.state.currentItem_GroupUser.model.id != 0) {
            this.setState({ prevCurrentItem_GroupUser: { ...this.state.currentItem_GroupUser } })              //   
        }

        var newItem: Model_GroupUser = { id: 0, name: '', groupUserRelations: [] }

        this.setState({ currentItem_GroupUser: { model: newItem, rowKey: newItem.id } })
        this.setState({ isNew: true, isVisibleEditArea: true })
        this.setState({ listUser: this.getListUser() })

        this.resetDataForCorrect()

        let first = this.state.refComponents.txtGroupName

        if (first != null) {
            first.instance.focus()        //     
        }
    }
    // 
    handleButtonDeleteClick() {
        if (this.state.dataGroupUser.find(x => x.id === this.state.currentItem_GroupUser.model.id) == null) { return }

        this.confirmDeleteFormVisible()             //    .
    }
    //  
    handleButtonSaveClick() {
        if (this.state.currentItem_GroupUser.model.id === 0 && this.state.isNew === false) {
            this.setState({ isNeedToSave: false })
            return
        }

        if (this.state.isNeedToSave) {
            this.confirmSaveFormVisible()           //    .
        };
    }
    //  
    handleButtonAddToGroupClick() {
        if (
            this.state.currentItem_GroupUser == null ||
            this.state.currentItem_GroupUser.model == null ||

            this.state.currentItem_ListUser == null ||
            this.state.currentItem_ListUser.model == null ||
            this.state.currentItem_ListUser.model.id === 0
        ) { return }

        let group = this.state.currentItem_GroupUser.model
        let groupId = this.state.currentItem_GroupUser.model.id
        let userId = this.state.currentItem_ListUser.model.id
        let userName = this.state.currentItem_ListUser.model.userName

        if (group.groupUserRelations.findIndex(x => x.groupUserRelation.groupId === groupId && x.groupUserRelation.userId === userId) === -1) {

            group.groupUserRelations.push({
                id: userId,
                groupUserRelation: {
                    id: 0,
                    userId: userId,
                    groupId: groupId
                },
                userName: userName
            })

            this.setState({ currentItem_GroupUser: { model: group, rowKey: this.state.currentItem_GroupUser.rowKey } })

            let dgdGroupUserRelation = this.state.refComponents.dgdGroupUserRelation

            dgdGroupUserRelation.instance.refresh()

            this.setState({ listUser: this.getListUser() })
            this.setState({ isNeedToSave: true })
        }

        this.applyChange_GroupUser(undefined)                   //   
    }
    //  
    handleButtonRemoveFromGroupClick() {
        if (
            this.state.currentItem_GroupUserRelation == null ||
            this.state.currentItem_GroupUserRelation.model == null ||
            this.state.currentItem_GroupUserRelation.model.groupUserRelation == null
        ) { return }

        let relation = this.state.currentItem_GroupUserRelation.model

        let group = this.state.currentItem_GroupUser.model

        let index = group.groupUserRelations.findIndex(x => x.id === relation.id)

        group.groupUserRelations.splice(index, 1);

        let dgdGroupUserRelation = this.state.refComponents.dgdGroupUserRelation

        dgdGroupUserRelation.instance.refresh()

        this.setState({ listUser: this.getListUser() })
        this.setState({ isNeedToSave: true })

        this.applyChange_GroupUser(undefined)                   //   
    }

    //   
    resetDataForCorrect() {
        this.setState({ dataIsCorrect: { groupName: { isValid: true, message: '' } } })
    }

    //    ,    
    checkDataForCorrect(model: Model_GroupUser): boolean {
        let name = model.name == null ? '' : model.name.trim()

        let groupNameIsValid = name != null && name != '' && name != undefined
        let groupNameMessage = groupNameIsValid ? '' : getLabelText('GroupName_Incorrect')

        if (groupNameIsValid) {
            groupNameIsValid = this.state.dataGroupUser.find(x => x.name.toUpperCase() === name.toUpperCase() && x.id != model.id) === undefined
            groupNameMessage = groupNameIsValid ? '' : getLabelText('GroupName_IsDublicate')
        }

        this.setState({ dataIsCorrect: { groupName: { isValid: groupNameIsValid, message: groupNameMessage } } })

        if (groupNameIsValid === false) {
            let comp = this.state.refComponents.txtGroupName

            if (comp != null) {
                comp.instance.focus()
            }
        }

        return groupNameIsValid     // 
    }
    //     .
    confirmSaveFormClick() {
        var item = this.state.currentItem_GroupUser.model

        //       
        if (this.checkDataForCorrect(item) === false) {
            this.setState({ confirmFormVisible: false });

            return
        }

        let group: MailingGroupUser;
        let groupUserRelations: MailingGroupUserRelation[] = [];

        let relations = this.state.currentItem_GroupUser.model.groupUserRelations

        for (let i = 0; i < relations.length; i++) {
            groupUserRelations.push({
                id: relations[i].groupUserRelation.id,
                groupId: relations[i].groupUserRelation.groupId,
                userId: relations[i].groupUserRelation.userId
            })
        }

        group = {
            id: item.id,
            name: item.name == null ? '' : item.name.trim(),
            mailingGroupUserRelations: groupUserRelations
        }

        if (this.state.currentItem_GroupUser.model.id === 0) {

            dbwrap.AddRow("MailingGroupUsers", group, null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    var currentItem = this.state.currentItem_GroupUser.model
                    currentItem.id = data.data.id

                    this.setState({ prevCurrentItem_GroupUser: { model: this.state.currentItem_GroupUser.model, rowKey: this.state.currentItem_GroupUser.rowKey } })

                    this.handleButtonRefreshClick();
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisible: true
                    });
                }
            })
        }
        else {
            dbwrap.UpdateRow("MailingGroupUsers", group, null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.handleButtonRefreshClick();
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisible: true
                    });
                }
            })
        }

        this.closeConfirmSaveFormClick('saved');
    }
    //    .
    closeConfirmSaveFormClick(action: string) {
        if (action != 'saved') { action = 'cancel' }

        this.setState({
            confirmFormVisible: false,
            isNeedToSave: false
        });

        if (this.state.currentItem_GroupUser != null && this.state.currentItem_GroupUser != undefined && action == 'cancel') {
            var item = this.state.dataGroupUser.find(x => x.id === this.state.currentItem_GroupUser.model.id)

            if (item != null && item != undefined) {
                this.setState({ currentItem_GroupUser: { model: this.getDeepClone_GroupUser(item), rowKey: this.state.currentItem_GroupUser.rowKey } })
                this.setState({ listUser: this.getListUser() })
            }
        }

        //         ,   ,         (  )
        if (this.state.currentItem_GroupUser.model.id === 0) {
            this.setState({ currentItem_GroupUser: { model: { id: 0, name: '', groupUserRelations: [] }, rowKey: 0 } })
            this.setState({ listUser: this.getListUser() })

            if (this.state.isNew && this.state.prevCurrentItem_GroupUser != null) {
                this.setState({ currentItem_GroupUser: { ...this.state.prevCurrentItem_GroupUser } })
            }

            this.setState({ isNew: false, isNeedToSave: false })
        }

        this.resetDataForCorrect()                                  //    

        this.handleButtonRefreshClick()

        this.clearFilters()
    }
    //     .
    confirmDeleteFormClick() {
        var item = this.state.currentItem_GroupUser.model

        dbwrap.DeleteRow("MailingGroupUsers", item.id, null).then((data: dbwrap.responseResult) => {
            if (data.success) {
                this.handleButtonRefreshClick();
            }
            else {
                let errorMessage: string = ''

                if (data.data.parsedErrObject == null) {
                    errorMessage = data.data.request.responseText
                }
                else {
                    errorMessage = data.data.parsedErrObject.message
                }

                this.setState({
                    popupTitle: getLabelText('PopupTitle_Error'),
                    popupMessage: errorMessage,
                    popupVisible: true
                });
            }
        })

        this.resetDataForCorrect()          //    

        this.closeConfirmDeleteFormClick();
    }
    //    .
    closeConfirmDeleteFormClick() {

        this.setState({
            confirmFormVisible: false,
            isNeedToSave: false
        });

        this.handleButtonRefreshClick()

        //    ,    
        if (this.state.dataGroupUser.length <= 1) {
            this.setState(
                {
                    currentItem_GroupUser: { model: { id: 0, name: '', groupUserRelations: [] }, rowKey: 0 },
                    isNew: false,
                    isNeedToSave: false
                }
            )

            this.setState({ listUser: this.getListUser() })
        }

        this.resetDataForCorrect()          //    
    }
    setToPrevRow() {
        this.setState({ isVisibleToastForEdit: true })

        let dgdGroupUser = this.state.refComponents.dgdGroupUser

        if (dgdGroupUser != null) {
            if (this.state.prevCurrentItem_GroupUser != null) {
                dgdGroupUser.instance.navigateToRow(this.state.prevCurrentItem_GroupUser.model.id)
            }

            if (this.state.currentItem_GroupUser != null && this.state.currentItem_GroupUser.model.id != 0) {
                dgdGroupUser.instance.navigateToRow(this.state.currentItem_GroupUser.model.id)
            }
        }
    }
    onToastHiding() {
        this.setState({ isVisibleToastForEdit: false })
    }

    //  grid.
    handleButtonRefreshClick() {
        this.componentDidMount()
    }
    //   
    handleButtonRevertClick() {
        this.closeConfirmSaveFormClick('cancel')
    }
    //  popup.
    hidePopup() {
        this.setState({ popupVisible: false });
    }
    //  popup.    
    hidePopupForCriticalError() {
        this.setState({ popupVisibleForCriticalError: false });
        this.setState({ hasCriticalError: true });                      //True -       
    }

    render() {
        return (
            <div>
                <h3 style={{ width: "auto", textAlign: "center" }}>
                    {getLabelText('PageName')}
                </h3>

                <div hidden={this.state.hasCriticalError === false}>
                    <table width='100%' style={{ height: '80vh' }}>
                        <tr>
                            <td align='center'>
                                <h1>
                                    {getLabelText('Message_DirectoryNotAvailable')}
                                </h1>
                            </td>
                        </tr>
                    </table>
                </div>

                <div hidden={this.state.hasCriticalError}>
                    <Button icon="add" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNew || this.state.isNeedToSave} onClick={this.handleButtonNewClick} hint={getLabelText('Hint_Button_Add')} className='mb-2 mr-2' />
                    <Button icon="trash" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNew || this.state.isVisibleEditArea === false || this.state.isNeedToSave} onClick={this.handleButtonDeleteClick} hint={getLabelText('Hint_Button_Delete')} className='mb-2 mr-2' />
                    <Button icon="save" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNeedToSave === false} onClick={this.handleButtonSaveClick} hint={getLabelText('Hint_Button_Save')} className='mb-2 mr-2' />
                    <Button icon="refresh" activeStateEnabled={false} hoverStateEnabled={false} visible={this.state.isNeedToSave === false && this.state.isNew === false} disabled={this.state.isNew} onClick={this.handleButtonRefreshClick} hint={getLabelText('Hint_Button_Refresh')} className='mb-2 mr-2' />
                    <Button icon="revert" activeStateEnabled={false} hoverStateEnabled={false} visible={this.state.isNeedToSave || this.state.isNew} onClick={this.handleButtonRevertClick} hint={getLabelText('Hint_Button_Cancel')} className='mb-2 mr-2' />

                    <div hidden={this.state.isVisibleEditArea === false && this.state.isNew === false}>
                        <table width={"100%"}>
                            <tr>
                                <td width={"16%"} /><td width={"84%"} />
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('GroupsName') + ":"}
                                </td>
                                <td>
                                    <TextBox maxLength={100} value={this.state.currentItem_GroupUser.model.name} valueChangeEvent="input" onValueChanged={this.onChangeGroupName}
                                        isValid={this.state.dataIsCorrect.groupName.isValid} hint={this.state.dataIsCorrect.groupName.message}
                                        ref={(ref) => { this.state.refComponents["txtGroupName"] = ref }}                //    
                                    />
                                </td>
                            </tr>
                        </table>
                    </div>

                    <Popup
                        visible={this.state.confirmFormVisible}
                        dragEnabled={false}
                        closeOnOutsideClick={false}
                        showCloseButton={false}
                        showTitle={true}
                        width={500}
                        height={300}
                        title={this.state.confirmFormTitle}>

                        <TextArea
                            height={150}
                            value={this.state.confirmFormMessage}
                            readOnly={true}
                        />

                        <ToolbarItem location="after" toolbar="bottom" widget="dxButton" options={{ text: this.state.confirmFormOkButtonText, onClick: this.state.confirmFormOkButton }}></ToolbarItem>
                        <ToolbarItem location="after" toolbar="bottom" widget="dxButton" options={{ text: this.state.confirmFormCancelButtonText, onClick: this.state.confirmFormCancelButton }}></ToolbarItem>
                    </Popup>

                    <MessagePopup
                        visible={this.state.popupVisible}
                        title={this.state.popupTitle}
                        hidePopup={this.hidePopup}
                        message={this.state.popupMessage}
                    />
                    <MessagePopup
                        visible={this.state.popupVisibleForCriticalError}
                        title={this.state.popupTitle}
                        hidePopup={this.hidePopupForCriticalError}
                        message={this.state.popupMessage}
                    />

                    <br />
                    <ResponsiveBox>
                        <Row ratio={0.01} />
                        <Row ratio={1} />
                        <Row ratio={2} />

                        <Col ratio={1} />
                        <Col ratio={0.05} />

                        <Col ratio={1} />
                        <Col ratio={0.05} />

                        <Col ratio={0.5} />
                        <Col ratio={0.05} />

                        <Col ratio={1} />
                        <Item>
                            <Location row={0} col={0} />
                            <p><b>{getLabelText('Groups')}</b></p>
                        </Item>
                        <Item>
                            <Location row={0} col={2} />
                            <p><b>{getLabelText('AllUsers')}</b></p>
                        </Item>
                        <Item>
                            <Location row={0} col={6} />
                            <p><b>{getLabelText('GroupUsers')}</b></p>
                        </Item>
                        <Item>
                            <Location row={1} col={0} rowspan={2} />
                            <DataGrid
                                dataSource={this.state.dataGroupUser}
                                keyExpr="id"
                                showBorders={true}
                                focusedRowEnabled={true}
                                allowColumnResizing={true}
                                allowColumnReordering={true}
                                onFocusedRowChanged={this.onFocusedRowChanged_GroupUser}
                                onRowClick={this.onRowClick_GroupUser}
                                focusedRowKey={this.state.currentItem_GroupUser.rowKey}
                                onFocusedRowChanging={this.onFocusedRowChanging_GroupUser}

                                autoNavigateToFocusedRow={true}
                                onContentReady={this.onContentReady_GroupUser}
                                ref={(ref) => { this.state.refComponents["dgdGroupUser"] = ref }}

                                height={window.innerHeight - 270}
                            >

                                <Paging defaultPageSize={15} />
                                <Pager
                                    visible={true}
                                    displayMode={'full'}
                                    showInfo={true}
                                    showNavigationButtons={true} />

                                <FilterRow visible={true} />
                                <HeaderFilter visible={true} />
                                <Sorting mode="multiple" />
                                <SearchPanel visible={false} />

                                <Column dataField="name" dataType="string" caption={getLabelText('GroupsName')} />
                            </DataGrid>
                        </Item>
                        <Item>
                            <Location row={1} col={2} rowspan={2} />
                            <DataGrid
                                dataSource={this.state.listUser}
                                keyExpr="id"
                                showBorders={true}
                                focusedRowEnabled={true}
                                allowColumnResizing={true}
                                allowColumnReordering={true}
                                onFocusedRowChanged={this.onFocusedRowChanged_ListUser}
                                focusedRowKey={this.state.currentItem_ListUser.rowKey}

                                autoNavigateToFocusedRow={true}
                                onContentReady={this.onContentReady_ListUser}
                                ref={(ref) => { this.state.refComponents["dgdListUser"] = ref }}

                                height={window.innerHeight - 270}
                            >
                                <Paging defaultPageSize={15} />
                                <Pager
                                    visible={true}
                                    displayMode={'full'}
                                    showInfo={true}
                                    showNavigationButtons={true} />

                                <FilterRow visible={true} />
                                <HeaderFilter visible={true} />
                                <Sorting mode="multiple" />
                                <SearchPanel visible={false} />

                                <Column dataField="userName" dataType="string" caption={getLabelText('UsersName')} />
                            </DataGrid>
                        </Item>
                        <Item>
                            <Location row={2} col={4} />
                            <Button
                                activeStateEnabled={false} hoverStateEnabled={false}
                                disabled={(this.state.isNew === false && this.state.isVisibleEditArea === false) || this.state.isVisibleButtonAddToGroup === false}
                                text={getLabelText('Button_AddToGroup')}
                                onClick={this.handleButtonAddToGroupClick}
                            />
                            <br />
                            <Button
                                activeStateEnabled={false} hoverStateEnabled={false}
                                disabled={(this.state.isNew === false && this.state.isVisibleEditArea === false) || this.state.isVisibleButtonRemoveFromGroup === false}
                                text={getLabelText('Button_RemoveFromGroup')}
                                onClick={this.handleButtonRemoveFromGroupClick}
                            />
                        </Item>
                        <Item>
                            <Location row={1} col={6} rowspan={2} />
                            <DataGrid
                                dataSource={this.state.currentItem_GroupUser.model.groupUserRelations}
                                keyExpr="id"
                                showBorders={true}
                                focusedRowEnabled={true}
                                allowColumnResizing={true}
                                allowColumnReordering={true}
                                onFocusedRowChanged={this.onFocusedRowChanged_GroupUserRelation}
                                focusedRowKey={this.state.currentItem_GroupUserRelation.rowKey}

                                autoNavigateToFocusedRow={true}
                                onContentReady={this.onContentReady_GroupUserRelation}
                                ref={(ref) => { this.state.refComponents["dgdGroupUserRelation"] = ref }}

                                height={window.innerHeight - 270}
                            >
                                <Paging defaultPageSize={15} />
                                <Pager
                                    visible={true}
                                    displayMode={'full'}
                                    showInfo={true}
                                    showNavigationButtons={true} />

                                <FilterRow visible={true} />
                                <HeaderFilter visible={true} />
                                <Sorting mode="multiple" />
                                <SearchPanel visible={false} />

                                <Column dataField="userName" dataType="string" caption={getLabelText('UsersName')} />
                            </DataGrid>
                        </Item>
                    </ResponsiveBox>

                    <Toast
                        visible={this.state.isVisibleToastForEdit}
                        message={getLabelText('Message_EditMode')}
                        type={'warning'}
                        onHiding={this.onToastHiding}
                        displayTime={3000}
                        width={400}
                        position={'center center'}
                    />
                </div>
            </div >
        );
    }
}

function mapStateToProps(storePar: any, ownProps?: any) { return {} }

function mapDispatchToProps(dispatch: any) { return { actions: {} } }

export const MailingGroupUsersDict = connect(mapStateToProps, mapDispatchToProps)(MailingGroupUsersDictClass);