import React from 'react';
import TreeView from 'devextreme-react/tree-view';

export interface ITVSelectorData{
    allItems:any[],
    oldSelectedIds:number[],
    newSelectedIds:number[],
    hasChanges:boolean
}

interface ITreeViewSelectorProps {
    dataPropName:string
    displayExpr:string,
    parentIdExpr?:string,
    keyExpr:string,
    parent:React.Component
}

export class TreeViewSelector extends React.Component<ITreeViewSelectorProps, any> {
    tvRef:object;
    constructor(props: any) {
        super(props);
        this.tvRef = React.createRef();
    }

    componentDidUpdate(){
        let s=1;
    }
    
    handleSelectionChanged(e:any){
        let newSelIds = this.getItems().newSelectedIds;
        let oldSelIds = [...this.getItems().oldSelectedIds];
        let addRemoveNestedNodes = ((parentNode:any,add:boolean)=>{
            if(add){
                //  
                parentNode.items.forEach((chItm:any)=>{
                    if(!newSelIds.find(itm=>itm === chItm.itemData.id)){
                        newSelIds.push(chItm.itemData.id);
                    }
                    addRemoveNestedNodes(chItm,true);
                });
            } else {
                //  
                parentNode.items.forEach((chItm:any)=>{
                    newSelIds = newSelIds.filter(itm=>itm !== chItm.itemData.id);
                    addRemoveNestedNodes(chItm,false);
                });
            }
        });
        if(e.itemData.selected){
            if(!newSelIds.find((itm:number)=>itm === e.itemData.id)){
                newSelIds.push(e.itemData.id);
                addRemoveNestedNodes(e.node,true);
            }
        } else {
            newSelIds = newSelIds.filter((itm:any)=>itm !== e.itemData.id ); //      
            addRemoveNestedNodes(e.node,false);
        }        
        let hasChanges = newSelIds.length != oldSelIds.length;
        if(!hasChanges){
            oldSelIds.forEach((itm:number)=>{
                if(newSelIds.findIndex((ni:any)=>ni === itm)=== -1){
                    hasChanges = true;
                }
            });
        }
        let data = this.getItems();
        data.newSelectedIds = newSelIds;
        data.hasChanges = hasChanges;
        console.log(data);
        this.props.parent.setState({data});
    }

    getItems():ITVSelectorData{
        let st:any =  this.props.parent.state;
        let data = st[this.props.dataPropName] as ITVSelectorData;
        return data;
    }

    render(){
        return (
                <TreeView 
                ref={(ref) => {
                    if (ref) {
                        this.tvRef = ref;
                    }
                }}
                items={this.getItems().allItems}
                dataStructure="plain"
                displayExpr={this.props.displayExpr}
                parentIdExpr={this.props.parentIdExpr}
                keyExpr={this.props.keyExpr}
                selectByClick={true}
                selectionMode="multiple"
                showCheckBoxesMode="normal"
                onItemSelectionChanged={(e) => this.handleSelectionChanged(e)}
            />            
        );

    }

}
