import React from 'react';
import getLocMsg, { LocMsgId } from '../localization';
import Button from 'devextreme-react/button';
import * as dbwrap from '../DbWrapper';
import { GuiElementsId, GetGuiElementName } from '../Routes';
import { ErrorMsg, ErrorMsgProps,ErrorMsgFromDict } from '../ReusableComponents/DxStyleSimple';
import { MessageType,errorParser, ErrorEnum } from '../Errors';
import DataGrid, {
    Column,
    Grouping,
    GroupPanel,
    Paging,
    SearchPanel,
    ColumnChooser,
    Editing,
    Form,
    Selection,
    Toolbar,
    Lookup,
    Item,
} from 'devextreme-react/data-grid';
import { Popup } from 'devextreme-react/popup';
import { IColProps } from 'devextreme-react/responsive-box';


enum syncOperationType {
    update,
    add,
    delete
}

interface syncReportRow {
    guiElementId: string,
    guiElementCaption: string,
    operation: string,
    operationResult: dbwrap.responseResult
}

enum syncStage {
    init,
    syncStarted,
    syncCompleted
}

interface syncMenuState {
    rowsToBeSynchronized: number,
    processedRows: number,
    reportRows: syncReportRow[],
    stage: syncStage,
    errorObj: any
}

interface AppMenuObjectRow {
    id?: number,
    uid: string,
    name: string
}

export class SynchronizationMenuItems extends React.Component<any, syncMenuState> {
    constructor(props: any) {
        super(props);
        this.state = {
            rowsToBeSynchronized: -1,
            processedRows: 0,
            reportRows: [],
            stage: syncStage.init,
            errorObj: null
        }
    }

    componentDidMount() {
    }

    AddNewRow(row: AppMenuObjectRow) {
        dbwrap.AddRow("ApplicationMenuObjects", row, null).then((result: dbwrap.responseResult) => {
            let newRow: syncReportRow = { guiElementId: row.uid, guiElementCaption: row.name, operation: "Add", operationResult: result };
            let newRepRows = [...this.state.reportRows];
            newRepRows.push(newRow);
            this.setState({ reportRows: newRepRows });
            this.IncrementProcessedRowCounter();
        });
    }

    UpdateExistingRow(newRow: AppMenuObjectRow, oldRow: AppMenuObjectRow) {
        if (oldRow.name != newRow.name) {
            newRow.id = oldRow.id;
            let updatedRow = { ...newRow };
            dbwrap.UpdateRow("ApplicationMenuObjects", [updatedRow], null).then((result: dbwrap.responseResult) => {
                let newRow: syncReportRow = { guiElementId: updatedRow.uid, guiElementCaption: updatedRow.name, operation: "Update", operationResult: result };
                let newRepRows = [...this.state.reportRows];
                newRepRows.push(newRow);
                this.setState({ reportRows: newRepRows });
                this.IncrementProcessedRowCounter();
            });
        } else {
            this.IncrementProcessedRowCounter();
        }
    }

    DeleteMissingrow(row: AppMenuObjectRow) {
        let id: number = row.id ? row.id : -1;
        dbwrap.DeleteRow("ApplicationMenuObjects", id, null).then((result: dbwrap.responseResult) => {
            let newRow: syncReportRow = { guiElementId: row.uid, guiElementCaption: row.name, operation: "Delete", operationResult: result };
            let newRepRows = [...this.state.reportRows];
            newRepRows.push(newRow);
            this.setState({ reportRows: newRepRows });
            this.IncrementProcessedRowCounter();
        });
    }

    IncrementProcessedRowCounter() {
        this.setState({ processedRows: this.state.processedRows + 1 }, () => {
            this.CheckCompleted();
        });
    }

    CheckCompleted() {
        if (this.state.processedRows === this.state.rowsToBeSynchronized) {
            this.setState({ stage: syncStage.syncCompleted });
        }
    }

    handleApplyBtnClick() {
        this.setState({ reportRows: [], stage: syncStage.syncStarted });
        dbwrap.GetRows("ApplicationMenuObjects", null).then((result: dbwrap.responseResult) => {
            if (result.success) {
                let rwToBeSync = 0;
                let srvRows = result.data as Array<AppMenuObjectRow>;
                let clientUids: string[] = [];
                for (let item in GuiElementsId) {
                    let itemKey = GuiElementsId[item]
                    if (isNaN(Number(item))) {
                        if (item === "None") {
                            continue;
                        }
                        clientUids.push(item);
                        rwToBeSync++;
                        //@ts-ignore
                        let itmName = GetGuiElementName(itemKey);
                        let newRow: AppMenuObjectRow = {
                            uid: item,
                            name: itmName
                        };
                        let currRow = srvRows.find((row) => row.uid === item);
                        if (currRow) {
                            this.UpdateExistingRow(newRow, currRow);
                        }
                        else {
                            this.AddNewRow(newRow);
                        }
                    }
                }
                let rowsForDeleting = srvRows.filter((srvRow) => !clientUids.includes(srvRow.uid));
                rwToBeSync += rowsForDeleting.length;
                this.setState({ rowsToBeSynchronized: rwToBeSync });
                rowsForDeleting.forEach((itm) => {
                    this.DeleteMissingrow(itm);
                });
                this.CheckCompleted();
            }
            else {
                //2do handleError
            }
        });
    }
 
    opResultRender(cellData: any) {
        if (cellData.data.operationResult.success) {
            return (<div>OK</div>);
        } else {
            let err = errorParser(cellData.data.operationResult.data);
            return (<Button className="dx-toast-error" text="Error" 
                onClick={() => {
                    this.setState({errorObj: err });
                }} />);
        }
    }

    renderReportRows() {
        if (this.state.stage !== syncStage.init && this.state.reportRows.length > 0) {
            return (
                <DataGrid
                    dataSource={this.state.reportRows}
                >
                    <Column dataField="guiElementId" caption="Uid"></Column>
                    <Column dataField="guiElementCaption" caption=""></Column>
                    <Column dataField="operation" caption=""></Column>
                    <Column dataField="operationResult.success" caption="" cellRender={(data) => this.opResultRender(data)}></Column>
                </DataGrid>
            );
        }
        if (this.state.stage === syncStage.syncCompleted && this.state.reportRows.length == 0) {
            let msg: ErrorMsgProps = { message: "     .  ", type: MessageType.success };
            return ErrorMsg(msg);
        }
    }
 
    renderErrDescription() {
        if (this.state.errorObj) {
                let parsedErrId = ErrorEnum.NoError;
                if(this.state.errorObj.hasOwnProperty("parsedErrId")){
                    parsedErrId = this.state.errorObj.parsedErrId;    
                }
                return ErrorMsgFromDict(parsedErrId);
        }
    }

    renderApplyBtn() {
        if (this.state.stage === syncStage.init) {
            return (
                <div style={{ marginTop: "20px" }}>
                    <Button icon="check" text={getLocMsg(LocMsgId.Button_Apply)} onClick={() => this.handleApplyBtnClick()} />
                </div>
            );
        }
    }

    render() {
        return (
            <div>
                <h3 style={{ display: "inline-block", textAlign: "left" }}>{getLocMsg(this.props.msgId)}</h3>
                {this.renderApplyBtn()}
                {this.renderErrDescription()}
                {this.renderReportRows()}
            </div>
        );
    }
}