import * as React from 'react';
import { connect } from 'react-redux';
import * as dbwrap from '../../DbWrapper';
import getLocMsg, { LocMsgId } from '../../localization';
import DataGrid, {
    Column,
    Paging,
    Lookup,
    Sorting,
    FilterRow,
    HeaderFilter,
    ColumnChooser,
    SearchPanel,
    Pager
} from 'devextreme-react/data-grid';
import { Popup, Position } from 'devextreme-react/popup';
import Button from 'devextreme-react/button';
import { MessagePopup } from '../../components/CommonComponents/MessagePopup';
import { AvailabilitySchedule } from '../../Models/AvailabilitySchedule';
import { ScheduleItem } from '../../Models/ScheduleItem';
import { User } from '../../Models/User';
import { ConfirmForm } from '../../components/CommonComponents/ConfirmForm';
import TextBox from 'devextreme-react/text-box';
import { IncorrectData } from '../AvailabilitySchedules/IncorrectData';
import { DateBox, SelectBox } from 'devextreme-react';

interface AvailabilitySchedulesProps {

}

type AvailabilitySchedulesState = {
    availabilitySchedulesRows: AvailabilitySchedule[],
    scheduleItemsRows: ScheduleItem[],
    usersRows: User[],

    selectedAvailabilitySchedule: AvailabilitySchedule,
    previousAvailabilityItem: AvailabilitySchedule,

    selectedScheduleItem: ScheduleItem,
    previousScheduleItem: ScheduleItem,

    currentScheduleItems: ScheduleItem[],
    editableScheduleItems: ScheduleItem[],

    focusedRowKey: number,
    scheduleItemsFocusedRowKey: number | undefined,

    addEditFormVisible: boolean
    addEditFormTitle: string,
    confirmDeleteFormVisible: boolean,

    scheduleItemFormVisible: boolean,

    editScheduleItemVisible: boolean,
    removeScheduleItemVisible: boolean,

    popupTitle: string,
    popupMessage: string,
    popupVisible: boolean,
    popupVisibleForCriticalError: boolean,
    hasCriticalError: boolean,

    dataIsCorrect: { name: IncorrectData },
    scheduleItemIsCorrect: { weekDay: IncorrectData, startTime: IncorrectData, endTime: IncorrectData },

    availableWeekDays: any,
    weekDays: any,
    weekDaySelectBoxHidden: boolean,

    refComponents: any,
    isTrashEditButtonDisabled: boolean,
    isScheduleItemsGridVisible: boolean
}

const allowedPageSizes = [5, 10, 15, 20];
var l_dataGrid: any = null;
var l_dataGrid_ScheduleItems: any = null;

export class AvailabilitySchedulesDictClass extends React.Component<AvailabilitySchedulesProps, AvailabilitySchedulesState> {
    constructor(props: AvailabilitySchedulesProps) {
        super(props);
        this.state = {
            availabilitySchedulesRows: [],
            scheduleItemsRows: [],
            usersRows: [],

            selectedAvailabilitySchedule: { id: 0, name: '' },
            previousAvailabilityItem: { id: 0, name: '' },

            selectedScheduleItem: { id: 0 },
            previousScheduleItem: { id: 0 },

            currentScheduleItems: [],
            editableScheduleItems: [],

            focusedRowKey: 0,
            scheduleItemsFocusedRowKey: 0,

            addEditFormVisible: false,
            addEditFormTitle: '',
            confirmDeleteFormVisible: false,

            scheduleItemFormVisible: false,

            editScheduleItemVisible: false,
            removeScheduleItemVisible: false,

            popupTitle: "",
            popupMessage: "",
            popupVisible: false,
            popupVisibleForCriticalError: false,
            hasCriticalError: false,

            dataIsCorrect: { name: { isValid: true } },
            scheduleItemIsCorrect: {
                weekDay: { isValid: true },
                startTime: { isValid: true },
                endTime: { isValid: true }
            },

            availableWeekDays: [],
            weekDays: [],
            weekDaySelectBoxHidden: false,

            refComponents: new Map(),
            isTrashEditButtonDisabled: false,
            isScheduleItemsGridVisible: false
        };

        //#region Render functions
        this.renderAddEditForm = this.renderAddEditForm.bind(this);
        this.renderScheduleItemForm = this.renderScheduleItemForm.bind(this);
        //#endregion

        //#region Toolbar buttons functions
        this.handleButtonAddClick = this.handleButtonAddClick.bind(this);
        this.handleButtonRemoveClick = this.handleButtonRemoveClick.bind(this);
        this.handleButtonEditClick = this.handleButtonEditClick.bind(this);
        this.handleButtonRefreshClick = this.handleButtonRefreshClick.bind(this);
        //#endregion

        //#region AvailabilitySchedules DataGrid functions
        this.onFocusedRowChanged = this.onFocusedRowChanged.bind(this);
        this.onContentReady = this.onContentReady.bind(this);
        this.getLocalDateTime = this.getLocalDateTime.bind(this);
        //#endregion

        //#region ScheduleItems DataGrid functions
        this.getScheduleItemsRows = this.getScheduleItemsRows.bind(this);
        //#endregion

        //#region ScheduleItems AddEditForm functions
        this.saveScheduleClick = this.saveScheduleClick.bind(this);
        this.cancelAddEditFormClick = this.cancelAddEditFormClick.bind(this);
        //#region ScheduleItems DataGrid functions
        this.onScheduleItemsFocusedRowChanged = this.onScheduleItemsFocusedRowChanged.bind(this);
        this.onScheduleItemsContentReady = this.onScheduleItemsContentReady.bind(this);
        this.addScheduleItem = this.addScheduleItem.bind(this);
        this.editScheduleItem = this.editScheduleItem.bind(this);
        this.removeScheduleItem = this.removeScheduleItem.bind(this);
        //#endregion
        //#endregion

        //#region ScheduleItem Form functions
        this.saveScheduleItemClick = this.saveScheduleItemClick.bind(this);
        this.cancelScheduleItemFormClick = this.cancelScheduleItemFormClick.bind(this);
        this.onWeekDayValueChanged = this.onWeekDayValueChanged.bind(this);
        this.onStartTimeValueChanged = this.onStartTimeValueChanged.bind(this);
        this.onEndTimeValueChanged = this.onEndTimeValueChanged.bind(this);
        this.getWeekDays = this.getWeekDays.bind(this);
        this.getWeekDayName = this.getWeekDayName.bind(this);
        //#endregion

        //#region Popup functions
        this.hidePopup = this.hidePopup.bind(this);
        this.hidePopupForCriticalError = this.hidePopupForCriticalError.bind(this);
        //#endregion

        //#region Confirm functions
        this.confirmDeleteFormClick = this.confirmDeleteFormClick.bind(this);
        this.closeConfirmFormClick = this.closeConfirmFormClick.bind(this);
        //#endregion

        //#region Validation functions
        this.onChangeName = this.onChangeName.bind(this);
        this.onChangeComment = this.onChangeComment.bind(this);
        this.applyChange = this.applyChange.bind(this);
        this.checkSelectedRow = this.checkSelectedRow.bind(this);
        //#endregion
    }

    //#region Render
    //      .
    renderAddEditForm() {
        if (!this.state.addEditFormVisible) {
            return (<div />);
        }
        return (
            <Popup
                visible={this.state.addEditFormVisible}
                dragEnabled={false}
                closeOnOutsideClick={false}
                showCloseButton={false}
                showTitle={true}
                title={this.state.addEditFormTitle}
                container=".dx-viewport">
                <Position at="center" my="center" />

                <Button
                    style={{ margin: "0 10px 10px 0" }}
                    text={getLocMsg(LocMsgId.Button_Save)}
                    onClick={this.saveScheduleClick} />

                <Button
                    style={{ margin: "0 0 10px 0" }}
                    text={getLocMsg(LocMsgId.Button_Cancel)}
                    onClick={this.cancelAddEditFormClick} />

                <div className='mb-2'>
                    <div className='row mb-2'>
                        <div className='col-2 my-auto'>
                            {getLocMsg(LocMsgId.Column_Name)}:
                        </div>
                        <div className='col'>
                            <TextBox value={this.state.selectedAvailabilitySchedule.name}
                                valueChangeEvent="input" onInput={this.onChangeName} maxLength={100}
                                isValid={this.state.dataIsCorrect.name.isValid} hint={this.state.dataIsCorrect.name.message}
                                ref={(ref) => { this.state.refComponents["txtName"] = ref }} />
                        </div>
                    </div>

                    <div className='row'>
                        <div className='col-2 my-auto'>
                            {getLocMsg(LocMsgId.Column_Comment)}:
                        </div>
                        <div className='col'>
                            <TextBox value={this.state.selectedAvailabilitySchedule.comment} maxLength={250}
                                valueChangeEvent="input" onInput={this.onChangeComment} />
                        </div>
                    </div>
                </div>

                <Button icon="add" hint={getLocMsg(LocMsgId.Button_AddSchedule)} onClick={this.addScheduleItem} className='mb-2 mr-2' visible={this.state.editableScheduleItems.length < 7} />
                <Button icon="edit" hint={getLocMsg(LocMsgId.Button_EditSchedule)} onClick={this.editScheduleItem} className='mb-2 mr-2' visible={this.state.editableScheduleItems.length != 0} />
                <Button icon="trash" hint={getLocMsg(LocMsgId.Button_DeleteSchedule)} onClick={this.removeScheduleItem} className='mb-2 mr-2' visible={this.state.editableScheduleItems.length != 0} />

                <DataGrid
                    dataSource={this.state.editableScheduleItems}
                    keyExpr="weekDay"
                    showBorders={true}
                    focusedRowEnabled={true}
                    focusedRowKey={this.state.scheduleItemsFocusedRowKey}
                    onFocusedRowChanged={this.onScheduleItemsFocusedRowChanged}
                    autoNavigateToFocusedRow={true}
                    onContentReady={this.onScheduleItemsContentReady}
                    ref={(ref) => { l_dataGrid_ScheduleItems = ref }}>
                    <Sorting clearText={getLocMsg(LocMsgId.Button_ClearSorting)} ascendingText={getLocMsg(LocMsgId.Button_SortAscending)}
                        descendingText={getLocMsg(LocMsgId.Button_SortDescending)} mode="multiple" />
                    <Paging defaultPageSize={20} />
                    <Pager
                        visible={true}
                        allowedPageSizes={allowedPageSizes}
                        displayMode={'full'}
                        showPageSizeSelector={true}
                        showInfo={true}
                        showNavigationButtons={true} />
                    <Column dataField="scheduleId" dataType="number" caption="ScheduleId" visible={false} />
                    <Column dataField="weekDay" caption={getLocMsg(LocMsgId.Column_WeekDay)} alignment="left">
                        <Lookup dataSource={this.state.weekDays} displayExpr="name" valueExpr="id" />
                    </Column>
                    <Column dataField="startTime" caption={getLocMsg(LocMsgId.Column_StartTime)}
                        alignment="left" format="HH:mm" allowEditing="true" dataType="datetime">
                    </Column>
                    <Column dataField="endTime" caption={getLocMsg(LocMsgId.Column_EndTime)}
                        alignment="left" format="HH:mm" allowEditing="true" dataType="datetime">
                    </Column>
                </DataGrid>
            </Popup>
        )
    }

    //        .
    renderScheduleItemForm() {
        if (!this.state.scheduleItemFormVisible) {
            return (<div />);
        }
        return (
            <Popup
                visible={this.state.scheduleItemFormVisible}
                dragEnabled={false}
                closeOnOutsideClick={false}
                showCloseButton={false}
                showTitle={true}
                width="600px"
                height="300px"
                title={getLocMsg(LocMsgId.Menu_Dicts_Managements_ScheduleItem)}
                container=".dx-viewport">
                <Position at="center" my="center" />

                <Button
                    style={{ margin: "0 10px 10px 0" }}
                    text={getLocMsg(LocMsgId.Button_Save)}
                    onClick={this.saveScheduleItemClick} />

                <Button
                    style={{ margin: "0 0 10px 0" }}
                    text={getLocMsg(LocMsgId.Button_Cancel)}
                    onClick={this.cancelScheduleItemFormClick} />

                <div className='mb-2'>
                    <div className='row mb-2'>
                        <div className='col-3 my-auto'>
                            {getLocMsg(LocMsgId.Column_WeekDay)}:
                        </div>
                        <div className='col' hidden={this.state.weekDaySelectBoxHidden}>
                            <SelectBox dataSource={this.state.availableWeekDays} displayExpr="name" valueExpr="id" onValueChanged={this.onWeekDayValueChanged}
                                placeholder={getLocMsg(LocMsgId.Message_SelectWeekDay)} value={this.state.selectedScheduleItem.weekDay}
                                isValid={this.state.scheduleItemIsCorrect.weekDay.isValid} hint={this.state.scheduleItemIsCorrect.weekDay.message}
                                ref={(ref) => { this.state.refComponents["selectWeekDay"] = ref }} />
                        </div>
                        <div className='col my-auto' hidden={!this.state.weekDaySelectBoxHidden}>
                            {this.getWeekDayName(this.state.selectedScheduleItem)}
                        </div>
                    </div>

                    <div className='row mb-2'>
                        <div className='col-3 my-auto'>
                            {getLocMsg(LocMsgId.Column_StartTime)}:
                        </div>
                        <div className='col'>
                            <DateBox value={this.state.selectedScheduleItem.startTime} pickerType="rollers" type="time" cancelButtonText=""
                                onValueChanged={this.onStartTimeValueChanged} isValid={this.state.scheduleItemIsCorrect.startTime.isValid}
                                hint={this.state.scheduleItemIsCorrect.startTime.message}
                                ref={(ref) => { this.state.refComponents["dateStartTime"] = ref }} />
                        </div>
                    </div>

                    <div className='row mb-2'>
                        <div className='col-3 my-auto'>
                            {getLocMsg(LocMsgId.Column_EndTime)}:
                        </div>
                        <div className='col'>
                            <DateBox value={this.state.selectedScheduleItem.endTime} pickerType="rollers" type="time" cancelButtonText=""
                                onValueChanged={this.onEndTimeValueChanged} isValid={this.state.scheduleItemIsCorrect.endTime.isValid}
                                hint={this.state.scheduleItemIsCorrect.endTime.message}
                                ref={(ref) => { this.state.refComponents["dateEndTime"] = ref }} />
                        </div>
                    </div>
                </div>
            </Popup>
        )
    }

    render() {
        return (
            <div>
                <h3 style={{ width: "auto", textAlign: "center" }}>
                    {getLocMsg(LocMsgId.Menu_Dicts_Managements_AvailabilitySchedule)}
                </h3>

                <div hidden={this.state.hasCriticalError === false}>
                    <div style={{ height: '80vh' }} className="d-flex flex-column justify-content-center align-items-center">
                        <h1>{getLocMsg(LocMsgId.Message_DirectoryNotAvailable)}</h1>
                    </div>
                </div>

                <div hidden={this.state.hasCriticalError}>
                    <Button icon="add" hint={getLocMsg(LocMsgId.Button_Add)} onClick={this.handleButtonAddClick} className='mb-2 mr-2' />
                    <Button icon="trash" hint={getLocMsg(LocMsgId.Button_Delete)} onClick={this.handleButtonRemoveClick} disabled={this.state.isTrashEditButtonDisabled} className='mb-2 mr-2' />
                    <Button icon="edit" hint={getLocMsg(LocMsgId.Button_Edit)} onClick={this.handleButtonEditClick} disabled={this.state.isTrashEditButtonDisabled} className='mb-2 mr-2' />
                    <Button icon="refresh" hint={getLocMsg(LocMsgId.Button_Refresh)} onClick={this.handleButtonRefreshClick} className='mb-2 mr-2' />

                    {this.renderAddEditForm()}
                    {this.renderScheduleItemForm()}

                    <ConfirmForm title={getLocMsg(LocMsgId.Message_Confirm)} message={getLocMsg(LocMsgId.Message_Deleting)}
                        visible={this.state.confirmDeleteFormVisible} confirmButtonText={getLocMsg(LocMsgId.Button_Yes)} confirmForm={this.confirmDeleteFormClick}
                        cancelButtonText={getLocMsg(LocMsgId.Button_No)} cancelForm={this.closeConfirmFormClick} />

                    <MessagePopup title={this.state.popupTitle} hidePopup={this.hidePopup} message={this.state.popupMessage} visible={this.state.popupVisible} />
                    <MessagePopup title={this.state.popupTitle} hidePopup={this.hidePopupForCriticalError} message={this.state.popupMessage} visible={this.state.popupVisibleForCriticalError} />

                    <DataGrid
                        dataSource={this.state.availabilitySchedulesRows}
                        keyExpr="id"
                        showBorders={true}
                        focusedRowEnabled={true}
                        focusedRowKey={this.state.focusedRowKey}
                        onFocusedRowChanged={this.onFocusedRowChanged}
                        autoNavigateToFocusedRow={true}
                        onContentReady={this.onContentReady}
                        ref={(ref) => { l_dataGrid = ref }}>
                        <Paging defaultPageSize={10} />
                        <Pager
                            visible={true}
                            allowedPageSizes={allowedPageSizes}
                            displayMode={'full'}
                            showPageSizeSelector={true}
                            showInfo={true}
                            showNavigationButtons={true} />
                        <FilterRow visible={true} />
                        <HeaderFilter visible={true} />
                        <ColumnChooser enabled={true} mode="select" />
                        <Sorting mode="multiple" />
                        <SearchPanel visible={true} />
                        <Column dataField="id" dataType="number" caption="Id" visible={false} />
                        <Column dataField="name" caption={getLocMsg(LocMsgId.Column_Name)} alignment="left" />
                        <Column dataField="date" dataType="datetime" caption={getLocMsg(LocMsgId.Column_EditDate)} alignment="left" />
                        <Column dataField="userId" caption={getLocMsg(LocMsgId.Column_User)} alignment="left">
                            <Lookup dataSource={this.state.usersRows} valueExpr="id" displayExpr="username" />
                        </Column>
                        <Column dataField="comment" caption={getLocMsg(LocMsgId.Column_Comment)} alignment="left" />
                    </DataGrid>
                    <br />
                    <div hidden={this.state.isScheduleItemsGridVisible === false}>
                        <h3 style={{ width: "auto", textAlign: "center" }}>
                            {getLocMsg(LocMsgId.Menu_Dicts_Managements_ScheduleItem)}
                        </h3>
                        <DataGrid
                            dataSource={this.state.currentScheduleItems}
                            keyExpr="id"
                            showBorders={true}>
                            <Paging defaultPageSize={10} />
                            <Pager
                                visible={true}
                                allowedPageSizes={allowedPageSizes}
                                displayMode={'full'}
                                showPageSizeSelector={true}
                                showInfo={true}
                                showNavigationButtons={true} />
                            <Column dataField="id" dataType="number" caption="Id" visible={false} />
                            <Column dataField="scheduleId" dataType="number" caption="ScheduleId" visible={false} />
                            <Column dataField="weekDay" caption={getLocMsg(LocMsgId.Column_WeekDay)} alignment="left">
                                <Lookup dataSource={this.state.weekDays} displayExpr="name" valueExpr="id" />
                            </Column>
                            <Column dataField="startTime" caption={getLocMsg(LocMsgId.Column_StartTime)} dataType="datetime" alignment="left" format="HH:mm" />
                            <Column dataField="endTime" caption={getLocMsg(LocMsgId.Column_EndTime)} dataType="datetime" alignment="left" format="HH:mm" />
                        </DataGrid>
                    </div>
                </div>
            </div>
        );
    }
    //#endregion

    //#region Code
    componentDidMount() {
        (async () => {
            await dbwrap.GetRows("AvailabilitySchedules", null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    var schedules: AvailabilitySchedule[] = [];

                    //    .
                    data.data.forEach((element: AvailabilitySchedule) => {
                        element.date = new Date(element.date + 'Z');
                        schedules.push(element);
                    });

                    this.setState({ availabilitySchedulesRows: schedules });
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                        popupMessage: errorMessage,
                        popupVisibleForCriticalError: true
                    });

                    return;
                }

                (async () => {
                    await dbwrap.GetRows("ScheduleItems", null).then((data: dbwrap.responseResult) => {
                        if (data.success) {
                            this.setState({ scheduleItemsRows: data.data });
                        }
                        else {
                            let errorMessage: string = ''

                            if (data.data.parsedErrObject == null) {
                                errorMessage = data.data.request.responseText
                            }
                            else {
                                errorMessage = data.data.parsedErrObject.message
                            }

                            this.setState({
                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                popupMessage: errorMessage,
                                popupVisibleForCriticalError: true
                            });

                            return;
                        }

                        (async () => {
                            await dbwrap.GetRows("Users", null).then((data: dbwrap.responseResult) => {
                                if (data.success) {
                                    this.setState({ usersRows: data.data });

                                    var weekDays = [{
                                        id: 0,
                                        name: getLocMsg(LocMsgId.WeekDay_Monday)
                                    }, {
                                        id: 1,
                                        name: getLocMsg(LocMsgId.WeekDay_Tuesday)
                                    }, {
                                        id: 2,
                                        name: getLocMsg(LocMsgId.WeekDay_Wednesday)
                                    }, {
                                        id: 3,
                                        name: getLocMsg(LocMsgId.WeekDay_Thursday)
                                    }, {
                                        id: 4,
                                        name: getLocMsg(LocMsgId.WeekDay_Friday)
                                    }, {
                                        id: 5,
                                        name: getLocMsg(LocMsgId.WeekDay_Saturday)
                                    }, {
                                        id: 6,
                                        name: getLocMsg(LocMsgId.WeekDay_Sunday)
                                    }]

                                    this.setState({ weekDays: weekDays });

                                    this.render();
                                }
                                else {
                                    let errorMessage: string = '';

                                    if (data.data.parsedErrObject == null) {
                                        errorMessage = data.data.request.responseText
                                    }
                                    else {
                                        errorMessage = data.data.parsedErrObject.message
                                    }

                                    this.setState({
                                        popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                        popupMessage: errorMessage,
                                        popupVisibleForCriticalError: true
                                    });
                                }
                            });
                        })();
                    });
                })();
            });
        })();
    }

    //#region Toolbar buttons functions
    //   .
    handleButtonAddClick() {
        this.setState({
            selectedAvailabilitySchedule: { id: 0, name: '', scheduleItems: [] },
            editableScheduleItems: [],
            selectedScheduleItem: { id: 0 },
            previousAvailabilityItem: { ...this.state.selectedAvailabilitySchedule },
            focusedRowKey: -1,
            addEditFormVisible: true,
            addEditFormTitle: getLocMsg(LocMsgId.Message_AddAvailabilitySchedule)
        });

        this.resetDataForCorrect();
    }

    //   .
    handleButtonRemoveClick() {
        if (!this.checkSelectedRow()) return;

        this.setState({
            confirmDeleteFormVisible: true
        });
    }

    //   .
    handleButtonEditClick() {
        if (!this.checkSelectedRow()) return;

        var availabilitySchedule = { ...this.state.selectedAvailabilitySchedule };
        //@ts-ignore
        var items: ScheduleItem[] = availabilitySchedule.scheduleItems;
        items = Object.values(items);

        this.setState({
            editableScheduleItems: items,
            previousAvailabilityItem: { ...this.state.selectedAvailabilitySchedule },
            selectedScheduleItem: { id: 0 },
            addEditFormVisible: true,
            addEditFormTitle: getLocMsg(LocMsgId.Message_EditAvailabilitySchedule)
        });

        this.resetDataForCorrect();
    }

    //  grid.
    handleButtonRefreshClick() {
        this.componentDidMount();
    }
    //#endregion

    //#region AvailabilitySchedules DataGrid functions
    //     DataGrid.
    onFocusedRowChanged(e: any) {
        if (e == null || e.row == null) {
            return;
        }

        var data = e.row.data;
        if (data == null) {
            return;
        }

        data.scheduleItems = this.state.scheduleItemsRows.filter(x => x.scheduleId === data.id);

        this.setState({
            selectedAvailabilitySchedule: { ...data },
            currentScheduleItems: data.scheduleItems,
            focusedRowKey: data.id
        });
    }

    onContentReady(e: any) {
        var item = this.state.selectedAvailabilitySchedule;
        var key = item.id;
        var keyExistsInFilteredRows = -1;
        var filteredRows: any;

        if (item.id != 0) {
            item.scheduleItems = this.state.scheduleItemsRows.filter(x => x.scheduleId === item.id);
            this.setState({
                previousAvailabilityItem: { ...item },
                currentScheduleItems: item.scheduleItems
            });
        }

        //       .
        this.setState({ selectedAvailabilitySchedule: { ...item }, focusedRowKey: key });

        if (item != null && l_dataGrid != null) {
            //           .
            l_dataGrid.instance.getDataSource().store()
                .load({ filter: l_dataGrid.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                });

            //        .
            keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)
        }

        //             Id,    .
        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {
            if (l_dataGrid != null) {
                //  Key   (  -    ).
                key = l_dataGrid.instance.getKeyByRowIndex(0)
            }
        }

        if (key != null && l_dataGrid != null) {
            l_dataGrid.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    filteredRows = result;
                });
            item = filteredRows.find((x: any) => x.id === key)
        }

        item.scheduleItems = this.state.scheduleItemsRows.filter(x => x.scheduleId === item.id);
        this.setState({ selectedAvailabilitySchedule: { ...item }, focusedRowKey: key, currentScheduleItems: item.scheduleItems });
        // ,   ,    .
        if (l_dataGrid != null) {
            //           .
            l_dataGrid.instance.getDataSource().store()
                .load({ filter: l_dataGrid.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                    if (result.length === 0) {
                        this.setState({
                            currentScheduleItems: [],
                            isTrashEditButtonDisabled: true,
                            isScheduleItemsGridVisible: false
                        });
                    }
                    else {
                        this.setState({
                            isTrashEditButtonDisabled: false,
                            isScheduleItemsGridVisible: true
                        });
                    }
                });
        }
    }

    //@ts-ignore
    getLocalDateTime(data) {
        var resultParse = Date.parse(data.date + 'Z');
        if (isNaN(resultParse))
            return "";
        var date = new Date(resultParse);
        return date.toLocaleDateString() + " " + date.toLocaleTimeString();
    }
    //#endregion

    //#region ScheduleItems DataGrid functions
    //    .
    getScheduleItemsRows(id: number) {
        //@ts-ignore
        return this.state.scheduleItemsRows.filter(x => x.scheduleId === id)
    }
    //#endregion

    //#region ScheduleItems AddEditForm functions
    //   .
    saveScheduleClick() {
        var schedule = { ...this.state.selectedAvailabilitySchedule };

        var arr: ScheduleItem[] = [];

        this.state.editableScheduleItems.forEach((element) => {
            arr.push({
                "id": element.id,
                "scheduleId": element.scheduleId,
                "weekDay": element.weekDay,
                "startTime": element.startTime,
                "endTime": element.endTime
            })
        });

        schedule.scheduleItems = arr;

        if (this.checkDataForCorrect(schedule) === false) {
            return;
        }

        //@ts-ignore
        schedule.name = schedule.name.trim();
        
        if (schedule.comment != null && schedule.comment != '') {
            //@ts-ignore
            schedule.comment = schedule.comment.trim();
        }

        if (schedule.id == 0) {
            dbwrap.AddRow("AvailabilitySchedules", schedule, null)
                .then((data: dbwrap.responseResult) => {
                    if (data.success) {
                        var currentItem = this.state.selectedAvailabilitySchedule;
                        currentItem.id = data.data.id;

                        this.setState({
                            popupTitle: getLocMsg(LocMsgId.PopupTitle_Information),
                            popupMessage: getLocMsg(LocMsgId.Message_Success),
                            popupVisible: true,
                            addEditFormVisible: false
                        });
                    }
                    else {
                        if (data.data.response != null && data.data.response.status == 453) {
                            this.setState({
                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                popupMessage: getLocMsg(LocMsgId.Message_AvailabilityScheduleSaveError),
                                popupVisible: true
                            });

                            return;
                        }
                        else {
                            let errorMessage: string = ''

                            if (data.data.parsedErrObject == null) {
                                errorMessage = data.data.request.responseText
                            }
                            else {
                                errorMessage = data.data.parsedErrObject.message
                            }

                            this.setState({
                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                popupMessage: errorMessage,
                                popupVisible: true
                            });
                        }
                    }
                    this.handleButtonRefreshClick();
                })
        }
        else {
            //   AvailabilitySchedule.
            dbwrap.UpdateRow("AvailabilitySchedules", schedule, null)
                .then((data: dbwrap.responseResult) => {
                    if (data.success) {
                        this.setState({
                            selectedAvailabilitySchedule: { ...schedule },
                            focusedRowKey: this.state.selectedAvailabilitySchedule.id
                        });

                        this.setState({
                            popupTitle: getLocMsg(LocMsgId.PopupTitle_Information),
                            popupMessage: getLocMsg(LocMsgId.Message_Success),
                            popupVisible: true
                        });
                    }
                    else {
                        if (data.data.response != null && data.data.response.status == 452) {
                            this.setState({
                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                popupMessage: getLocMsg(LocMsgId.Message_AvailabilityScheduleEditError),
                                popupVisible: true
                            });

                            return;
                        }
                        else if (data.data.response.status == 453) {
                            this.setState({
                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                popupMessage: getLocMsg(LocMsgId.Message_AvailabilityScheduleSaveError),
                                popupVisible: true
                            });

                            return;
                        }
                        else {
                            let errorMessage: string = ''

                            if (data.data.parsedErrObject == null) {
                                errorMessage = data.data.request.responseText
                            }
                            else {
                                errorMessage = data.data.parsedErrObject.message
                            }

                            this.setState({
                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                popupMessage: errorMessage,
                                popupVisible: true
                            });
                        }
                    }

                    this.setState({
                        addEditFormVisible: false
                    });

                    this.handleButtonRefreshClick();
                });
        }
    }

    //       .
    cancelAddEditFormClick() {
        this.setState({
            addEditFormVisible: false,
            selectedAvailabilitySchedule: { ...this.state.previousAvailabilityItem },
            focusedRowKey: this.state.previousAvailabilityItem.id
        });
    }

    //#region ScheduleItems DataGrid functions
    onScheduleItemsFocusedRowChanged(e: any) {
        if (e == null || e.row == null) {
            return;
        }

        var data = e.row.data;
        if (data == null) {
            return;
        }

        this.setState({
            selectedScheduleItem: { ...data },
            scheduleItemsFocusedRowKey: data.weekDay
        });
    }

    onScheduleItemsContentReady(e: any) {
        var item = this.state.selectedScheduleItem;
        var key = item.weekDay;
        var keyExistsInFilteredRows = -1;
        var filteredRows: any;

        if (item.id != 0) {
            this.setState({ previousScheduleItem: { ...item } });
        }

        //       .
        this.setState({ selectedScheduleItem: { ...item }, scheduleItemsFocusedRowKey: key });

        if (item != null && l_dataGrid_ScheduleItems != null) {
            //           .
            l_dataGrid_ScheduleItems.instance.getDataSource().store()
                .load({ filter: l_dataGrid_ScheduleItems.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                });

            //        .
            keyExistsInFilteredRows = filteredRows.find((x: any) => x.weekDay === key)
        }

        //             Id,    .
        if (item == null || keyExistsInFilteredRows == null) {
            if (l_dataGrid_ScheduleItems != null) {
                //  Key   (  -    ).
                key = l_dataGrid_ScheduleItems.instance.getKeyByRowIndex(0)
            }
        }

        if (key != null && l_dataGrid_ScheduleItems != null) {
            l_dataGrid_ScheduleItems.instance.getDataSource().store().load({ filter: ['weekDay', '=', key] })
                .then((result: any) => {
                    filteredRows = result;
                });
            item = filteredRows.find((x: any) => x.weekDay === key)
        }

        this.setState({ selectedScheduleItem: { ...item }, scheduleItemsFocusedRowKey: key });

        // ,   ,    .
        if (l_dataGrid_ScheduleItems != null) {
            //           .
            l_dataGrid_ScheduleItems.instance.getDataSource().store()
                .load({ filter: l_dataGrid_ScheduleItems.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                });

            if (filteredRows.length === 0) {
                this.setState({
                    editScheduleItemVisible: false,
                    removeScheduleItemVisible: false
                });
            }
        }
    }

    //   .
    addScheduleItem() {
        this.getWeekDays();

        this.setState({
            previousScheduleItem: { ...this.state.selectedScheduleItem },
            selectedScheduleItem: { id: 0 },
            weekDaySelectBoxHidden: false,
            scheduleItemFormVisible: true
        });

        this.resetScheduleItemForCorrect();
    }

    //   .
    editScheduleItem() {
        this.getWeekDays();

        this.setState({
            previousScheduleItem: { ...this.state.selectedScheduleItem },
            weekDaySelectBoxHidden: true,
            scheduleItemFormVisible: true
        });

        this.resetScheduleItemForCorrect();
    }

    //   .
    removeScheduleItem() {
        var items: ScheduleItem[] = this.state.editableScheduleItems;
        items = Object.values(items);

        var index = items.findIndex(x => x.weekDay === this.state.selectedScheduleItem.weekDay);
        items.splice(index, 1);

        this.setState({
            editableScheduleItems: items
        });

        l_dataGrid_ScheduleItems.instance.refresh();
    }
    //#endregion
    //#endregion

    //#region ScheduleItem Form functions
    //   .
    saveScheduleItemClick() {
        var items: ScheduleItem[] = this.state.editableScheduleItems;
        items = Object.values(items);

        var selectedItem: ScheduleItem = this.state.selectedScheduleItem;

        if (this.checkScheduleItemForCorrect(selectedItem) === false) {
            return;
        }

        //@ts-ignore
        if (selectedItem.startTime >= selectedItem.endTime) {
            this.setState({
                scheduleItemIsCorrect: {
                    weekDay: { isValid: true, message: '' },
                    startTime: { isValid: false, message: getLocMsg(LocMsgId.Message_InvalidTime) },
                    endTime: { isValid: false, message: getLocMsg(LocMsgId.Message_InvalidTime) }
                }
            });

            return;
        }

        if (selectedItem.scheduleId == null) {
            //   .
            selectedItem.scheduleId = this.state.selectedAvailabilitySchedule.id;

            items.push(selectedItem);

            this.setState({
                scheduleItemsFocusedRowKey: selectedItem.weekDay
            });
        }
        else {
            //   .
            var index = items.findIndex(x => x.weekDay === this.state.selectedScheduleItem.weekDay);
            items[index] = this.state.selectedScheduleItem;
        }

        this.setState({
            editableScheduleItems: items,
            scheduleItemFormVisible: false,
            editScheduleItemVisible: true,
            removeScheduleItemVisible: true
        });

        l_dataGrid_ScheduleItems.instance.refresh();
    }

    cancelScheduleItemFormClick() {
        this.setState({
            selectedScheduleItem: this.state.previousScheduleItem,
            scheduleItemFormVisible: false
        });
    }

    //   .
    onWeekDayValueChanged(e: any) {
        var item = this.state.selectedScheduleItem;
        item.weekDay = e.value;

        this.setState({
            selectedScheduleItem: item
        });
    }

    //   .
    onStartTimeValueChanged(e: any) {
        if (typeof e.value == 'object') {
            e.value = e.value.toLocaleTimeString('en-US', { hour12: false });
        }

        var splittedStartTime = e.value.split(':');
        var newStartTime = new Date();
        newStartTime.setHours(splittedStartTime[0]);
        newStartTime.setMinutes(splittedStartTime[1]);
        newStartTime.setSeconds(0);
        e.value = newStartTime.toLocaleTimeString();

        var item = this.state.selectedScheduleItem;
        item.startTime = e.value;

        this.setState({
            selectedScheduleItem: item
        });
    }

    //   .
    onEndTimeValueChanged(e: any) {
        if (typeof e.value == 'object') {
            e.value = e.value.toLocaleTimeString('en-US', { hour12: false });
        }

        var splittedEndTime = e.value.split(':');
        var newEndTime = new Date();
        newEndTime.setHours(splittedEndTime[0]);
        newEndTime.setMinutes(splittedEndTime[1]);
        newEndTime.setSeconds(0);
        e.value = newEndTime.toLocaleTimeString();

        var item = this.state.selectedScheduleItem;
        item.endTime = e.value;

        this.setState({
            selectedScheduleItem: item
        });
    }

    //      .
    getWeekDays() {
        var selectedDays: any = [];
        this.state.editableScheduleItems.forEach((element) => selectedDays.push(element.weekDay));

        var allowDays: any = [];

        //@ts-ignore
        this.state.weekDays.forEach((element) => {
            //@ts-ignore
            if (!selectedDays.some(x => x === element.id)) {
                allowDays.push(element);
            }
        });

        this.setState({ availableWeekDays: allowDays });
    }

    //    .
    getWeekDayName(e: ScheduleItem): string {
        var weekDayName: string = "";

        //@ts-ignore
        this.state.weekDays.forEach((element) => {
            if (element.id === e.weekDay) {
                weekDayName = element.name;
            }
        });

        return weekDayName;
    }
    //#endregion

    //#region Popup functions
    //  popup.
    hidePopup() {
        this.setState({ popupVisible: false })
    }

    hidePopupForCriticalError() {
        this.setState({
            popupVisibleForCriticalError: false,
            hasCriticalError: true
        });
    }
    //#endregion

    //#region ConfirmForm functions
    //     .
    confirmDeleteFormClick() {
        this.closeConfirmFormClick();

        //@ts-ignore
        dbwrap.DeleteRow("AvailabilitySchedules", this.state.selectedAvailabilitySchedule.id, null)
            .then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.setState({
                        popupTitle: getLocMsg(LocMsgId.PopupTitle_Information),
                        popupMessage: getLocMsg(LocMsgId.Message_Success),
                        popupVisible: true
                    });
                }
                else {
                    if (data.data.response != null && data.data.response.status == 452) {
                        this.setState({
                            popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                            popupMessage: getLocMsg(LocMsgId.Message_AvailabilityScheduleDeleteError),
                            popupVisible: true
                        });

                        return;
                    }
                    else {
                        let errorMessage: string = ''

                        if (data.data.parsedErrObject == null) {
                            errorMessage = data.data.request.responseText
                        }
                        else {
                            errorMessage = data.data.parsedErrObject.message
                        }

                        this.setState({
                            popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                            popupMessage: errorMessage,
                            popupVisible: true
                        });
                    }
                }

                this.setState({
                    selectedAvailabilitySchedule: { id: 0, name: '' }
                });

                this.handleButtonRefreshClick();
            });
    }

    //    .
    closeConfirmFormClick() {
        this.setState({
            confirmDeleteFormVisible: false
        });
    }
    //#endregion

    //#region Validation functions
    //    .
    onChangeName = (e: any) => {
        var changedItem = this.state.selectedAvailabilitySchedule;

        changedItem.name = e.component._changedValue;
        this.applyChange(changedItem);
    }

    //   .
    onChangeComment = (e: any) => {
        var changedItem = this.state.selectedAvailabilitySchedule;

        changedItem.comment = e.component._changedValue;

        this.applyChange(changedItem);
    }

    //      .
    applyChange(target: any) {
        this.setState({ selectedAvailabilitySchedule: target })
    }

    //   .
    resetDataForCorrect() {
        this.setState({ dataIsCorrect: { name: { isValid: true, message: '' } } })
    }

    //        .
    checkDataForCorrect(model: AvailabilitySchedule): boolean {
        let name = model.name == null ? '' : model.name.trim();

        var nameIsValid = model.name != null && model.name != '' && model.name != undefined && model.name.trim().length != 0
        var nameMessage = nameIsValid ? '' : getLocMsg(LocMsgId.Message_NameRequired)

        if (nameIsValid) {
            nameIsValid = this.state.availabilitySchedulesRows.find(x => x.name.toUpperCase() === name.toUpperCase() && x.id != model.id) === undefined
            nameMessage = nameIsValid ? '' : getLocMsg(LocMsgId.Message_DuplicateName)
        }

        this.setState({ dataIsCorrect: { name: { isValid: nameIsValid, message: nameMessage } } })

        if (model.scheduleItems != null && model.scheduleItems.length == 0) {
            this.setState({
                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                popupMessage: getLocMsg(LocMsgId.Message_ScheduleItemsRequired),
                popupVisible: true
            });
            return false;
        }

        if (nameIsValid === false) {
            let comp = this.state.refComponents.txtName;

            if (comp != null) {
                comp.instance.focus();
            }
        }

        return nameIsValid;
    }

    //    ScheduleItem.
    resetScheduleItemForCorrect() {
        this.setState({
            scheduleItemIsCorrect: {
                weekDay: { isValid: true, message: '' },
                startTime: { isValid: true, message: '' },
                endTime: { isValid: true, message: '' }
            }
        });
    }

    //  ScheduleItem      .
    checkScheduleItemForCorrect(model: ScheduleItem): boolean {
        var weekDayIsValid = model.weekDay != null && model.weekDay != undefined;
        var weekDayMessage = weekDayIsValid ? '' : getLocMsg(LocMsgId.Message_WeekDayRequired);

        var startTimeIsValid = model.startTime != null && model.startTime != undefined;
        var startTimeMessage = startTimeIsValid ? '' : getLocMsg(LocMsgId.Message_StartTimeRequired);

        var endTimeIsValid = model.endTime != null && model.endTime != undefined;
        var endTimeMessage = endTimeIsValid ? '' : getLocMsg(LocMsgId.Message_EndTimeRequired);

        this.setState({
            scheduleItemIsCorrect: {
                weekDay: { isValid: weekDayIsValid, message: weekDayMessage },
                startTime: { isValid: startTimeIsValid, message: startTimeMessage },
                endTime: { isValid: endTimeIsValid, message: endTimeMessage }
            }
        });

        if (weekDayIsValid === false) {
            let comp = this.state.refComponents.selectWeekDay;

            if (comp != null) {
                comp.instance.focus();
                comp.instance.open();
            }
        }
        else if (startTimeIsValid === false) {
            let comp = this.state.refComponents.dateStartTime;

            if (comp != null) {
                comp.instance.focus();
                comp.instance.open();
            }
        }
        else if (endTimeIsValid === false) {
            let comp = this.state.refComponents.dateEndTime;

            if (comp != null) {
                comp.instance.focus();
                comp.instance.open();
            }
        }

        return weekDayIsValid && startTimeIsValid && endTimeIsValid;
    }

    //    .
    checkSelectedRow() {
        if (this.state.focusedRowKey == 0) {
            this.setState({
                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                popupMessage: getLocMsg(LocMsgId.Message_NoSelectedAvailabilitySchedule),
                popupVisible: true
            });
            return false;
        }
        return true;
    }
    //#endregion 
    //#endregion
}

function mapStateToProps(storePar: any, ownProps?: any) {
    return {}
}

