import * as React from 'react';
import { getImageTerminal } from './Images';

export function itemTerminal(data: any) {
    return (
        <table>
            <tr>
                <td>
                    <img src={data && getImageTerminal(data.name)} />
                </td>
                <td>
                    {data && data.terminalState}
                </td>
            </tr>
        </table>
    );
}
