import getLocMsg, { LocMsgId } from '../../localization';

export default function getLabelText(code: string): string {
    switch (code) {
        case 'PageName':
            return getLocMsg(LocMsgId.Menu_Dicts_Monitoring_MailingListUser);

        case 'UserName':
            return getLocMsg(LocMsgId.Column_Username);
        case 'EmailList':
            return getLocMsg(LocMsgId.Column_EmailList);
        case 'IsGroupEmail':
            return getLocMsg(LocMsgId.Column_IsGroupEmail);
        case 'Description':
            return getLocMsg(LocMsgId.Column_Description);

        case 'NeedSelect':
            return getLocMsg(LocMsgId.Message_NeedSelect);
        case 'PopupTitle_Error':
            return getLocMsg(LocMsgId.PopupTitle_Error);

        case 'Message_Error':
            return getLocMsg(LocMsgId.Message_Error);
        case 'Message_DataIntegrityViolationError':
            return getLocMsg(LocMsgId.Message_DataIntegrityViolationError);
        case 'Message_EditMode':
            return getLocMsg(LocMsgId.Message_EditMode);

        case 'Message_Confirm':
            return getLocMsg(LocMsgId.Message_Confirm);
        case 'Message_ConfirmSave':
            return getLocMsg(LocMsgId.Message_Saving);
        case 'Message_ConfirmDelete':
            return getLocMsg(LocMsgId.Message_Deleting);
        case 'Button_Save':
            return getLocMsg(LocMsgId.Button_Save);
        case 'Button_Cancel':
            return getLocMsg(LocMsgId.Button_Cancel);
        case 'Button_Yes':
            return getLocMsg(LocMsgId.Button_Yes);
        case 'Button_No':
            return getLocMsg(LocMsgId.Button_No);

        case 'UserName_Incorrect':
            return getLocMsg(LocMsgId.Message_Required);
        case 'UserName_IsDublicate':
            return getLocMsg(LocMsgId.Message_DuplicateName);
        case 'EMails_Incorrect':
            return getLocMsg(LocMsgId.Message_EMails_Incorrect);
        case 'Message_DirectoryNotAvailable':
            return getLocMsg(LocMsgId.Message_DirectoryNotAvailable);

        case 'Hint_Button_Add':
            return getLocMsg(LocMsgId.Button_Add);
        case 'Hint_Button_Delete':
            return getLocMsg(LocMsgId.Button_Delete);
        case 'Hint_Button_Save':
            return getLocMsg(LocMsgId.Button_Save);
        case 'Hint_Button_Refresh':
            return getLocMsg(LocMsgId.Button_Refresh);
        case 'Hint_Button_Cancel':
            return getLocMsg(LocMsgId.Button_Cancel);
    }

    return '';
}
