import {LangEnum} from '../localization';
export const ACT_CHANGE_LANG = 'ACT_CHANGE_LANG';
export const ACT_JWT_TESTRESULT_CHANGED = 'ACT_JWT_TESTRESULT_CHANGED';
export const ACT_ENABlEDGUIITEMS_CHANGED = 'ACT_ENABlEDGUIITEMS_CHANGED';

export interface AppSettingsState {
    Language: LangEnum;
    JWTTestResult?:boolean,
    EnabledGuiItemIds:any[]
}


const defaultState:AppSettingsState = {
    Language:LangEnum.ru,
    JWTTestResult:undefined,
    EnabledGuiItemIds:[]
}


interface ChangeLangAction {
    type:string,
    payload:any
}


function reducer(state:AppSettingsState = defaultState,action:ChangeLangAction){
    if(action.type !=ACT_CHANGE_LANG && action.type != ACT_JWT_TESTRESULT_CHANGED && ACT_ENABlEDGUIITEMS_CHANGED != ACT_ENABlEDGUIITEMS_CHANGED ){
        return state;
    }
    switch (action.type){
        case ACT_CHANGE_LANG:
            let result = {...state as AppSettingsState,Language:action.payload};
            return result;
        case ACT_JWT_TESTRESULT_CHANGED:
            let tstRes = (action.payload?true:false);
            let res = {...state as AppSettingsState,JWTTestResult:tstRes};
            return res;
        case ACT_ENABlEDGUIITEMS_CHANGED:
            result = {...state as AppSettingsState,EnabledGuiItemIds:action.payload};
            return result;
        default:
            return state;
    }
}

export default reducer;