export function GetGuiElementName(uid: GuiElementsId) {
    let result = GuiElementNames.find((itm) => itm.uid === uid);
    if (result) {
        return result.name;
    }
    return "";
}

interface SideBarTreeViewNode {
    ID: string;
    expanded?: boolean;
    categoryId?: string;
    msgId: LocMsgId;
    path: string;
    icon?: string;
    component?(): React.ReactNode;
};


function getPath(uid: GuiElementsId): string {
    let result = "";
    let uidStr = GuiElementsId[uid];
    uidStr.split("_").forEach(itm => {
        result += "/" + itm;
    });
    return result;
}

function NewSidebarItem(uid: GuiElementsId,
    parentUid: GuiElementsId,
    msgID: LocMsgId,
    icn: string,
    comp?: () => React.ReactNode,
    exp: boolean = false): SideBarTreeViewNode {
    let result: SideBarTreeViewNode = {
        ID: GuiElementsId[uid],
        msgId: msgID,
        path: getPath(uid),
        expanded: exp,
        component: comp
    }
    if (parentUid !== GuiElementsId.None) {
        result.categoryId = GuiElementsId[parentUid]
    }
    if (icn) {
        result.icon = icn;
    }
    return result;
}

