export function GetSidebarItems() {
    let st = store.getState();
    if(!st.AppSettings.JWTTestResult){
        return [];
    }
    let allItems = SidebarItems.map(itm => { return { ...itm, name: getLocMsg(itm.msgId) }; });
    let result = allItems;
    if(st.AppSettings.EnabledGuiItemIds.length>0){
        let isSuperAdmin:boolean = st.AppSettings.EnabledGuiItemIds.some((itm)=>itm.superAdmin===true);
        if(!isSuperAdmin){
            //node leafs
            let leafs = result.filter(itm=>{
                let fitm = st.AppSettings.EnabledGuiItemIds.find(ei=>ei.uid === itm.ID)
                return fitm;
            }); 
            result = [...leafs];
            let addParentNodes = (node:any) =>{
                if(node.categoryId){
                    if(result.findIndex(r=>r.ID === node.categoryId)===-1){
                        let parentNode = allItems.find(itm=>itm.ID === node.categoryId);
                        //@ts-ignore
                        result.push(parentNode);
                        addParentNodes(parentNode);
                    }
                }
            }

            leafs.forEach(itm=>{
                addParentNodes(itm);
            });   
        }
    }
    return result;
}

export function getRoutes() {
    let allNestedItems = SidebarItems.filter(itm => itm.categoryId !== undefined);
    let routes: any = [];
    for (var i: number = 0; i < allNestedItems.length; i++) {
        let nestedItem = allNestedItems[i];
        let routeItem = <Route key={"route" + i} path={nestedItem.path} render={() => {
            if (nestedItem.component) {
                return nestedItem.component();
            }
        }} />;
        routes.push(routeItem);
    }
    return (
        <React.Fragment>
            {routes}
        </React.Fragment>
    );
}
