export function getStatusFilteredList(data: any): any {
    switch (data.state.currentItem.model.stateDeviceCode) {
        case 'Good':
            return data.state.equipmentStatusList.filter((x:any) => x.name === 'Good')
        case 'Warning':
            return data.state.equipmentStatusList.filter((x:any) => x.name === 'Good' || x.name === 'Warning')
        case 'Unknown':
            return data.state.equipmentStatusList.filter((x:any) => x.name === 'Good' || x.name === 'Warning' || x.name === 'Error')
        case 'Error':
            return data.state.equipmentStatusList.filter((x:any) => x.name === 'Good' || x.name === 'Warning' || x.name === 'Error')
        case 'Disabled':
            return data.state.equipmentStatusList.filter((x:any) => x.name === 'Disabled')
    }

    return data.state.equipmentStatusList
}
