import * as React from 'react';
import { getImageDevice, getImageTerminal } from './Images';

export function stateDeviceImage(data: any) {
    return (
        <table>
            <tr>
                <td><img src={data && getImageDevice(data.data.stateDeviceCode)} /></td>
                <td />
                <td>{data && data.displayValue}</td>
            </tr>
        </table>
    );
}

export function stateTerminalImage(data: any) {
    return (
        <table>
            <tr>
                <td><img src={data && getImageTerminal(data.data.stateTerminalCode)} /></td>
                <td />
                <td>{data && data.displayValue}</td>
            </tr>
        </table>
    );
}
