import React from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";


function Icon(size=32,color="#17a2b8") {
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            width={size}
            height={size}
            fillRule="evenodd"
            strokeLinejoin="bevel"
            strokeWidth="0.501"
            overflow="visible"
            viewBox="0 0 96 96"
        >
            <g
                fill="none"
                stroke="#000"
                fontFamily="Times New Roman"
                fontSize="16"
                transform="scale(1 -1)"
            >
                <g transform="translate(0 -96)">
                    <g strokeMiterlimit="79.84">
                        <path
                            stroke={color}
                            strokeLinecap="round"
                            strokeLinejoin="miter"
                            strokeWidth="4.5"
                            d="M8.089 77.737V17.508c0-6.229 5.056-11.285 11.285-11.285h59.329c6.229 0 11.285 5.056 11.285 11.285v60.229c0 6.229-5.056 11.285-11.285 11.285H19.374c-6.229 0-11.285-5.056-11.285-11.285z"
                        ></path>
                        <path
                            fill="#00ff03"
                            stroke="none"
                            strokeLinejoin="miter"
                            strokeWidth="3"
                            d="M23.396 53.191l12.921-37.875 3.364-.178 42.123 56.284-11.858 9.204-27.257-36.814-5.133 14.158-14.16-4.779z"
                        ></path>
                        <path
                            fill="#004c8d"
                            stroke="none"
                            strokeWidth="0.5"
                            d="M40.634 12.369l-6.102.323L20.383 54.17l18.413 6.214 4.449-12.273 26.124 35.284 15.448-11.99-44.183-59.036zm-2.177 4.653L78.52 70.555l-8.268 6.419L41.863 38.63l-5.818 16.043-9.906-3.344 11.693-34.273.625-.034z"
                        ></path>
                    </g>
                </g>
            </g>
            </svg>
    );
}

export default Icon;
