import * as React from 'react';
import getImage from './Images';

export default function Item(data: any) {
    return (
        <table>
            <tr>
                <td>
                    <img src={data && getImage(data.name)} />
                </td>
                <td>
                    {data && data.description}
                </td>
            </tr>
        </table>
    );
}
