import React from "react";


export enum MessageType {
    info = "dx-toast-info",
    warning = "dx-toast-warning",
    error = "dx-toast-error",
    success = "dx-toast-success"
}

export enum ErrorEnum {
    NoError,
    NotSavedRowChanges,
    MSSQL_547,
    MSSQL_2601,
    WrongLoginOrPassword,
    ERR401,
    ERR413,
    NetworkError,
    ServerStatusError,
    ParseFailed,
}

export interface ErrorType {
    id: ErrorEnum,
    message: string,
    type: MessageType
}

export const AllErrors: ErrorType[] = [
    { id: ErrorEnum.NotSavedRowChanges, message: "  ", type: MessageType.warning },
    { id: ErrorEnum.MSSQL_547, message: "  .        ", type: MessageType.error },
    { id: ErrorEnum.MSSQL_2601, message: "     ", type: MessageType.error },
    { id: ErrorEnum.WrongLoginOrPassword, message: "   ", type: MessageType.error },
    { id: ErrorEnum.ERR401, message: "  ", type: MessageType.error },
    { id: ErrorEnum.ERR413, message: "      ", type: MessageType.error },
    { id: ErrorEnum.NetworkError, message: " ", type: MessageType.error },
];

type ServerErrStatus = {
    status:number,
    message:string
}

const ServerErrStatuses:ServerErrStatus[] = [
    {status:452,message:"     "},
    {status:453,message:" "},
    {status:454,message:"     "},
    {status:455,message:"     "},
]

   

export function getProp(obj: any, propName: string): any {
    if(!obj){
        return null;
    }
    if (obj.hasOwnProperty(propName)) {
        //@ts-ignore
        return obj[propName];
    }
    return null;
}
export function getPropEx(obj: any, propChain: string[]): any {
    let currObj = obj;
    for (let i: number = 0; i < propChain.length; i++) {
        let prop = propChain[i];
        let value = getProp(currObj, prop);
        if (!value) {
            return null;
        }
        currObj = value;
    }
    return currObj;
}

export const lastNotParsedError = {
    message:""
} 

function parseFailed(err:any){
    if(!err){
        err = {};
    }
    let result = buildErrObject(ErrorEnum.ParseFailed, err);
    if(err.response && err.response.data && typeof(err.response.data)==="string"){
        lastNotParsedError.message = err.response.data;
        return result;        
    }
    if(err.hasOwnProperty("message")){
        lastNotParsedError.message = getProp(err,"message");
    }
    return result;
}

function buildErrObject(pErrId:ErrorEnum,err:Object){
    if(pErrId === ErrorEnum.ServerStatusError){
        let status = getPropEx(err, ["response", "status"]);
        let srvErr = ServerErrStatuses.find(s=>s.status === status);
        let msg = srvErr?srvErr.message:"";
        let errObj:ErrorType = {id:ErrorEnum.ServerStatusError,message:msg,type:MessageType.error};
        return {parsedErrObject:errObj, parsedErrId: pErrId, ...err };
    }
    let pErrObj = AllErrors.find(e=>e.id === pErrId);
    let resultObject = {parsedErrObject:pErrObj, parsedErrId: pErrId, ...err };
    return resultObject;
}

function MSSQLError(err:any):any{
    let errSrcObj = getPropEx(err, ["response", "data", "InnerException", "Data"]);
    if(!errSrcObj && err.Data){
        errSrcObj = err.Data;
    }
    if(!errSrcObj){
        return parseFailed(err);
    }
    let evtSrc = getProp(errSrcObj, "HelpLink.EvtSrc");
    let evtId = getProp(errSrcObj, "HelpLink.EvtID");
    if (!evtSrc || !evtId || evtSrc !== "MSSQLServer") {
        return ErrorEnum.ParseFailed;
    }    
    if (evtId === "547") {
        return buildErrObject(ErrorEnum.MSSQL_547,err);
    }    
    if (evtId === "2601") {
        return buildErrObject(ErrorEnum.MSSQL_2601,err);
    }    
    return parseFailed(err);
}
 
 
export function errorParser(err: any): any {
    let errStr = err.toString();
    if(errStr === "Error: Network Error"){
        return buildErrObject( ErrorEnum.NetworkError, err);
    }
    let respData = getPropEx(err, ["response", "data"]);
    if(respData && respData === " " || respData==="  "){
        return buildErrObject( ErrorEnum.WrongLoginOrPassword, err);
    }
    let status:any = getPropEx(err, ["status"]);;
    if(err.status){
        status = err.status;
    } else {
        status = getPropEx(err, ["response", "status"]);
    }
    if(status){
        if(status === 401){
            return buildErrObject( ErrorEnum.ERR401, err);
        }
        if(status === 413){
            return buildErrObject( ErrorEnum.ERR413,err);
        }
        if (ServerErrStatuses.find(s=>s.status === status)){
            return buildErrObject(ErrorEnum.ServerStatusError,err);
        }
    }
    return MSSQLError(err);
}