import React, { useRef, useContext, useEffect, useLayoutEffect as useOriginalLayoutEffect, createContext } from 'react';
import { connect } from 'react-redux';
import * as dbwrap from '../DbWrapper';
//import DataGrid,{DataGridHandle,TextEditor,Column,SortColumn,HeaderRendererProps} from 'react-data-grid';

import { LoadingStage, EnumColors, Tooltips } from '../commonTypes';
import { RenderTooltip } from '../helpers';
import { AnmWaitIcon, PlusIcon, CancelIcon, PencilIcon, RefreshIcon } from '../Images/Icons';
import { Alert, Row, OverlayTrigger, NavItem } from "react-bootstrap"
import getLocMsg, { LocMsgId } from '../localization';
import { Tables, GetDGColumns, GetColumns } from '../DTO/Tables';
import DataGrid, {
    Column,
    Grouping,
    GroupPanel,
    Paging,
    SearchPanel,
    ColumnChooser,
    Editing,
    Form,
    Selection,
    Popup,
    Toolbar,
    Lookup,
    Item,
} from 'devextreme-react/data-grid';
import Button from 'devextreme-react/button';
import { ButtonGroup } from 'devextreme-react/button-group';
import { RowValidatingInfo, RowRemovingInfo } from 'devextreme/ui/data_grid';
import { FormEditor } from './FormEditor';
import { ErrorEnum, getProp } from '../Errors';

interface TEPProps {
    TableName: Tables;
    msgId: LocMsgId;
    rows: [];
    loadingStage: LoadingStage;
    error: object;
    currentRowIndex: number;
    actions: {
        GetRows(JSXContext: TableEditorPageClass): Array<any>,
        GetRowIndex(jsxContext: TableEditorPageClass, rowUid: object): number,
        HasChanges(jsxContext: TableEditorPageClass): boolean,
        DeleteRow(jsxContext: TableEditorPageClass, rowUid: number): void,
        RollbackChangesInRedux(jsxContext: TableEditorPageClass): void,
        SelectRow(JSXContext: TableEditorPageClass, rowIndex: number): void,
        SetSelectedRowByUID(jsxContext: TableEditorPageClass, row: object, updateDataGrid: boolean): void,
        SaveRowChangesToRedux(jsxContext: TableEditorPageClass, oldRowValue: any, newRowValue: any): void
        SaveRowChangesOnServer(jsxContext: TableEditorPageClass, newData: any): void,
    };
}


declare type Maybe<T> = T | undefined | null;

export enum TableEditorMode {
    None,
    EditRow,
    AddRow
}

type TEPState = {
    width: number;
    height: number;
    isRowSelected: boolean;
    oldRowValue: any;
    newRowValue: any;
    error: ErrorEnum;
    editorMode: TableEditorMode;
    hasChanges: boolean
}

const headerHeight = 50;
const headerStyle = { marginLeft: "10px", marginTop: "5px", color: EnumColors.border };
const btnGroupStyle = {
    marginLeft: "20px",
    border: "solid 1px " + EnumColors.border,
    borderRadius: "2px",
    padding: "3px"
}

export class TableEditorPageClass extends React.Component<TEPProps, TEPState> {
    gridRef: object;
    constructor(props: TEPProps) {
        super(props);
        this.state = {
            width: window.innerWidth,
            height: window.innerHeight,
            isRowSelected: false,
            oldRowValue: null,
            newRowValue: null,
            error: ErrorEnum.NoError,
            editorMode: TableEditorMode.None,
            hasChanges: false
        };
        this.updateWindowSize = this.updateWindowSize.bind(this);
        this.gridRef = React.createRef();
        this.props.actions.GetRows(this);
        sessionStorage.setItem('locale', 'ru');
        this.handleButtonEditClick = this.handleButtonEditClick.bind(this);
        this.handleButtonAddClick = this.handleButtonAddClick.bind(this);
        this.handleButtonDeleteClick = this.handleButtonDeleteClick.bind(this);
        this.handleContentReady = this.handleContentReady.bind(this);
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateWindowSize);
    }


    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowSize);
        if (this.props.actions.HasChanges(this)) {
            this.props.actions.RollbackChangesInRedux(this);
        }
    }

    updateWindowSize(): void {
        this.setState({ width: window.innerWidth, height: window.innerHeight })
    }

    componentDidUpdate() {
    }

    getColumns(): Column[] {
        return GetDGColumns(this.props.TableName);
    }

    getRows() {
        return this.props.rows;
    }

    getGrid(): DataGrid<any, any> {
        //@ts-ignore
        return this.gridRef.instance;
    }

    getEmptyRow(): object {
        let cols = GetColumns(this.props.TableName);
        let emptyRow = {};
        cols.forEach((itm) => {
            //@ts-ignore
            emptyRow[itm.dataField] = undefined;
        });
        return emptyRow;
    }

    getErrorMessage(): string {
        //@ts-ignore
        return this.props.error.message;
    }

    handleButtonAddClick() {
        if (this.props.actions.HasChanges(this)) {
            this.props.actions.RollbackChangesInRedux(this);
        }
        this.setState({ editorMode: TableEditorMode.AddRow, hasChanges: false, oldRowValue: null, newRowValue: this.getEmptyRow(), error: ErrorEnum.NoError });
    }

    handleRowChange(updatedRowValue: any) {
        this.setState({ newRowValue: updatedRowValue })
        this.props.actions.SaveRowChangesToRedux(this, this.state.oldRowValue, updatedRowValue);
    }

    handleButtonDeleteClick() {
        let currRow = this.props.rows[this.props.currentRowIndex];
        //@ts-ignore
        let rowUid = currRow.id;
        this.props.actions.DeleteRow(this, rowUid);
    }

    handleContentReady() {
        if (this.state.oldRowValue === null && this.state.newRowValue === null) {
            let currRow = this.props.rows[this.props.currentRowIndex];
            this.setState({ oldRowValue: currRow, newRowValue: currRow, isRowSelected: true });
        }
    }

    handleButtonEditClick() {
        //@ts-ignore
        let rowIndex = this.getGrid().getRowIndexByKey(this.getGrid().getSelectedRowKeys()[0]);
        //@ts-ignore        
        this.getGrid().editRow(rowIndex);
    }

    handleRefreshButtonClick() {

    }

    renderWait() {
        return (<h2>{AnmWaitIcon}  ...</h2>);
    }

    renderError() {
        return (<Alert variant={"danger"} >{this.getErrorMessage()}</Alert>);
    }

    render() {
        if (this.props.loadingStage === LoadingStage.loading) {
            return (
                <div style={{ width: "100%" }}>
                    {this.renderWait()}
                </div>
            );
        }
        if (this.props.loadingStage === LoadingStage.error && this.state.error === ErrorEnum.NoError) {
            //return (
            //    <div style={{ width: "100%" }}>
            //        {this.renderError()}
            //    </div>
            //);
        }
        let rowSelected = (this.state.isRowSelected && this.props.currentRowIndex != -1);
        let btnDeleteStyle = rowSelected && this.state.editorMode !== TableEditorMode.AddRow ? { visibility: "visible" } : { visibility: "hidden" };
        let btnAddStyle = this.state.editorMode === TableEditorMode.AddRow ? { borderColor: "lightGreen", borderWidth: "4px" } : {};
        return (
            <div>
                <h3 style={{ display: "inline-block", width: "300px", textAlign: "left" }}>{getLocMsg(this.props.msgId)}</h3>
                <ButtonGroup>
                    <Item key="btnAdd" location="before" disabled={this.state.editorMode === TableEditorMode.AddRow} ><Button style={btnAddStyle} icon="add" onClick={() => this.handleButtonAddClick()} /></Item>
                    <Item key="btnDelete" location="before"><Button style={btnDeleteStyle} icon="remove" onClick={this.handleButtonDeleteClick} /></Item>
                </ButtonGroup>
                <FormEditor tableEditorInstance={this} />
                <DataGrid
                    ref={(gref) => {
                        if (gref) {
                            this.gridRef = gref;
                        }
                    }}
                    dataSource={this.getRows()}
                    focusedRowEnabled={true}
                    focusedRowIndex={this.props.currentRowIndex}
                    keyExpr="id"
                    showBorders={true}
                    onContentReady={() => this.handleContentReady()}
                    onFocusedRowChanging={(e) => {
                        if (this.props.actions.HasChanges(this)) {
                            this.setState({ error: ErrorEnum.NotSavedRowChanges });
                            e.cancel = true;
                            return;
                        }
                        let newRow: object = this.getRows()[e.newRowIndex];
                        //@ts-ignore
                        let newSelectedRowUID: object = newRow.id;
                        this.setState({ isRowSelected: true, oldRowValue: { ...newRow }, newRowValue: { ...newRow }, editorMode: TableEditorMode.None, error: ErrorEnum.NoError });
                        this.props.actions.SetSelectedRowByUID(this, newSelectedRowUID, false);
                    }}
                >
                    <Selection mode="single" />
                    <Editing mode="form" />
                    {this.getColumns()}
                </DataGrid>
            </div>
        );
    }
}

function mapStateToProps(storePar: any, ownProps?: any) {
    let store = storePar;
    let result = store.Table.find((tbl: any) => tbl.tableName === ownProps.TableName);
    result = { ...result, ...storePar.AppSettings };
    return result
}

function mapDispatchToProps(dispatch: any) {
    return {
        actions: {
            GetRows: (jsxContext: TableEditorPageClass): void => {
                return dbwrap.GetRows(jsxContext.props.TableName, dispatch);
            },
            GetRowIndex: (jsxContext: TableEditorPageClass, uid: object): number => {
                let result: number = jsxContext.props.rows.findIndex((r) => {
                    //@ts-ignore
                    let id: number = r.id;
                    //@ts-ignore
                    return id === uid;
                });
                return result;
            },
            HasChanges(jsxContext: TableEditorPageClass) {
                for (let prop in jsxContext.state.oldRowValue) {
                    //@ts-ignore            
                    if (jsxContext.state.newRowValue[prop] !== jsxContext.state.oldRowValue[prop]) {
                        return true;
                    }
                }
                return false;
            },
            DeleteRow(jsxContext: TableEditorPageClass, rowUid: number) {
                //@ts-ignore 
                dbwrap.DeleteRow(jsxContext.props.TableName, rowUid, dispatch)
                    .then((result: dbwrap.responseResult) => {
                        if (result.success) {
                            let newRowIndex = jsxContext.props.currentRowIndex - 1;
                            if (jsxContext.props.rows.length >= newRowIndex) {
                                jsxContext.props.actions.SelectRow(jsxContext, newRowIndex);
                                let newRow = jsxContext.props.rows[newRowIndex] as object;
                                jsxContext.setState({ isRowSelected: true, oldRowValue: { ...newRow }, newRowValue: { ...newRow }, editorMode: TableEditorMode.None });
                            }
                        } else {
                            if (result.data.hasOwnProperty("parsedErrId")) {
                                let errId: ErrorEnum = getProp(result.data, "parsedErrId");
                                jsxContext.setState({ error: errId });
                            }
                        }
                    });
            },
            RollbackChangesInRedux(jsxContext: TableEditorPageClass) {
                dispatch({
                    type: dbwrap.ACT_ROW_UPDATE,
                    payload: { tableName: jsxContext.props.TableName, row: jsxContext.state.oldRowValue, error: null }
                });
                let value = { ...jsxContext.state.oldRowValue };
                jsxContext.setState({ newRowValue: value, error: ErrorEnum.NoError });
            },
            SaveRowChangesToRedux(jsxContext: TableEditorPageClass, oldRowValue: any, newRowValue: any) {
                if (oldRowValue === undefined) {
                    // add new row
                } else {
                    // update existing row
                    dispatch({
                        type: dbwrap.ACT_ROW_UPDATE,
                        payload: { tableName: jsxContext.props.TableName, row: newRowValue, error: null }
                    });
                }
            },
            SaveRowChangesOnServer(jsxContext: TableEditorPageClass, newData: any) {
                if (jsxContext.state.oldRowValue) {
                    //     
                    jsxContext.props.actions.SaveRowChangesToRedux(jsxContext, jsxContext.state.oldRowValue, jsxContext.state.newRowValue);
                    dbwrap.UpdateRow(jsxContext.props.TableName, newData, dispatch)
                        .then((data: any) => {
                            jsxContext.setState({ oldRowValue: data, newRowValue: data, error: ErrorEnum.NoError })
                            //@ts-ignore
                            jsxContext.getGrid().refresh()
                        })
                        .catch((err: any) => {
                            console.log("err");
                        });
                } else {
                    //   
                    dbwrap.AddRow(jsxContext.props.TableName, newData, dispatch)
                        .then((data: any) => {
                            jsxContext.setState({ oldRowValue: data, newRowValue: data, error: ErrorEnum.NoError, editorMode: TableEditorMode.None })
                            //@ts-ignore
                            jsxContext.getGrid().refresh();
                            let rowUID = data.id;
                            let rowIndex = this.GetRowIndex(jsxContext, rowUID);
                            jsxContext.props.actions.SetSelectedRowByUID(jsxContext, data.id, false);
                        })
                        .catch((err: any) => {
                            console.log("err");
                        });
                }
            },
            SelectRow(jsxContext: TableEditorPageClass, rowIndex: number) {
                dispatch({ type: dbwrap.ACT_SETCURRENTROW_INDEX, payload: { tableName: jsxContext.props.TableName, currentRowIndex: rowIndex } });
            },
            SetSelectedRowByUID: (jsxContext: TableEditorPageClass, rowUID: object, updateDataGrid: boolean = false): void => {
                let rowIndex: number = jsxContext.props.actions.GetRowIndex(jsxContext, rowUID);
                if (rowIndex === -1) return;
                if (!updateDataGrid) {
                    jsxContext.props.actions.SelectRow(jsxContext, rowIndex);
                    return;
                }
                //@ts-ignore
                let pageSize = jsxContext.getGrid().pageSize();
                let newPage = (rowIndex - rowIndex % pageSize) / pageSize + (rowIndex % pageSize === 0 ? -1 : 0);
                //@ts-ignore
                jsxContext.getGrid().pageIndex(newPage);
                //@ts-ignore
                jsxContext.getGrid().selectRows([rowUID]);
            },
        }
    }
}

export const TableEditorPage = connect(mapStateToProps, mapDispatchToProps)(TableEditorPageClass);
