import * as React from 'react';
import { connect } from 'react-redux';
import * as dbwrap from '../../DbWrapper';
import getLocMsg, { LocMsgId } from '../../localization';
import DataGrid, {
    Column,
    ColumnChooser,
    FilterRow,
    HeaderFilter,
    Lookup,
    SearchPanel,
    Selection,
    Sorting,
    Scrolling
} from 'devextreme-react/data-grid';
import Button from 'devextreme-react/button';
import { MessagePopup } from '../../components/CommonComponents/MessagePopup';
import { ConfirmForm } from '../../components/CommonComponents/ConfirmForm';
import { TerminalSchedule } from '../../Models/TerminalSchedule';
import { Terminal } from '../../Models/Terminal';
import SelectBox from 'devextreme-react/select-box';
import TextBox from 'devextreme-react/text-box';
import { AvailabilitySchedule } from '../../Models/AvailabilitySchedule';
import { TypePointPlace } from '../../Models/TypePointPlace';
import { City } from '../../Models/City';
import { Area } from '../../Models/Area';
import { TerminalAttribute } from '../../Models/TerminalAttribute';
import { Attribute } from '../../Models/Attribute';
import { TerminalSchedulesModel } from './TerminalSchedulesModel';

interface TerminalSchedulesProps {

}

type TerminalSchedulesState = {
    terminalScheduleRows: TerminalSchedule[],
    terminalRows: Terminal[],
    availabilityScheduleRows: AvailabilitySchedule[],
    areaRows: Area[],
    cityRows: City[],
    attributeRows: Attribute[],
    terminalAttributeRows: TerminalAttribute[],
    typePointPlaceRows: TypePointPlace[],

    selectedAvailabilitySchedule: AvailabilitySchedule,
    selectedAvailabilityScheduleId: number,

    confirmSaveFormVisible: boolean,
    rowData: any,

    popupTitle: string,
    popupMessage: string,
    popupVisible: boolean,
    popupVisibleForCriticalError: boolean,
    hasCriticalError: boolean,

    terminalSchedulesModel: TerminalSchedulesModel[],
    selectedTerminalSchedulesModel: TerminalSchedulesModel,

    dataGrid: any,

    isVisibleEditArea: boolean,
    isNeedSave: boolean,
    isNeedClearSelection: boolean
}

var l_dataGrid: any = null;

export class TerminalSchedulesDictClass extends React.Component<TerminalSchedulesProps, TerminalSchedulesState> {
    constructor(props: TerminalSchedulesProps) {
        super(props);
        this.state = {
            terminalScheduleRows: [],
            terminalRows: [],
            availabilityScheduleRows: [],
            areaRows: [],
            cityRows: [],
            attributeRows: [],
            terminalAttributeRows: [],
            typePointPlaceRows: [],

            selectedAvailabilitySchedule: { id: 0, name: '' },
            selectedAvailabilityScheduleId: 0,

            confirmSaveFormVisible: false,
            rowData: [],

            popupTitle: "",
            popupMessage: "",
            popupVisible: false,
            popupVisibleForCriticalError: false,
            hasCriticalError: false,

            terminalSchedulesModel: [],
            selectedTerminalSchedulesModel: { id: 0 },

            dataGrid: {},

            isVisibleEditArea: false,
            isNeedSave: false,
            isNeedClearSelection: false
        };

        //#region Toolbar buttons functions
        this.handleButtonSaveClick = this.handleButtonSaveClick.bind(this);
        this.handleButtonRefreshClick = this.handleButtonRefreshClick.bind(this);
        //#endregion

        //#region TerminalSchedules DataGrid functions
        this.getTerminalSchedulesModelRows = this.getTerminalSchedulesModelRows.bind(this);
        this.checkSelection = this.checkSelection.bind(this);
        this.onSelectionChanged = this.onSelectionChanged.bind(this);
        this.onRowClick = this.onRowClick.bind(this);
        this.onCellClick = this.onCellClick.bind(this);
        this.onOptionChanged = this.onOptionChanged.bind(this);
        this.onContentReady = this.onContentReady.bind(this);
        this.onInitialized = this.onInitialized.bind(this);
        this.onValueChanged = this.onValueChanged.bind(this);
        this.getDateTime = this.getDateTime.bind(this);
        //#endregion

        //#region Popup functions
        this.hidePopup = this.hidePopup.bind(this);
        this.hidePopupForCriticalError = this.hidePopupForCriticalError.bind(this);
        //#endregion

        //#region ConfirmForm functions
        this.confirmFormClick = this.confirmFormClick.bind(this);
        this.closeConfirmFormClick = this.closeConfirmFormClick.bind(this);
        //#endregion
    }

    //#region Render
    render() {
        return (
            <div>
                <h3 style={{ width: "auto", textAlign: "center" }}>
                    {getLocMsg(LocMsgId.Menu_Dicts_Managements_TerminalSchedule)}
                </h3>

                <div hidden={this.state.hasCriticalError === false}>
                    <div style={{ height: '80vh' }} className="d-flex flex-column justify-content-center align-items-center">
                        <h1>{getLocMsg(LocMsgId.Message_DirectoryNotAvailable)}</h1>
                    </div>
                </div>

                <div hidden={this.state.hasCriticalError}>
                    <Button icon="save" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNeedSave === false} hint={getLocMsg(LocMsgId.Button_Save)} onClick={this.handleButtonSaveClick} className='mb-2 mr-2' />
                    <Button icon="refresh" activeStateEnabled={false} hoverStateEnabled={false} hint={getLocMsg(LocMsgId.Button_Refresh)} onClick={this.handleButtonRefreshClick} className='mb-2 mr-2' />

                    <div className="mt-3" hidden={this.state.isVisibleEditArea === false}>
                        <div className='row mb-2'>
                            <div className='col-2 my-auto'>
                                {getLocMsg(LocMsgId.Column_Schedule)}:
                            </div>
                            <div className='col'>
                                <SelectBox items={this.state.availabilityScheduleRows} displayExpr="name" placeholder={getLocMsg(LocMsgId.Message_NeedSelect)}
                                    valueExpr="id" onValueChanged={this.onValueChanged} value={this.state.selectedAvailabilitySchedule.id} />
                            </div>
                        </div>

                        <div className='row'>
                            <div className='col-2 my-auto'>
                                {getLocMsg(LocMsgId.Column_EditDate)}:
                            </div>
                            <div className='col'>
                                <TextBox readOnly={true} value={this.getDateTime(this.state.selectedAvailabilitySchedule.date)} />
                            </div>
                        </div>
                    </div>

                    <ConfirmForm title={getLocMsg(LocMsgId.Message_Confirm)} message={getLocMsg(LocMsgId.Message_ConfirmSaveTerminalSchedules) + this.state.rowData.length}
                        visible={this.state.confirmSaveFormVisible} confirmButtonText={getLocMsg(LocMsgId.Button_Save)} confirmForm={this.confirmFormClick}
                        cancelButtonText={getLocMsg(LocMsgId.Button_Cancel)} cancelForm={this.closeConfirmFormClick} />

                    <MessagePopup title={this.state.popupTitle} hidePopup={this.hidePopup} message={this.state.popupMessage} visible={this.state.popupVisible} />
                    <MessagePopup title={this.state.popupTitle} hidePopup={this.hidePopupForCriticalError} message={this.state.popupMessage} visible={this.state.popupVisibleForCriticalError} />

                    <br />

                    <DataGrid
                        //@ts-ignore
                        dataSource={this.state.terminalSchedulesModel}
                        keyExpr="id"
                        showBorders={true}
                        onSelectionChanged={this.onSelectionChanged}
                        onRowClick={this.onRowClick}
                        onCellClick={this.onCellClick}
                        onOptionChanged={this.onOptionChanged}
                        onContentReady={this.onContentReady}
                        ref={(ref) => { l_dataGrid = ref }}
                        onInitialized={this.onInitialized}>
                        <Scrolling mode="virtual" />
                        <FilterRow visible={true} />
                        <HeaderFilter visible={true} />
                        <ColumnChooser enabled={true} mode="select" />
                        <Sorting mode="multiple" />
                        <SearchPanel visible={true} />

                        <Column dataField="name" dataType="string" caption={getLocMsg(LocMsgId.Column_Name)} />
                        <Column dataField="areaId" caption={getLocMsg(LocMsgId.Column_Area)} alignment="left">
                            <Lookup dataSource={this.state.areaRows} valueExpr="id" displayExpr="name" />
                        </Column>
                        <Column dataField="cityId" caption={getLocMsg(LocMsgId.Column_City)} alignment="left">
                            <Lookup dataSource={this.state.cityRows} valueExpr="id" displayExpr="name" />
                        </Column>
                        <Column dataField="address" dataType="string" caption={getLocMsg(LocMsgId.Column_Address)} />
                        <Column dataField="attributeId" caption={getLocMsg(LocMsgId.Column_AttributeType)} alignment="left">
                            <Lookup dataSource={this.state.attributeRows} valueExpr="id" displayExpr="name" />
                        </Column>
                        <Column dataField="typePointPlaceId" caption={getLocMsg(LocMsgId.Column_TypePointPlace)} alignment="left">
                            <Lookup dataSource={this.state.typePointPlaceRows} valueExpr="id" displayExpr="name" />
                        </Column>
                        <Column dataField="scheduleId" caption={getLocMsg(LocMsgId.Column_Schedule)} alignment="left">
                            <Lookup dataSource={this.state.availabilityScheduleRows} valueExpr="id" displayExpr="name" />
                        </Column>
                        <Column dataField="applyDate" dataType="datetime" caption={getLocMsg(LocMsgId.Column_ScheduleApplyDate)} alignment="left" />
                        <Selection
                            deferred="true"
                            mode="multiple"
                            selectAllMode="allPages"
                            showCheckBoxesMode="always"
                            allowSelectAll="true" />
                    </DataGrid>
                </div>
            </div>
        );
    }
    //#endregion

    //#region Code
    componentDidMount() {
        (async () => {
            await dbwrap.GetRows("AvailabilitySchedules", null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    var schedules: AvailabilitySchedule[] = [];

                    //    .
                    data.data.forEach((element: AvailabilitySchedule) => {
                        element.date = new Date(element.date + 'Z');
                        schedules.push(element);
                    });

                    this.setState({ availabilityScheduleRows: schedules });
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                        popupMessage: errorMessage,
                        popupVisibleForCriticalError: true
                    });

                    return;
                }

                (async () => {
                    await dbwrap.GetRows("TerminalSchedules", null).then((data: dbwrap.responseResult) => {
                        if (data.success) {
                            var terminalSchedules: TerminalSchedule[] = [];

                            //    .
                            data.data.forEach((element: TerminalSchedule) => {
                                element.date = new Date(element.date + 'Z');
                                terminalSchedules.push(element);
                            });

                            this.setState({ terminalScheduleRows: terminalSchedules });
                        }
                        else {
                            let errorMessage: string = ''

                            if (data.data.parsedErrObject == null) {
                                errorMessage = data.data.request.responseText
                            }
                            else {
                                errorMessage = data.data.parsedErrObject.message
                            }

                            this.setState({
                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                popupMessage: errorMessage,
                                popupVisibleForCriticalError: true
                            });

                            return;
                        }

                        (async () => {
                            await dbwrap.GetRows("Terminals", null).then((data: dbwrap.responseResult) => {
                                if (data.success) {
                                    this.setState({ terminalRows: data.data });
                                }
                                else {
                                    let errorMessage: string = ''

                                    if (data.data.parsedErrObject == null) {
                                        errorMessage = data.data.request.responseText
                                    }
                                    else {
                                        errorMessage = data.data.parsedErrObject.message
                                    }

                                    this.setState({
                                        popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                        popupMessage: errorMessage,
                                        popupVisibleForCriticalError: true
                                    });

                                    return;
                                }

                                (async () => {
                                    await dbwrap.GetRows("TypePointPlaces", null).then((data: dbwrap.responseResult) => {
                                        if (data.success) {
                                            this.setState({ typePointPlaceRows: data.data });
                                        }
                                        else {
                                            let errorMessage: string = ''

                                            if (data.data.parsedErrObject == null) {
                                                errorMessage = data.data.request.responseText
                                            }
                                            else {
                                                errorMessage = data.data.parsedErrObject.message
                                            }

                                            this.setState({
                                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                                popupMessage: errorMessage,
                                                popupVisibleForCriticalError: true
                                            });

                                            return;
                                        }

                                        (async () => {
                                            await dbwrap.GetRows("Cities", null).then((data: dbwrap.responseResult) => {
                                                if (data.success) {
                                                    this.setState({ cityRows: data.data });
                                                }
                                                else {
                                                    let errorMessage: string = ''

                                                    if (data.data.parsedErrObject == null) {
                                                        errorMessage = data.data.request.responseText
                                                    }
                                                    else {
                                                        errorMessage = data.data.parsedErrObject.message
                                                    }

                                                    this.setState({
                                                        popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                                        popupMessage: errorMessage,
                                                        popupVisibleForCriticalError: true
                                                    });

                                                    return;
                                                }

                                                (async () => {
                                                    await dbwrap.GetRows("Areas", null).then((data: dbwrap.responseResult) => {
                                                        if (data.success) {
                                                            this.setState({ areaRows: data.data });
                                                        }
                                                        else {
                                                            let errorMessage: string = ''

                                                            if (data.data.parsedErrObject == null) {
                                                                errorMessage = data.data.request.responseText
                                                            }
                                                            else {
                                                                errorMessage = data.data.parsedErrObject.message
                                                            }

                                                            this.setState({
                                                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                                                popupMessage: errorMessage,
                                                                popupVisibleForCriticalError: true
                                                            });

                                                            return;
                                                        }

                                                        (async () => {
                                                            await dbwrap.GetRows("Attributes", null).then((data: dbwrap.responseResult) => {
                                                                if (data.success) {
                                                                    this.setState({ attributeRows: data.data });
                                                                }
                                                                else {
                                                                    let errorMessage: string = ''

                                                                    if (data.data.parsedErrObject == null) {
                                                                        errorMessage = data.data.request.responseText
                                                                    }
                                                                    else {
                                                                        errorMessage = data.data.parsedErrObject.message
                                                                    }

                                                                    this.setState({
                                                                        popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                                                        popupMessage: errorMessage,
                                                                        popupVisibleForCriticalError: true
                                                                    });

                                                                    return;
                                                                }

                                                                (async () => {
                                                                    await dbwrap.GetRows("TerminalAttributes", null).then((data: dbwrap.responseResult) => {
                                                                        if (data.success) {
                                                                            this.setState({ terminalAttributeRows: data.data });

                                                                            this.getTerminalSchedulesModelRows();

                                                                            this.render();
                                                                        }
                                                                        else {
                                                                            let errorMessage: string = ''

                                                                            if (data.data.parsedErrObject == null) {
                                                                                errorMessage = data.data.request.responseText
                                                                            }
                                                                            else {
                                                                                errorMessage = data.data.parsedErrObject.message
                                                                            }

                                                                            this.setState({
                                                                                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                                                                                popupMessage: errorMessage,
                                                                                popupVisibleForCriticalError: true
                                                                            });
                                                                        }
                                                                    });
                                                                })();
                                                            });
                                                        })();
                                                    });
                                                })();
                                            });
                                        })();
                                    });
                                })();
                            });
                        })();
                    });
                })();
            });
        })();
    }

    //#region Toolbar buttons functions
    //    .
    handleButtonSaveClick() {
        var localThis = this;

        if (this.state.selectedAvailabilitySchedule.id == 0) {
            this.setState({
                popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                popupMessage: getLocMsg(LocMsgId.Message_NoSelectedAvailabilitySchedule),
                popupVisible: true
            });
            return;
        }

        //@ts-ignore
        this.state.dataGrid.getSelectedRowsData().then((rowData) => {
            //    .
            localThis.setState({
                rowData: rowData,
                confirmSaveFormVisible: true
            })
        });
    }

    //  grid.
    handleButtonRefreshClick() {
        this.componentDidMount();
    }
    //#endregion

    //#region TerminalSchedules DataGrid functions
    //     datagrid.
    getTerminalSchedulesModelRows() {
        if (this.state.availabilityScheduleRows.length == 0 || this.state.terminalRows.length == 0)
            return [];

        var arr: TerminalSchedulesModel[] = [];

        this.state.terminalRows.forEach((element) => {
            var terminalScheduleItem = this.state.terminalScheduleRows.find(x => x.terminalId == element.id);
            if (terminalScheduleItem == undefined)
                terminalScheduleItem = {} as TerminalSchedule;
            //@ts-ignore
            var selectedAvailability = this.state.availabilityScheduleRows.find(x => x.id == terminalScheduleItem.scheduleId);
            if (selectedAvailability == undefined)
                selectedAvailability = {} as AvailabilitySchedule;

            var city = this.state.cityRows.find(x => x.id == element.cityId);
            if (city == undefined)
                city = {} as City;

            var terminalAttribute = this.state.terminalAttributeRows.find(x => x.terminalId == element.id);
            if (terminalAttribute == undefined)
                terminalAttribute = {} as TerminalAttribute

            arr.push({
                "id": element.id,
                "name": element.name,
                "areaId": city.areaId,
                "cityId": element.cityId,
                "address": element.address,
                "attributeId": terminalAttribute.attributeId,
                "typePointPlaceId": element.typePointPlaceId,
                "scheduleId": selectedAvailability.id,
                //@ts-ignore
                "applyDate": terminalScheduleItem.date,
                "terminalScheduleId": terminalScheduleItem.id
            });
        })

        this.setState({
            terminalSchedulesModel: arr
        });

        this.state.dataGrid.refresh();
    }

    checkSelection() {
        var localThis = this;

        //@ts-ignore
        this.state.dataGrid.getSelectedRowsData().then((rowData) => {
            if (rowData.length == 0) {
                localThis.setState({ isNeedSave: false });
            }
            else if (rowData.length == 1 && rowData[0].scheduleId == localThis.state.selectedAvailabilitySchedule.id) {
                localThis.setState({ isNeedSave: false });
            }
            else {
                localThis.setState({ isNeedSave: true });
            }
        });
    }

    onSelectionChanged(e: any) {
        this.checkSelection();
    }

    onRowClick(e: any) {
        this.setState({
            selectedTerminalSchedulesModel: { ...e.data },
            isNeedSave: false
        });

        if (e.data.scheduleId != null) {
            this.setState({ selectedAvailabilityScheduleId: e.data.scheduleId });
            this.onValueChanged({ value: e.data.scheduleId });
        }
    }

    onCellClick(e: any) {
        if (e.columnIndex != 0 && e.rowType == 'data') {
            this.state.dataGrid.clearSelection();

            var checkbox = this.state.dataGrid.getCellElement(e.rowIndex, 0);
            if (checkbox != null) {
                checkbox.click();
            }
            return;
        }

        //      .
        if (this.state.isNeedClearSelection) {
            this.state.dataGrid.clearSelection();
            this.setState({ isNeedClearSelection: false });
            this.state.dataGrid.refresh();
        }

        var localThis = this;

        if (e.rowType == 'data') {
            this.checkSelection();
        }

        if (e.rowType == 'header') {
            //    checkbox.
            if (e.columnIndex == 0) {
                var localThis = this;

                //@ts-ignore
                this.state.dataGrid.getSelectedRowsData().then((rowData) => {
                    //@ts-ignore
                    if (rowData.length == 0) {
                        localThis.setState({ isNeedSave: false });
                    }
                    else {
                        localThis.setState({ isNeedSave: true });
                    }
                });
            }
            else {
                this.setState({ isNeedClearSelection: true });
            }
        }

        if (e.rowType == 'filter') {
            this.setState({ isNeedClearSelection: true });
        }
    }

    onOptionChanged(e: any) {
        if (e.fullName.endsWith('.filterValues')) {
            this.setState({ isNeedClearSelection: true });
        }
    }

    onContentReady(e: any) {
        var item: TerminalSchedulesModel = { id: 0 };
        var key = 0;
        var keyExistsInFilteredRows = -1;
        var filteredRows: any;

        if (item != null && l_dataGrid != null) {
            //           .
            l_dataGrid.instance.getDataSource().store()
                .load({ filter: l_dataGrid.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                });

            //        .
            keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)
        }

        //             Id,    .
        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {
            if (l_dataGrid != null) {
                //  Key   (  -    ).
                key = l_dataGrid.instance.getKeyByRowIndex(0)
            }
        }

        if (key != null && l_dataGrid != null) {
            l_dataGrid.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    filteredRows = result;
                });
            item = filteredRows.find((x: any) => x.id === key)
        }

        this.setState({ selectedTerminalSchedulesModel: { ...item } });

        if (item.scheduleId != null) {
            this.onValueChanged({ value: item.scheduleId });

            var checkbox = this.state.dataGrid.getCellElement(0, 1);
            if (checkbox != null) {
                checkbox.click();
            }
        }

        // ,   ,    .
        if (l_dataGrid != null) {
            //           .
            l_dataGrid.instance.getDataSource().store()
                .load({ filter: l_dataGrid.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                });

            if (filteredRows.length === 0) {
                this.setState({
                    isVisibleEditArea: false,
                    isNeedSave: false
                });
            }
            else {
                this.setState({ isVisibleEditArea: true });
            }
        }
    }

    onInitialized(e: any) {
        this.setState({
            dataGrid: e.component
        })
    }

    //    .
    onValueChanged(e: any) {
        if (this.state.selectedAvailabilitySchedule.id == 0) {
            this.setState({
                //@ts-ignore
                selectedAvailabilitySchedule: this.state.availabilityScheduleRows.find(x => x.id == e.value),
                selectedAvailabilityScheduleId: e.value
            });
        }
        else {
            var newSchedule = this.state.availabilityScheduleRows.find(x => x.id == e.value);

            var localThis = this;

            //@ts-ignore
            this.state.dataGrid.getSelectedRowsData().then((rowData) => {
                //@ts-ignore
                if (rowData.length == 1) {
                    //@ts-ignore
                    if (rowData[0].scheduleId == newSchedule.id) {
                        localThis.setState({ isNeedSave: false });
                    }
                    else {
                        localThis.setState({ isNeedSave: true });
                    }
                }
            });

            this.setState({
                //@ts-ignore
                selectedAvailabilitySchedule: newSchedule
            });
        }
    }

    //@ts-ignore
    getDateTime(data) {
        if (data == null)
            return "";

        var date = new Date(data);
        return date.toLocaleDateString() + ", " + ("0" + date.getHours()).slice(-2) + ":" + ("0" + date.getMinutes()).slice(-2);
    }
    //#endregion

    //#region Popup functions
    //  popup.
    hidePopup() {
        this.setState({ popupVisible: false })
    }

    hidePopupForCriticalError() {
        this.setState({
            popupVisibleForCriticalError: false,
            hasCriticalError: true
        });
    }
    //#endregion

    //#region ConfirmForm functions
    //     .
    confirmFormClick() {
        this.closeConfirmFormClick()

        var terminalSchedules: TerminalSchedule[] = [];
        this.state.rowData.forEach((element: TerminalSchedulesModel) => {
            terminalSchedules.push({
                "id": element.terminalScheduleId,
                "scheduleId": this.state.selectedAvailabilitySchedule.id,
                "terminalId": element.id
            });
        });
        //  TerminalSchedule.
        dbwrap.UpdateRow("TerminalSchedules", terminalSchedules, null).then((data: dbwrap.responseResult) => {
            if (!data.success) {
                let errorMessage: string = ''

                if (data.data.parsedErrObject == null) {
                    errorMessage = data.data.request.responseText
                }
                else {
                    errorMessage = data.data.parsedErrObject.message
                }

                this.setState({
                    popupTitle: getLocMsg(LocMsgId.PopupTitle_Error),
                    popupMessage: errorMessage,
                    popupVisible: true
                });
            }

            this.handleButtonRefreshClick();
        })
    }

    //    .
    closeConfirmFormClick() {
        this.setState({
            confirmSaveFormVisible: false
        });
    }
    //#endregion
    //#endregion
}

function mapStateToProps(storePar: any, ownProps?: any) {
    return {}
}

function mapDispatchToProps(dispatch: any) {
    return {
        actions: {

        }
    }
}

export const TerminalSchedulesDict = connect(mapStateToProps, mapDispatchToProps)(TerminalSchedulesDictClass);