import * as React from 'react';
import { Popup, Position } from 'devextreme-react/popup';
import { ToolbarItem } from 'devextreme-react/data-grid';
import getLocMsg, { LocMsgId } from '../../localization';
import { TextArea } from 'devextreme-react';

interface MessagePopupProps {
    title: string,
    message: string,
    visible: boolean,
    hidePopup(): void
}

export class MessagePopup extends React.Component<MessagePopupProps> {
    closeButtonOptions: { text: string; onClick: () => void; };

    constructor(props: MessagePopupProps) {
        super(props);

        this.cancelPopupClick = this.cancelPopupClick.bind(this);
        
        this.closeButtonOptions = {
            text: getLocMsg(LocMsgId.Button_Close),
            onClick: this.cancelPopupClick,
          };
    }

    cancelPopupClick() {
        this.props.hidePopup()
    }

    render() {
        return (
            <Popup
                visible={this.props.visible}
                showTitle={false}
                width={500}
                height={300}>
                <Position at="center" my="center" />

                <TextArea
                    height={150}
                    value={this.props.message}
                    readOnly={true}
                />

                <ToolbarItem location="before" text={this.props.title}></ToolbarItem>
                <ToolbarItem location="after" toolbar="bottom" widget="dxButton" options={this.closeButtonOptions}></ToolbarItem>
            </Popup>
        );
    }
}