import * as Disabled from './Images/Disabled.png';
import * as DeviceWorking from './Images/DeviceWorking.png';
import * as DeviceWarning from './Images/DeviceWarning.png';
import * as DeviceNotWorking from './Images/DeviceNotWorking.png';
import * as DeviceUndetermined from './Images/DeviceUndetermined.png';
import * as PassiveMode from './Images/PassiveMode.png';

import * as NA from './Images/NA.png';
import * as TerminalNotWorking from './Images/TerminalNotWorking.png';
import * as TerminalWarning from './Images/TerminalWarning.png';
import * as TerminalWorking from './Images/TerminalWorking.png';

export function getImageDevice(code?: string): any {
    switch (code) {
        case 'Disabled':       //
            return Disabled;
        case 'Good':       //
            return DeviceWorking;
        case 'Warning':       //
            return DeviceWarning;
        case 'Error':       // 
            return DeviceNotWorking;
        case 'Unknown':       // 
            return DeviceUndetermined;
        case 'Inserving':       // 
            return PassiveMode;
    }

    return null;
}

export function getImageTerminal(code?: string): any {
    switch (code) {
        case 'Disabled':
            return NA;
        case 'Good':
            return TerminalWorking;
        case 'Warning':
            return TerminalWarning;
        case 'Error':
            return TerminalNotWorking;
    }

    return null;
}