import React from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";


function Icon(size=32,color="#17a2b8") {
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            width={size}
            height={size}
            fillRule="evenodd"
            strokeLinejoin="bevel"
            strokeWidth="0.501"
            overflow="visible"
            viewBox="0 0 96 96"
        >
            <g
                fill="none"
                stroke="#000"
                fontFamily="Times New Roman"
                fontSize="16"
                transform="scale(1 -1)"
            >
                <g transform="translate(0 -96)">
                    <path
                        stroke={color}
                        strokeLinecap="round"
                        strokeLinejoin="miter"
                        strokeMiterlimit="79.84"
                        strokeWidth="4.5"
                        d="M8.089 77.737V17.508c0-6.229 5.056-11.285 11.285-11.285h59.329c6.229 0 11.285 5.056 11.285 11.285v60.229c0 6.229-5.056 11.285-11.285 11.285H19.374c-6.229 0-11.285-5.056-11.285-11.285z"
                    ></path>
                </g>
            </g>
            </svg>
    );
}

export default Icon;
