import * as dbwrap from '../DbWrapper';
import { MessageType, errorParser, ErrorEnum, getProp } from '../Errors';
import { TableEditorMode } from './TableEditorPage';

export interface ICurrentRow {
    oldRowValue: any;
    newRowValue: any;
}

interface ITableEditor {
    tableName: string,
    getCurrentRow(): ICurrentRow,
    setCurrentRow(currRow: ICurrentRow): void,
    getCurrentRowIndex(): number,
    setCurrentRowIndex(idx: number): void,
    onRowListChanged(rows: object[]): void,
    onError(errorId: ErrorEnum): void
}

export class TEControllerBase {
    editorMode: TableEditorMode = TableEditorMode.None;
    tblEditor: ITableEditor;
    prevCurrRow: any;
    rows: any[];
    constructor(tableEditor: ITableEditor) {
        this.tblEditor = tableEditor;
        this.rows = [];
        dbwrap.GetRows(this.tblEditor.tableName, null).then((result: dbwrap.responseResult) => {
            if (result.success) {
                this.rows = result.data;
                this.tblEditor.onRowListChanged(result.data);
                if (this.rows.length > 0) {
                    //@ts-ignore
                    let currRow = this.rows[0];
                    this.HandleSelectNewCurrentRow(currRow);
                }
            } else {
                this._handleError(result);
            }
        });
        this.SaveChanges = this.SaveChanges.bind(this);
        this.CancelChanges = this.CancelChanges.bind(this);
        this.HandleFieldChange = this.HandleFieldChange.bind(this);
        this.prevCurrRow = null;
    }

    HasChanges() {
        let currRow = this.tblEditor.getCurrentRow();
        for (let prop in currRow.oldRowValue) {
            //@ts-ignore            
            if (currRow.newRowValue[prop] !== currRow.oldRowValue[prop]) {
                return true;
            }
        }
        return false;
    }

    _handleError(result: dbwrap.responseResult) {
        let parsedErr = errorParser(result.data);
        if (parsedErr.hasOwnProperty("parsedErrId")) {
            let errId: ErrorEnum = getProp(parsedErr, "parsedErrId");
            this.tblEditor.onError(errId);
            return;
        }
    }

    _checkNotSavedChanges() {
        if (this.HasChanges()) {
            this.tblEditor.onError(ErrorEnum.NotSavedRowChanges);
            return true;
        }
        return false;
    }

    _getEmptyRow() {
        let currRow = this.tblEditor.getCurrentRow();
        let emptyRow = { ...currRow.oldRowValue };
        for (let prop in currRow.oldRowValue) {
            emptyRow[prop] = undefined;
        }
        return emptyRow;
    }

    HandleFieldChange(e: any, fieldName: string) {
        let newValue = "";
        if (e.event && e.event.currentTarget) {
            newValue = e.event.currentTarget.value;
        }
        if (e.value) {
            newValue = e.value;
        }
        //@ts-ignore
        let currRow = this.tblEditor.getCurrentRow();
        currRow.newRowValue[fieldName] = newValue;
        this.tblEditor.setCurrentRow({ ...currRow });
        if (this.editorMode !== TableEditorMode.AddRow) {
            this.editorMode = TableEditorMode.EditRow;
        }
    }

    HandleSelectNewCurrentRow(newRow: any): boolean {
        if (this._checkNotSavedChanges()) {
            return false;
        }
        this.tblEditor.setCurrentRow({ newRowValue: { ...newRow }, oldRowValue: newRow });
        this.editorMode = TableEditorMode.None;
        this.tblEditor.onError(ErrorEnum.NoError);
        return true;
    }

    SaveChanges() {
        if(!this.HasChanges()){
            return dbwrap.returnOkResult();
        }
        if (this.editorMode === TableEditorMode.EditRow) {
            let newRowVal = this.tblEditor.getCurrentRow().newRowValue;
            return dbwrap.UpdateRow(this.tblEditor.tableName, newRowVal, null).then((result: dbwrap.responseResult) => {
                if (result.success) {
                    this.tblEditor.setCurrentRow({ newRowValue: newRowVal, oldRowValue: { ...newRowVal } });
                    //@ts-ignore
                    let crIdx = this.rows.findIndex((itm) => itm.id === newRowVal.id);
                    //@ts-ignore
                    this.rows[crIdx] = newRowVal;
                    this.tblEditor.onRowListChanged(this.rows);
                    this.editorMode = TableEditorMode.None;
                    this.tblEditor.onError(ErrorEnum.NoError);
                } else {
                    this._handleError(result);
                }
                return result;
            });
        }
        if (this.editorMode === TableEditorMode.AddRow) {
            let newRowVal = this.tblEditor.getCurrentRow().newRowValue;
            dbwrap.AddRow(this.tblEditor.tableName, newRowVal, null).then((result: dbwrap.responseResult) => {
                if (result.success) {
                    let newRow = result.data;
                    //@ts-ignore
                    this.rows.push(newRow);
                    this.tblEditor.setCurrentRow({ newRowValue: { ...newRow }, oldRowValue: newRow });
                    this.tblEditor.onRowListChanged(this.rows);
                    this.tblEditor.setCurrentRowIndex(this.rows.length + 1);
                    this.editorMode = TableEditorMode.None;
                    this.tblEditor.onError(ErrorEnum.NoError);
                } else {                    
                    this._handleError(result);
                }
                return result;
            });
        }
    }

    CancelChanges() {
        if (this.editorMode === TableEditorMode.AddRow) {
            this.tblEditor.setCurrentRow(this.prevCurrRow);
            this.editorMode = TableEditorMode.None;
            this.tblEditor.onError(ErrorEnum.NoError);
            return;
        }
        let oldRowVal = this.tblEditor.getCurrentRow().oldRowValue;
        let currRowVal = { newRowValue: { ...oldRowVal }, oldRowValue: oldRowVal };
        this.tblEditor.setCurrentRow(currRowVal);
        this.editorMode = TableEditorMode.None;
        this.tblEditor.onError(ErrorEnum.NoError);
    }

    AddRow() {
        this.prevCurrRow = this.tblEditor.getCurrentRow();
        let newRowVal = this._getEmptyRow();
        let oldRowVal = { ...newRowVal };
        let currRowVal = { newRowValue: newRowVal, oldRowValue: { ...newRowVal } };
        this.tblEditor.setCurrentRow(currRowVal);
        this.editorMode = TableEditorMode.AddRow;
        this.tblEditor.onError(ErrorEnum.NoError);
    }

    DeleteRow() {
        if (this.rows.length > 0) {
            //@ts-ignore
            let cr = this.tblEditor.getCurrentRow();
            let rowUid = cr.oldRowValue.id;
            dbwrap.DeleteRow(this.tblEditor.tableName, rowUid, null).then((result: dbwrap.responseResult) => {
                if (result.success) {
                    //@ts-ignore
                    this.rows = this.rows.filter((itm) => itm.id !== rowUid);
                    let currRowIndex = this.tblEditor.getCurrentRowIndex();
                    if (currRowIndex > 0) {
                        currRowIndex--;
                    }
                    let curRow = this.rows[currRowIndex] as object;
                    this.tblEditor.setCurrentRow({ newRowValue: { ...curRow }, oldRowValue: curRow });
                    this.tblEditor.onRowListChanged(this.rows);
                    this.tblEditor.setCurrentRowIndex(currRowIndex);
                    this.editorMode = TableEditorMode.None;
                    this.tblEditor.onError(ErrorEnum.NoError);
                } else {
                    this._handleError(result);
                }
            });
        }
    }
}