import React from 'react';
import Button from 'devextreme-react/button';
import { ButtonGroup } from 'devextreme-react/button-group';
import * as dbwrap from '../DbWrapper';
import Tooltip from "react-bootstrap/Tooltip";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import {RenderTooltip} from "../helpers";

import DataGrid, {
    Column,
    Item,
    Grouping,
    GroupPanel,   
    HeaderFilter,
    FilterRow, 
    Editing,
    Selection,
    FieldLookup,
} from 'devextreme-react/data-grid';
import ContextMenu from 'devextreme-react/context-menu';
import {updateState,GetUpdateStateImg,GetUpdateTooltipText} from '../commonTypes';
import getLocMsg, { LocMsgId } from '../localization';
import { itemTerminal } from '../DictionaryComponents/DeviceStatus/Item';


interface TupField {
    name:string,
    title:string,
    cellRender?(rowData:any):JSX.Element
}

const TupRow:TupField[] = [
    {name:"id",title:"ID "},
    {name:"filial",title:""},
    {name:"name",title:""},
    {name:"typeDev",title:" "},
    {name:"subnet",title:""},
    {name:"address",title:""},
    {name:"pingdate",title:"   "},
    {name:"currVer",title:" "},
    {name:"lastTup",title:"  "},
    {name:"status",title:" "},
    {name:"notes",title:""},
    {name:"inServDate",title:"  "},
    {name:"comment",title:""},
];

interface TaskTupPageState {
    width: number,
    height: number,
    currentRowIndex: number,
    rows:any[],
    btnDwnlEnabled:boolean,
    btnInstallEnabled:boolean,
    btnCancelDwnlEnabled:boolean,
    btnCancelInstallEnabled:boolean,
    btnRollbackEnabled:boolean,
    currentRow: object,
}

export class TaskTupPage extends React.Component<any, TaskTupPageState> {
    tableName: string = "Users";
    gridRef: object;
    constructor(props: any) {
        super(props);
        this.state = {
            width: window.innerWidth,
            height: window.innerHeight,
            currentRowIndex: -1,
            rows:[],
            btnDwnlEnabled:false,
            btnInstallEnabled:false,
            btnCancelDwnlEnabled:false,
            btnCancelInstallEnabled:false,
            btnRollbackEnabled:false,
            currentRow: { newRowValue: {}, oldRowValue: {} },
        }
        this.updStatusRender = this.updStatusRender.bind(this);
        this.hadleSelectionChange = this.hadleSelectionChange.bind(this);
        //@ts-ignore
        TupRow.find(itm=>itm.name==="status").cellRender = this.updStatusRender;         
        this.gridRef = React.createRef();
    }

    componentDidMount(){
        this.loadRows();
    }

    getHeight(elm: string) {
        let heightBeforeGrid = 280;
        let heightForTviewsAndGrid = this.state.height - heightBeforeGrid - 50;
        let tViewsKoef = 0.4;//        Treeview ("GUI "  "")
        let gridKoef = 1 - tViewsKoef;  //        
        let tViewsHeight = heightForTviewsAndGrid * tViewsKoef;
        let gridHeight = heightForTviewsAndGrid * gridKoef;
        if (elm === "tview") {
            return tViewsHeight;
        }
        if (elm === "grid") {
            return gridHeight;
        }
        throw "wrong elm " + elm;
    }


    getGrid(): DataGrid<any, any> {
        //@ts-ignore
        return this.gridRef.instance;
    }


    getCurrentRowIndex(): number {
        return this.state.currentRowIndex;
    }

    onRowListChanged(rows: any[]) {
        let currInd = this.state.currentRowIndex;
        //@ts-ignore
        this.getGrid().refresh();
    }

    setCurrentRowIndex(idx: number) {
        this.setState({ currentRowIndex: idx });
    }



    loadRows(){
    this.setState({rows:[   
                            {id:"1",filial:"F1",name:"1",typeDev:"",subnet:"",status:updateState.New},
                            {id:"2",filial:"F1",name:"1",typeDev:"",subnet:"",status:updateState.InProcess},
                            {id:"3",filial:"F1",name:"1",typeDev:"",subnet:"",status:updateState.Complete},
                            {id:"4",filial:"F2",name:"2",typeDev:"",subnet:"",status:updateState.Error},
                            {id:"5",filial:"F2",name:"2",typeDev:"",subnet:""},
                            {id:"6",filial:"F2",name:"2",typeDev:"",subnet:""},
                            {id:"7",filial:"F2",name:"2",typeDev:"",subnet:""},
                            {id:"8",filial:"F2",name:"2",typeDev:"",subnet:""},
                            {id:"9",filial:"F2",name:"2",typeDev:"",subnet:""},
                            {id:"10",filial:"F2",name:"2",typeDev:"",subnet:""},
                        ],currentRowIndex:0});
    }
    
    getColumns(){
        let result:JSX.Element[] = [];
        TupRow.forEach((fld)=>{
            let newItem = <Column key={fld.name} dataField={fld.name} caption={fld.title} />;
            if(fld.cellRender){
                newItem = <Column key={fld.name} dataField={fld.name} caption={fld.title} cellRender={fld.cellRender} />;
            }
            result.push(newItem);
        });
        return result;
    }

    updStatusRender(cellData:any):JSX.Element{
        let isactive = this.state.currentRowIndex === cellData.rowIndex;
        let color = isactive?"white":undefined;
        let toolTipMsg:string = GetUpdateTooltipText(cellData.data.status);
        return( 
            <OverlayTrigger placement="right"  overlay={(props)=>RenderTooltip(props,toolTipMsg)}>
                <div>
                    {GetUpdateStateImg(cellData.data.status,color)}
                </div>
            </OverlayTrigger>
        );
    }

    hadleSelectionChange(e:any){

        let selrows:[] = e.selectedRowsData;
        let dwnlEnabled = selrows.every((itm:any)=>itm.status === undefined || itm.status === updateState.Unknown);
        this.setState({btnDwnlEnabled:dwnlEnabled})
        
    }

    renderDataGrid() {
        return (
            <DataGrid
                ref={(gref) => {
                    if (gref) {
                        this.gridRef = gref;
                    }
                }}
                style={{ height: this.getHeight("grid") }}
                dataSource={this.state.rows}
                focusedRowEnabled={true}
                focusedRowIndex={this.state.currentRowIndex}
                keyExpr="id"
                onSelectionChanged={this.hadleSelectionChange}
                onFocusedRowChanging={(e) => {
                    this.setState({currentRowIndex:e.newRowIndex});
                }}
            >
                <GroupPanel visible={true} />
                <HeaderFilter visible={true} />
                <Selection mode="multiple" />
                {this.getColumns()}    
            </DataGrid>
        );
    }

    render() {
        let rowSelected = (this.state.currentRowIndex != -1);
        return (<div id="userRolesPage">
            <h3 style={{ display: "inline-block", textAlign: "left" }}>{getLocMsg(LocMsgId.Menu_Reports_AutoUpdates_Tup)}</h3>
            <div style={{ display: "flex", flexDirection:"row",flexWrap:"wrap", marginBottom: "20px", marginTop: "10px" }}>
                <Button icon="download" disabled={!this.state.btnDwnlEnabled} text={getLocMsg(LocMsgId.Button_Update_Download)}  onClick={() => console.log("")} style={{ marginLeft: "10px",marginTop:"10px"}} />
                <Button icon="todo" disabled={!this.state.btnInstallEnabled}  text={getLocMsg(LocMsgId.Button_Update_Install)}  onClick={() => console.log("")} style={{ marginLeft: "10px",marginTop:"10px" }} />
                <Button icon="clear" disabled={!this.state.btnCancelDwnlEnabled} text={getLocMsg(LocMsgId.Button_Update_CancelDownload)}  onClick={() => console.log("")} style={{ marginLeft: "10px",marginTop:"10px" }} />
                <Button icon="clear" disabled={!this.state.btnCancelInstallEnabled} text={getLocMsg(LocMsgId.Button_Update_CancelInstall)}  onClick={() => console.log("")} style={{ marginLeft: "10px",marginTop:"10px" }} />
                <Button icon="clear" disabled={!this.state.btnRollbackEnabled} text={getLocMsg(LocMsgId.Button_Update_Rollback)}  onClick={() => console.log("")} style={{ marginLeft: "10px" ,marginTop:"10px"}} />
            </div>
            {this.renderDataGrid()}
        </div>);
    }
}

