import getLocMsg, { LocMsgId } from '../../localization';

export default function getLabelText(code: string): string {
    switch (code) {
        case 'PageName':
            return getLocMsg(LocMsgId.Menu_Dicts_Monitoring_Event);
        case 'EventName':
            return getLocMsg(LocMsgId.Column_EventName);
        case 'EquipmentEvent':
            return getLocMsg(LocMsgId.Column_EquipmentEvent);
        case 'GroupName':
            return getLocMsg(LocMsgId.Column_GroupName);
        case 'Description':
            return getLocMsg(LocMsgId.Column_Description);
        case 'NeedSelect':
            return getLocMsg(LocMsgId.Message_NeedSelect);
        case 'IncidentCreationWaitTimeOrPercentage':
            return getLocMsg(LocMsgId.Column_IncidentCreationWaitTimeOrPercentage);
        case 'IncidentCreationWaitTime':
            return getLocMsg(LocMsgId.Column_IncidentCreationWaitTime);
        case 'IncidentCreationPercentage':
            return getLocMsg(LocMsgId.Column_IncidentCreationPercentage);
        case 'EquipmentStatus':
            return getLocMsg(LocMsgId.Column_EquipmentStatus);
        case 'CreateIncidentForEvent':
            return getLocMsg(LocMsgId.Column_CreateIncidentForEvent);
        case 'SendEmailToUser':
            return getLocMsg(LocMsgId.Column_SendEmailToUser);
        case 'ConsiderDeviceAvailabilityMode':
            return getLocMsg(LocMsgId.Column_ConsiderDeviceAvailabilityMode);
        case 'PopupTitle_Error':
            return getLocMsg(LocMsgId.PopupTitle_Error);
        case 'Message_Error':
            return getLocMsg(LocMsgId.Message_Error);
        case 'Message_Confirm':
            return getLocMsg(LocMsgId.Message_Confirm);
        case 'Message_ConfirmSave':
            return getLocMsg(LocMsgId.Message_Saving);
        case 'Button_Save':
            return getLocMsg(LocMsgId.Button_Save);
        case 'Button_Cancel':
            return getLocMsg(LocMsgId.Button_Cancel);

        case 'Causes_Incorrect':
            return getLocMsg(LocMsgId.Message_Required);
        case 'ReactionParameter_Incorrect':
            return getLocMsg(LocMsgId.Message_Value_Incorrect);

        case 'Message_DirectoryNotAvailable':
            return getLocMsg(LocMsgId.Message_DirectoryNotAvailable);
        case 'Message_EditMode':
            return getLocMsg(LocMsgId.Message_EditMode);

        case 'Message_MinValue':
            return getLocMsg(LocMsgId.Message_MinValue);
        case 'Message_MaxValue':
            return getLocMsg(LocMsgId.Message_MaxValue);

        case 'Hint_Button_Save':
            return getLocMsg(LocMsgId.Button_Save);
        case 'Hint_Button_Refresh':
            return getLocMsg(LocMsgId.Button_Refresh);
        case 'Hint_Button_Cancel':
            return getLocMsg(LocMsgId.Button_Cancel);
    }

    return '';
}
