import * as React from 'react';
import { connect } from 'react-redux';
import * as dbwrap from '../../DbWrapper';
import DataGrid, {
    Column,
    ColumnChooser,
    FilterRow,
    HeaderFilter,
    Lookup,
    SearchPanel,
    Sorting,
    Pager,
    Paging
} from 'devextreme-react/data-grid';
import Button from 'devextreme-react/button';
import { MessagePopup } from '../../components/CommonComponents/MessagePopup';
import { ConfirmForm } from '../../components/CommonComponents/ConfirmForm';
import TextBox from 'devextreme-react/text-box';
import { User } from '../../Models/User';
import { Popup, Position, ToolbarItem } from 'devextreme-react/popup';
import { Model } from './Model';
import getLabelText from './Labels';
import { EmailPlaceHolder } from '../../Models/issues/EmailPlaceHolder';
import HtmlEditor, { Toolbar, MediaResizing, Item, Variables, TableContextMenu } from 'devextreme-react/html-editor';
import { IncorrectData } from '../IssueEmailTemplates/IncorrectData';
import { Toast } from 'devextreme-react';

const sizeValues = ['8pt', '10pt', '12pt', '18pt', '24pt', '36pt', '48pt'];
const headerValues = [false, 1, 2, 3, 4, 5];
const fontValues = ['Arial', 'Courier New', 'Georgia', 'Impact', 'Lucida Console', 'Tahoma', 'Times New Roman', 'Verdana'];
const escapeCharacters = ['', ''];

interface IssueEmailTemplatesProps {

}

type IssueEmailTemplatesState = {
    data: Model[],
    currentItem: { model: Model, rowKey: number },
    isNeedToSave: boolean,
    isNew: boolean,
    prevCurrentItem?: { model: Model, rowKey: number },

    emailPlaceHolderRows: EmailPlaceHolder[],
    userRows: User[],
    placeHolders: string[],
    
    confirmSaveFormVisible: boolean,
    confirmDeleteFormVisible: boolean,
    htmlEditorFormVisible: boolean,

    isSaveFromHtmlEditor: boolean,

    popupTitle: string,
    popupMessage: string,
    popupVisible: boolean,
    popupVisibleForCriticalError: boolean,
    hasCriticalError: boolean,

    messageBody: string,

    dataIsCorrect: { name: IncorrectData, subject: IncorrectData },

    isVisibleEditArea: boolean,
    isVisibleToastForEdit: boolean,

    refComponents: any
}

const allowedPageSizes = [5, 10, 15, 20];

export class IssueEmailTemplatesDictClass extends React.Component<IssueEmailTemplatesProps, IssueEmailTemplatesState> {
    constructor(props: IssueEmailTemplatesProps) {
        super(props);
        this.state = {
            data: [],
            currentItem: {
                model: { id: 0, name: '' },
                rowKey: 0
            },
            isNeedToSave: false,
            isNew: false,

            emailPlaceHolderRows: [],
            userRows: [],
            placeHolders: [],

            confirmSaveFormVisible: false,
            confirmDeleteFormVisible: false,
            htmlEditorFormVisible: false,

            isSaveFromHtmlEditor: false,

            popupTitle: "",
            popupMessage: "",
            popupVisible: false,
            popupVisibleForCriticalError: false,
            hasCriticalError: false,

            messageBody: "",

            dataIsCorrect: {
                name: { isValid: true },
                subject: { isValid: true }
            },

            isVisibleEditArea: false,
            isVisibleToastForEdit: false,

            refComponents: new Map()
        };

        //#region Render functions
        this.renderEmailHtmlEditor = this.renderEmailHtmlEditor.bind(this);
        //#endregion

        //#region Toolbar buttons functions
        this.handleButtonAddClick = this.handleButtonAddClick.bind(this);
        this.handleButtonSaveClick = this.handleButtonSaveClick.bind(this);
        this.handleButtonRemoveClick = this.handleButtonRemoveClick.bind(this);
        this.handleButtonRefreshClick = this.handleButtonRefreshClick.bind(this);
        this.handleButtonRevertClick = this.handleButtonRevertClick.bind(this);
        this.handleButtonEditEmailClick = this.handleButtonEditEmailClick.bind(this);
        //#endregion

        //#region IssueEmailTemplates DataGrid functions
        this.changeCurrentRow = this.changeCurrentRow.bind(this);
        this.onFocusedRowChanged = this.onFocusedRowChanged.bind(this);
        this.onFocusedRowChanging = this.onFocusedRowChanging.bind(this);
        this.onContentReady = this.onContentReady.bind(this);
        this.onRowClick = this.onRowClick.bind(this);
        this.setToPrevRow = this.setToPrevRow.bind(this);
        this.onToastHiding = this.onToastHiding.bind(this);
        //#endregion

        //#region HtmlEditor functions
        this.htmlEditorValueChanged = this.htmlEditorValueChanged.bind(this);
        this.saveHtmlEditorForm = this.saveHtmlEditorForm.bind(this);
        this.closeHtmlEditorForm = this.closeHtmlEditorForm.bind(this);
        this.onInitialized = this.onInitialized.bind(this);
        //#endregion

        //#region Popup functions
        this.hidePopup = this.hidePopup.bind(this);
        this.hidePopupForCriticalError = this.hidePopupForCriticalError.bind(this);
        //#endregion

        //#region Confirm functions
        this.confirmFormClick = this.confirmFormClick.bind(this);
        this.closeConfirmFormClick = this.closeConfirmFormClick.bind(this);
        this.confirmDeleteFormClick = this.confirmDeleteFormClick.bind(this);
        this.closeConfirmDeleteFormClick = this.closeConfirmDeleteFormClick.bind(this);
        //#endregion

        //#region Validation functions
        this.onChangeName = this.onChangeName.bind(this);
        this.onChangeSubject = this.onChangeSubject.bind(this);
        this.applyChange = this.applyChange.bind(this);
        this.resetDataForCorrect = this.resetDataForCorrect.bind(this);
        this.checkDataForCorrect = this.checkDataForCorrect.bind(this);
        this.checkEmailBody = this.checkEmailBody.bind(this);
        this.checkEmailTemplate = this.checkEmailTemplate.bind(this);
        //#endregion
    }

    //#region Render
    //   HtmlEditor'.
    renderEmailHtmlEditor() {
        if (!this.state.htmlEditorFormVisible) {
            return null;
        }

        return (
            <Popup
                visible={this.state.htmlEditorFormVisible}
                dragEnabled={false}
                closeOnOutsideClick={false}
                showCloseButton={false}
                showTitle={true}
                width="90vw"
                title={this.state.currentItem.model.subject}>
                <Position at="center" my="center" />

                <Button
                    style={{ margin: "0 10px 10px 0" }}
                    text={getLabelText('Button_Save')}
                    type="normal"
                    stylingMode="contained"
                    onClick={this.saveHtmlEditorForm}
                    ref={(ref) => { this.state.refComponents["htmlEditorBtnOk"] = ref }}
                />

                <Button
                    style={{ margin: "0 0 10px 0" }}
                    text={getLabelText('Button_Close')}
                    type="normal"
                    stylingMode="contained"
                    onClick={this.closeHtmlEditorForm}
                />

                <HtmlEditor
                    height="90%"
                    defaultValue={this.state.messageBody}
                    valueType="html"
                    onValueChanged={this.htmlEditorValueChanged}
                    ref={(ref) => { this.state.refComponents["htmlEditor"] = ref }}
                    onInitialized={this.onInitialized}>
                    <MediaResizing enabled={true} />
                    <Toolbar multiline={false}>
                        <Item name="undo" />
                        <Item name="redo" />
                        <Item name="clear" />
                        <Item name="separator" />
                        <Item name="font" acceptedValues={fontValues} />
                        <Item name="separator" />
                        <Item name="header" acceptedValues={headerValues} />
                        <Item name="separator" />
                        <Item name="size" acceptedValues={sizeValues} />
                        <Item name="separator" />
                        <Item name="bold" />
                        <Item name="italic" />
                        <Item name="strike" />
                        <Item name="underline" />
                        <Item name="separator" />
                        <Item name="alignLeft" />
                        <Item name="alignCenter" />
                        <Item name="alignRight" />
                        <Item name="alignJustify" />
                        <Item name="separator" />
                        <Item name="color" />
                        <Item name="background" />
                        <Item name="orderedList" />
                        <Item name="bulletList" />
                        <Item name="separator" />
                        <Item name="link" />
                        <Item name="separator" />
                        <Item name="codeBlock" />
                        <Item name="blockquote" />
                        <Item name="separator" />
                        <Item name="variable" />
                    </Toolbar>
                    <TableContextMenu enabled={true} />
                    <Variables
                        dataSource={this.state.placeHolders}
                        escapeChar={escapeCharacters}
                    />
                </HtmlEditor>
            </Popup>
        );
    }

    render() {
        return (
            <div>
                <h3 style={{ width: "auto", textAlign: "center" }}>
                    {getLabelText('PageName')}
                </h3>

                <div hidden={this.state.hasCriticalError === false}>
                    <div style={{ height: '80vh' }} className="d-flex flex-column justify-content-center align-items-center">
                        <h1>{getLabelText('Message_DirectoryNotAvailable')}</h1>
                    </div>
                </div>

                <div hidden={this.state.hasCriticalError}>
                    <Button icon="add" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNew || this.state.isNeedToSave} onClick={this.handleButtonAddClick} hint={getLabelText('Hint_Button_Add')} className='mb-2 mr-2' />
                    <Button icon="trash" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNew || this.state.isVisibleEditArea === false || this.state.isNeedToSave} onClick={this.handleButtonRemoveClick} hint={getLabelText('Hint_Button_Delete')} className='mb-2 mr-2' />
                    <Button icon="save" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNeedToSave === false} onClick={this.handleButtonSaveClick} hint={getLabelText('Hint_Button_Save')} className='mb-2 mr-2' />
                    <Button icon="refresh" activeStateEnabled={false} hoverStateEnabled={false} visible={this.state.isNeedToSave === false && this.state.isNew === false} disabled={this.state.isNew} onClick={this.handleButtonRefreshClick} hint={getLabelText('Hint_Button_Refresh')} className='mb-2 mr-2' />
                    <Button icon="revert" activeStateEnabled={false} hoverStateEnabled={false} visible={this.state.isNeedToSave || this.state.isNew} onClick={this.handleButtonRevertClick} hint={getLabelText('Hint_Button_Cancel')} className='mb-2 mr-2' />

                    <div className="mt-3" hidden={this.state.isVisibleEditArea === false && this.state.isNew === false}>
                        <div className='row mb-2'>
                            <div className='col'>
                                <div className="d-flex justify-content-between align-items-center">
                                    {getLabelText('Name')}:
                                    <TextBox className="w-75" value={this.state.currentItem.model.name}
                                        valueChangeEvent="input" onValueChanged={this.onChangeName} maxLength={100}
                                        isValid={this.state.dataIsCorrect.name.isValid} hint={this.state.dataIsCorrect.name.message}
                                        ref={(ref) => { this.state.refComponents["txtName"] = ref }} />
                                </div>
                            </div>
                            <div className='col'>
                                <Button
                                    text={getLabelText('Button_EditEmailBody')}
                                    type="normal"
                                    stylingMode="contained"
                                    onClick={this.handleButtonEditEmailClick}
                                />
                            </div>
                        </div>

                        <div className='row'>
                            <div className='col-6'>
                                <div className="d-flex justify-content-between align-items-center">
                                    {getLabelText('Subject')}:
                                    <TextBox className="w-75" value={this.state.currentItem.model.subject}
                                        valueChangeEvent="input" onValueChanged={this.onChangeSubject} maxLength={100}
                                        isValid={this.state.dataIsCorrect.subject.isValid} hint={this.state.dataIsCorrect.subject.message}
                                        ref={(ref) => { this.state.refComponents["txtSubject"] = ref }} />
                                </div>
                            </div>
                        </div>
                    </div>

                    {this.renderEmailHtmlEditor()}

                    {/*  */}
                    <ConfirmForm title={getLabelText('Message_Confirm')} message={getLabelText('Message_ConfirmSave')}
                        visible={this.state.confirmSaveFormVisible} confirmButtonText={getLabelText('Button_Save')} confirmForm={this.confirmFormClick}
                        cancelButtonText={getLabelText('Button_Cancel')} cancelForm={this.closeConfirmFormClick} />

                    {/*  */}
                    <ConfirmForm title={getLabelText('Message_Confirm')} message={getLabelText('Message_ConfirmDelete') + '\n' + getLabelText('EmailTemplateName') + ': ' + this.state.currentItem.model.name}
                        visible={this.state.confirmDeleteFormVisible} confirmButtonText={getLabelText('Button_Yes')} confirmForm={this.confirmDeleteFormClick}
                        cancelButtonText={getLabelText('Button_No')} cancelForm={this.closeConfirmDeleteFormClick} />

                    <MessagePopup title={this.state.popupTitle} hidePopup={this.hidePopup} message={this.state.popupMessage} visible={this.state.popupVisible} />
                    <MessagePopup title={this.state.popupTitle} hidePopup={this.hidePopupForCriticalError} message={this.state.popupMessage} visible={this.state.popupVisibleForCriticalError} />

                    <br />

                    <DataGrid
                        dataSource={this.state.data}
                        keyExpr="id"
                        showBorders={true}
                        focusedRowEnabled={true}
                        allowColumnResizing={true}
                        allowColumnReordering={true}
                        onFocusedRowChanged={this.onFocusedRowChanged}
                        onRowClick={this.onRowClick}
                        focusedRowKey={this.state.currentItem.rowKey}
                        onFocusedRowChanging={this.onFocusedRowChanging}
                        autoNavigateToFocusedRow={true}
                        onContentReady={this.onContentReady}
                        ref={(ref) => { this.state.refComponents["dgdData"] = ref }}>
                        <Paging defaultPageSize={10} />
                        <Pager
                            visible={true}
                            allowedPageSizes={allowedPageSizes}
                            displayMode={'full'}
                            showPageSizeSelector={true}
                            showInfo={true}
                            showNavigationButtons={true} />
                        <FilterRow visible={true} />
                        <HeaderFilter visible={true} />
                        <ColumnChooser enabled={true} mode="select" />
                        <Sorting mode="multiple" />
                        <SearchPanel visible={true} />
                        <Column dataField="name" dataType="string" caption={getLabelText('Name')} />
                        <Column dataField="subject" dataType="string" caption={getLabelText('Subject')} />
                        <Column dataField="body" dataType="string" caption={getLabelText('Body')} visible={false} />
                        <Column dataField="userId" caption={getLabelText('Username')} alignment="left">
                            <Lookup dataSource={this.state.userRows} valueExpr="id" displayExpr="username" />
                        </Column>
                    </DataGrid>

                    <Toast
                        visible={this.state.isVisibleToastForEdit}
                        message={getLabelText('Message_EditMode')}
                        type={'warning'}
                        onHiding={this.onToastHiding}
                        displayTime={3000}
                        width={400}
                        position={'center center'}
                    />
                </div>
            </div>
        );
    }
    //#endregion

    //#region Code
    componentDidMount() {
        (async () => {
            await dbwrap.GetRows("EmailTemplates", null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.setState({ data: data.data });
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisibleForCriticalError: true
                    });

                    return;
                }

                (async () => {
                    await dbwrap.GetRows("Users", null).then((data: dbwrap.responseResult) => {
                        if (data.success) {
                            this.setState({ userRows: data.data });
                        }
                        else {
                            let errorMessage: string = ''

                            if (data.data.parsedErrObject == null) {
                                errorMessage = data.data.request.responseText
                            }
                            else {
                                errorMessage = data.data.parsedErrObject.message
                            }

                            this.setState({
                                popupTitle: getLabelText('PopupTitle_Error'),
                                popupMessage: errorMessage,
                                popupVisibleForCriticalError: true
                            });

                            return;
                        }

                        (async () => {
                            await dbwrap.GetRows("EmailPlaceHolders", null).then((data: dbwrap.responseResult) => {
                                if (data.success) {
                                    this.setState({ emailPlaceHolderRows: data.data });

                                    var arr: string[] = [];

                                    //      " - [ ]"  "[ ]".
                                    this.state.emailPlaceHolderRows.forEach((element) => {
                                        arr.push(element.name + " - " + element.placeHolder);
                                        //@ts-ignore
                                        arr.push(element.placeHolder);
                                    });

                                    this.setState({ placeHolders: arr });

                                    this.render();
                                }
                                else {
                                    let errorMessage: string = '';

                                    if (data.data.parsedErrObject == null) {
                                        errorMessage = data.data.request.responseText
                                    }
                                    else {
                                        errorMessage = data.data.parsedErrObject.message
                                    }

                                    this.setState({
                                        popupTitle: getLabelText('PopupTitle_Error'),
                                        popupMessage: errorMessage,
                                        popupVisibleForCriticalError: true
                                    });
                                }
                            });
                        })();
                    });
                })();
            });
        })();
    }

    //#region Toolbar buttons functions
    //   .
    handleButtonAddClick() {
        if (this.state.isNeedToSave) {
            this.setToPrevRow();

            return;
        };

        //    .
        this.setState({ prevCurrentItem: { ...this.state.currentItem } });

        var newItem: Model = { id: 0, name: '' };

        this.setState({ currentItem: { model: newItem, rowKey: newItem.id } });
        this.setState({ isNew: true });

        this.resetDataForCorrect();

        let first = this.state.refComponents.txtName;

        if (first != null) {
            //      .
            first.instance.focus();
        }
    }

    //   .
    handleButtonSaveClick() {
        if (this.state.currentItem.model.id === 0 && this.state.isNew === false) {
            this.setState({ isNeedToSave: false });
            return;
        }

        if (this.state.isNeedToSave) {
            //    .
            this.setState({ confirmSaveFormVisible: true });
        }
    }

    //   .
    handleButtonRemoveClick() {
        if (this.state.data.find(x => x.id === this.state.currentItem.model.id) == null) { return; }

        //    .
        this.setState({ confirmDeleteFormVisible: true });
    }

    //  grid.
    handleButtonRefreshClick() {
        this.componentDidMount();
    }

    //  .
    handleButtonRevertClick() {
        this.closeConfirmFormClick('cancel');
    }

    //        .
    handleButtonEditEmailClick() {
        var item = this.state.currentItem.model;

        if (this.checkDataForCorrect(item) === false) {
            return;
        }

        //   .
        var emailTemplate = { ...item };
        var message = String(emailTemplate.body);

        if (message != "undefined") {
            var firstIndex = message.indexOf("<body>");
            message = message.slice(firstIndex + 6);

            var lastIndex = message.indexOf("</body>");
            message = message.slice(0, lastIndex);
        }
        else {
            message = '';
        }

        this.setState({
            messageBody: message,
            htmlEditorFormVisible: true
        })
    }
    //#endregion

    //#region IssueEmailTemplates DataGrid functions
    changeCurrentRow(e: Model) {
        this.setState({ isNew: false });

        if (e == null) { return }

        const dataRow = e;

        this.setState({
            currentItem: {
                model: { ...dataRow },
                rowKey: dataRow.id
            },
        })
        this.setState({ isNeedToSave: false });

        this.resetDataForCorrect();
    }

    onFocusedRowChanged(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow();

            return;
        }

        this.changeCurrentRow(e.row && e.row.data);
    }

    onFocusedRowChanging(e: any) {
        if (this.state.isNeedToSave) {
            e.cancel = true;

            return;
        }
    }

    onContentReady(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow();

            return;
        }

        var item = this.state.currentItem.model;
        var key = item.id;
        var keyExistsInFilteredRows = -1;
        var filteredRows: any;

        //        , ,     .
        this.onFocusedRowChanged(item);
        //   isEmailTemplateEdited,    .
        if (this.state.isNeedToSave) { return }

        //       .
        this.setState({ currentItem: { model: { ...item }, rowKey: key } });

        let dgdData = this.state.refComponents.dgdData;

        if (item != null && dgdData != null) {
            //           .
            dgdData.instance.getDataSource().store()
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                });

            //        .
            keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)
        }

        //             Id,    .
        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {
            if (dgdData != null) {
                //  Key   (  -    ).
                key = dgdData.instance.getKeyByRowIndex(0)
            }
        }

        if (key != null && dgdData != null) {
            dgdData.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    filteredRows = result;
                });
            item = filteredRows.find((x: any) => x.id === key)
        }

        this.setState({ currentItem: { model: { ...item }, rowKey: key } });

        // ,   ,    .
        if (dgdData != null) {
            //           .
            dgdData.instance.getDataSource().store()
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    //      filteredRows.
                    filteredRows = result;
                });

            if (filteredRows.length === 0) {
                this.setState({ isVisibleEditArea: false });
            }
            else {
                this.setState({ isVisibleEditArea: true });
            }
        }
    }

    onRowClick(e: any) {
        if (this.state.currentItem.model.id === e.data.id) { return }

        if (this.state.isNeedToSave) {
            this.setToPrevRow();

            return;
        };

        if (this.state.currentItem.model.id === 0 || this.state.currentItem.model.id == null) {
            this.changeCurrentRow(e.data);
        }
    }

    setToPrevRow() {
        this.setState({ isVisibleToastForEdit: true });

        let dgdData = this.state.refComponents.dgdData;

        if (dgdData != null) {
            if (this.state.prevCurrentItem != null) {
                dgdData.instance.navigateToRow(this.state.prevCurrentItem.model.id);
            }
            if (this.state.currentItem != null && this.state.currentItem.model.id != 0) {
                dgdData.instance.navigateToRow(this.state.currentItem.model.id);
            }
        }
    }

    onToastHiding() {
        this.setState({ isVisibleToastForEdit: false });
    }
    //#endregion

    //#region HtmlEditor functions
    //     .
    htmlEditorValueChanged(e: any) {
        var changedItem = { ...this.state.currentItem.model };

        var isNeedRender: boolean = false;
        if (e.value == '') {
            changedItem.body = undefined;
        }
        else {
            var body: string = e.value;

            if (body.indexOf("\t") !== -1) {
                body = e.previousValue;
                isNeedRender = true;
            }
            //    &nbsp;  >  <.
            var isReplace: boolean = false;
            var newValue: string = "";

            for (var i = 0; i < body.length; i++) {
                if (body.charAt(i) == '>') {
                    isReplace = true;
                }

                if (body.charAt(i) == '<') {
                    isReplace = false;
                }

                if (isReplace) {
                    if (body.charAt(i) == ' ') {
                        newValue += '&nbsp;';
                    }
                    else {
                        newValue += body.charAt(i);
                    }
                }
                else {
                    newValue += body.charAt(i);
                }
            }

            this.setState({ messageBody: newValue });

            var email = '<!DOCTYPE html><html><head><style>p { white-space: nowrap; }</style></head><body>' + newValue + '</body></html>'
            changedItem.body = email;
        }

        this.applyChange(changedItem);
        if (isNeedRender) {
            let htmlEditor = this.state.refComponents.htmlEditor;
            if (htmlEditor != null) {
                htmlEditor.instance.undo();
                htmlEditor.instance.clearHistory();
            }
        }
    }

    //     .
    saveHtmlEditorForm() {
        if (!this.checkEmailBody()) return;

        if (this.state.isNeedToSave) {
            this.setState({ isSaveFromHtmlEditor: true });

            //  .
            this.handleButtonSaveClick();
        }
        else {
            this.closeHtmlEditorForm();
        }
    }

    //      .
    closeHtmlEditorForm() {
        this.setState({
            htmlEditorFormVisible: false,
            isSaveFromHtmlEditor: false
        });
    }

    onInitialized(e: any) {
        e.component.registerKeyHandler('tab', (e: any) => {
            let htmlEditorBtnOk = this.state.refComponents.htmlEditorBtnOk;
            if (htmlEditorBtnOk != null) {
                htmlEditorBtnOk.instance.focus();
            }
        });
    }
    //#endregion

    //#region Popup functions
    //  popup.
    hidePopup() {
        this.setState({ popupVisible: false })
    }

    hidePopupForCriticalError() {
        this.setState({
            popupVisibleForCriticalError: false,
            hasCriticalError: true
        });
    }
    //#endregion

    //#region Confirm functions
    //     .
    confirmFormClick() {
        var item = this.state.currentItem.model

        if (this.checkEmailTemplate(item) === false) {
            this.setState({ confirmSaveFormVisible: false });

            return;
        }

        item.name = item.name == null ? '' : item.name.trim();
        item.subject = item.subject == null ? '' : item.subject.trim();

        if (this.state.currentItem.model.id === 0) {
            //   .
            dbwrap.AddRow("EmailTemplates", this.state.currentItem.model, null)
                .then((data: dbwrap.responseResult) => {
                    if (data.success) {
                        var currentItem = this.state.currentItem.model
                        currentItem.id = data.data.id

                        if (this.state.isSaveFromHtmlEditor) {
                            this.setState({
                                popupTitle: getLabelText('PopupTitle_Information'),
                                popupMessage: getLabelText('Message_Success'),
                                popupVisible: true
                            });

                            this.setState({
                                isSaveFromHtmlEditor: false,
                                htmlEditorFormVisible: false
                            });
                        }

                        this.handleButtonRefreshClick();
                    }
                    else {
                        let errorMessage: string = ''

                        if (data.data.parsedErrObject == null) {
                            errorMessage = data.data.request.responseText
                        }
                        else {
                            errorMessage = data.data.parsedErrObject.message
                        }

                        this.setState({
                            popupTitle: getLabelText('PopupTitle_Error'),
                            popupMessage: errorMessage,
                            popupVisible: true
                        });
                    }
                })
        }
        else {
            dbwrap.UpdateRow("EmailTemplates", this.state.currentItem.model, null)
                .then((data: dbwrap.responseResult) => {
                    if (data.success) {
                        if (this.state.isSaveFromHtmlEditor) {
                            this.setState({
                                popupTitle: getLabelText('PopupTitle_Information'),
                                popupMessage: getLabelText('Message_Success'),
                                popupVisible: true
                            });

                            this.setState({
                                isSaveFromHtmlEditor: false,
                                htmlEditorFormVisible: false
                            });

                            this.closeConfirmFormClick('saved');
                        }

                        this.handleButtonRefreshClick();
                    }
                    else {
                        let errorMessage: string = ''

                        if (data.data.parsedErrObject == null) {
                            errorMessage = data.data.request.responseText
                        }
                        else {
                            errorMessage = data.data.parsedErrObject.message
                        }

                        this.setState({
                            popupTitle: getLabelText('PopupTitle_Error'),
                            popupMessage: errorMessage,
                            popupVisible: true
                        });
                    }
                })
        }

        this.closeConfirmFormClick('saved');
    }

    //     .
    closeConfirmFormClick(action?: string) {
        if (this.state.isSaveFromHtmlEditor && !this.state.isNew) {
            this.setState({
                confirmSaveFormVisible: false,
                isNeedToSave: true
            });
        }
        else {
            if (action != 'saved') { action = 'cancel' }

            this.setState({
                confirmSaveFormVisible: false,
                isNeedToSave: false
            });

            if (this.state.currentItem != null && this.state.currentItem != undefined && action == 'cancel') {
                var item = this.state.data.find(x => x.id === this.state.currentItem.model.id);

                if (item != null && item != undefined) {
                    this.setState({ currentItem: { model: { ...item }, rowKey: this.state.currentItem.rowKey } });
                }
            }

            if (this.state.currentItem.model.id === 0) {
                this.setState({ currentItem: { model: { id: 0, name: '' }, rowKey: 0 } });

                if (this.state.isNew && this.state.prevCurrentItem != null) {
                    this.setState({ currentItem: { ...this.state.prevCurrentItem } });
                }

                this.setState({ isNew: false });
            }

            this.setState({ prevCurrentItem: undefined });

            this.resetDataForCorrect();

            this.handleButtonRefreshClick();
        }
    }

    //    .
    confirmDeleteFormClick() {
        var item = this.state.currentItem.model;

        dbwrap.DeleteRow("EmailTemplates", item.id, null)
            .then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.handleButtonRefreshClick();
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisible: true
                    });
                }
            })

        this.resetDataForCorrect();

        this.closeConfirmDeleteFormClick();
    }

    //    .
    closeConfirmDeleteFormClick() {
        this.setState({
            confirmDeleteFormVisible: false,
            isNeedToSave: false
        });

        this.handleButtonRefreshClick();

        if (this.state.data.length <= 1) {
            this.setState(
                {
                    currentItem: { model: { id: 0, name: '' }, rowKey: 0 },
                    isNew: false,
                    isNeedToSave: false
                }
            )
        }

        this.resetDataForCorrect();
    }
    //#endregion

    //#region Validation functions
    //      .
    onChangeName = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.name = e.value;

        this.applyChange(changedItem);
    }

    //     .
    onChangeSubject = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.subject = e.value;

        this.applyChange(changedItem);
    }

    //      .
    applyChange(target: any) {
        let item = this.state.data.find(x => x.id === target.id);
        let currentItem: string = "";

        if (item != null) {
            currentItem = JSON.stringify(item);
        }

        let newItem = JSON.stringify(target);

        let hasChanged = currentItem != newItem

        this.setState({ currentItem: { model: target, rowKey: this.state.currentItem.rowKey } });

        this.setState({ isNeedToSave: hasChanged });
    }

    //   .
    resetDataForCorrect() {
        this.setState({ dataIsCorrect: { name: { isValid: true, message: '' }, subject: { isValid: true, message: '' } } })
    }

    //        .
    checkDataForCorrect(model: Model): boolean {
        let name = model.name == null ? '' : model.name.trim();

        var nameIsValid = model.name != null && model.name != '' && model.name != undefined && model.name.trim().length != 0
        var nameMessage = nameIsValid ? '' : getLabelText('Name_Incorrect')

        if (nameIsValid) {
            nameIsValid = this.state.data.find(x => x.name.toUpperCase() === name.toUpperCase() && x.id != model.id) === undefined
            nameMessage = nameIsValid ? '' : getLabelText('Name_IsDublicate')
        }

        var subjectIsValid = model.subject != null && model.subject != '' && model.subject != undefined && model.subject.trim().length != 0
        var subjectMessage = subjectIsValid ? '' : getLabelText('Subject_Incorrect')

        this.setState({ dataIsCorrect: { name: { isValid: nameIsValid, message: nameMessage }, subject: { isValid: subjectIsValid, message: subjectMessage } } })

        if (nameIsValid === false) {
            let comp = this.state.refComponents.txtName;

            if (comp != null) {
                comp.instance.focus();
            }
        }
        else if (subjectIsValid === false) {
            let comp = this.state.refComponents.txtSubject;

            if (comp != null) {
                comp.instance.focus();
            }
        }

        return nameIsValid && subjectIsValid
    }

    //   .
    checkEmailBody() {
        if (this.state.currentItem.model.body == null || this.state.currentItem.model.body == '') {
            this.setState({
                popupTitle: getLabelText('PopupTitle_Error'),
                popupMessage: getLabelText('Body_Incorrect'),
                popupVisible: true
            });
            return false;
        }
        return true;
    }

    //   .
    checkEmailTemplate(item: Model): boolean {
        return this.checkEmailBody() && this.checkDataForCorrect(item);
    }
    //#endregion
    //#endregion
}

