import React, { ReactNode } from 'react';
import {ErrorEnum,MessageType,AllErrors,lastNotParsedError} from '../Errors';
import Button from 'devextreme-react/button';

export type ErrorMsgProps = {
    message:string,
    type:MessageType
}


export function ErrorMsg({message,type}:ErrorMsgProps){
    if(!message){
        return (<div />);
    } 
    let clist = "dx-overlay-content dx-toast-content dx-resizable " + type;
    return(
        <div className={clist} style={{position:"relative"}} >
            <div className="dx-toast-icon"/>    
            <div className="dx-toast-message" role="alert">{message}</div>
        </div>
        );
}



export function ErrorMsgYesNo(emObj:ErrorMsgProps,callBackFunc:(result:boolean)=>void){
    if(!emObj.message){
        return (<div />);
    } 
    let clist = "dx-overlay-content dx-toast-content dx-resizable " + emObj.type;
    return(
        <div className={clist} style={{position:"relative"}} >
            <div className="dx-toast-icon"/>    
            <div className="dx-toast-message" role="alert">{emObj.message}</div>
            <div>
                <Button style={{marginLeft:"10px"}} text="" onClick={()=>callBackFunc(true)} /> 
                <Button style={{marginLeft:"10px"}} text="" onClick={()=>callBackFunc(false)} />
            </div>
        </div>
        );
}


export function ErrorMsgFromDict(errId:ErrorEnum ){
    if(errId === ErrorEnum.NoError){
        return (<div />);
    }
    if(errId === ErrorEnum.ParseFailed){
        let msg = lastNotParsedError.message;
        return ErrorMsg({message:msg,type:MessageType.error});
    }
    let errObj = AllErrors.find(itm=>itm.id === errId);
    if(errObj) {
        return ErrorMsg({message:errObj.message,type:errObj.type});
    } else {
        return {message:"ErrorMsgFromDict.    "+errId+"  ",type:MessageType.error};
    }
}

export interface DxButtonProps {
    icon?:string,
    message?:string,
    disabled?:boolean,
    onClick?(e:any):void
}

