import * as React from 'react';
import { connect } from 'react-redux';
import * as dbwrap from '../../DbWrapper';
import DataGrid, {
    Column,
    ColumnChooser,
    FilterRow,
    HeaderFilter,
    Lookup,
    SearchPanel,
    Sorting,
    Pager,
    Paging
} from 'devextreme-react/data-grid';
import Button from 'devextreme-react/button';
import { MessagePopup } from '../../components/CommonComponents/MessagePopup';
import { ConfirmForm } from '../../components/CommonComponents/ConfirmForm';
import SelectBox from 'devextreme-react/select-box';
import TextBox from 'devextreme-react/text-box';
import { CheckBox, NumberBox, TextArea, Toast } from 'devextreme-react';

import { Model } from './Model';
import getLabelText from './Labels';
import Item from './Item';
import Field from './Field';
import * as States from '../../Models/States';
import { IssuesCause } from '../../Models/IssuesCause';
import StateDeviceImage from './StateDeviceImage'
import { IncorrectData } from './IncorrectData';


interface Props { }

type State = {
    data: Model[],
    currentItem: { model: Model, rowKey: number },
    equipmentStatusList: States.State[],
    isNeedToSave: boolean,
    confirmSaveFormVisible: boolean
    popupTitle: string,
    popupMessage: string,
    popupVisible: boolean,
    isVisibleEditArea: boolean,
    popupVisibleForCriticalError: boolean,                        //    PopUp .    
    hasCriticalError: boolean,                                    //         
    nextRow: number,                                              //   

    causesIsCorrect: IncorrectData,                               //   .         
    reactionParameterIsCorrect: IncorrectData,                    //   .         

    refComponents: any,                                            //    
    isVisibleToastForEdit: boolean,                                //    ,      
    prevCurrentItem?: { model: Model, rowKey: number }             //        Grid,            
}

const allowedPageSizes = [5, 10, 15, 20];

export class IssueCausesDictClass extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);

        this.state = {
            data: [],
            currentItem: {
                model: { id: 0 },
                rowKey: 0
            },
            equipmentStatusList: [],
            isNeedToSave: false,
            confirmSaveFormVisible: false,
            popupTitle: "",
            popupMessage: "",
            popupVisible: false,
            isVisibleEditArea: false,
            popupVisibleForCriticalError: false,
            hasCriticalError: false,
            nextRow: 0,

            causesIsCorrect: { isValid: true },
            reactionParameterIsCorrect: { isValid: true },

            isVisibleToastForEdit: false,

            refComponents: new Map()
        };

        this.handleButtonSaveClick = this.handleButtonSaveClick.bind(this);
        this.handleButtonRefreshClick = this.handleButtonRefreshClick.bind(this);
        this.handleButtonRevertClick = this.handleButtonRevertClick.bind(this);

        this.onChangeCreateIncidentForEvent = this.onChangeCreateIncidentForEvent.bind(this);
        this.onChangeSendEmailToUser = this.onChangeSendEmailToUser.bind(this);
        this.onChangeConsiderDeviceAvailabilityMode = this.onChangeConsiderDeviceAvailabilityMode.bind(this);

        this.onChangeIncidentCreationWaitTime = this.onChangeIncidentCreationWaitTime.bind(this);

        this.onChangeDescription = this.onChangeDescription.bind(this);
        this.closeConfirmFormClick = this.closeConfirmFormClick.bind(this);
        this.confirmFormClick = this.confirmFormClick.bind(this);
        this.applyChange = this.applyChange.bind(this);
        this.changeCurrentRow = this.changeCurrentRow.bind(this);
        this.onChangeEventName = this.onChangeEventName.bind(this);

        this.hidePopup = this.hidePopup.bind(this);
        this.hidePopupForCriticalError = this.hidePopupForCriticalError.bind(this);

        this.onFocusedRowChanged = this.onFocusedRowChanged.bind(this);
        this.onContentReady = this.onContentReady.bind(this);
        this.onFocusedRowChanging = this.onFocusedRowChanging.bind(this);

        this.onToastHiding = this.onToastHiding.bind(this)
        this.onRowClick = this.onRowClick.bind(this);
    }

    setStatesName() {
        var newDataSet: Model[] = []

        for (var i = 0; i < this.state.data.length; i++) {
            var item = this.state.data[i]

            var stateDevice = this.state.equipmentStatusList.find(x => x.id === item.stateDeviceId)

            item.stateDeviceCode = stateDevice == null ? '' : stateDevice.name

            newDataSet.push(item)
        }

        this.setState({ data: newDataSet })
    }

    componentDidMount() {
        (async () => {
            await dbwrap.GetRows("ViewIssuesCauses", null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.setState({ data: data.data })
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisibleForCriticalError: true
                    });

                    return
                }

                (async () => {
                    await dbwrap.GetRows("States", null).then((data: dbwrap.responseResult) => {
                        if (data.success) {
                            this.setState({ equipmentStatusList: data.data })

                            this.setStatesName();                                   //        

                            if (this.state.nextRow != 0) {
                                this.setToNextRow()
                            }
                        }
                        else {
                            let errorMessage: string = ''

                            if (data.data.parsedErrObject == null) {
                                errorMessage = data.data.request.responseText
                            }
                            else {
                                errorMessage = data.data.parsedErrObject.message
                            }

                            this.setState({
                                popupTitle: getLabelText('PopupTitle_Error'),
                                popupMessage: errorMessage,
                                popupVisibleForCriticalError: true
                            });
                        }

                    });
                })();
            });
        })();
    }
    setCurrentRow(dataRow: Model) {
        if (dataRow == null) { return }

        this.setState({
            currentItem: {
                model: { ...dataRow },
                rowKey: dataRow.id
            },
        })
        this.setState({ isNeedToSave: false })

        this.resetDataForCorrect()          //    
    }
    changeCurrentRow(e: any) {
        if (e == null || e.row == null) {             //    ,    Row
            return
        }

        this.setCurrentRow(e.row && e.row.data);
    }
    onFocusedRowChanged(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }

        this.changeCurrentRow(e)
    }
    onFocusedRowChanging(e: any) {
        if (this.state.isNeedToSave) {
            e.cancel = true;

            return
        }
    }
    onContentReady(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }

        var item = this.state.currentItem.model
        var key = item.id
        var keyExistsInFilteredRows = -1
        var filteredRows: any

        this.onFocusedRowChanged(item)                                                     //       , ,     
        if (this.state.isNeedToSave) { return }                                            //  isNeedToSave     

        this.setState({ currentItem: { model: { ...item }, rowKey: key } })                //      

        let dgdData = this.state.refComponents.dgdData

        if (item != null && dgdData != null) {
            dgdData.instance.getDataSource().store()                                    //          .
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    filteredRows = result;                                                 //     filteredRows
                });

            keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)          //        
        }

        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {            //            Id,    
            if (dgdData != null) {
                key = dgdData.instance.getKeyByRowIndex(0)                              // Key    (  -    )
            }
        }

        if (key != null && dgdData != null) {
            dgdData.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    filteredRows = result;
                });
            item = filteredRows.find((x: any) => x.id === key)
        }

        this.setState({ currentItem: { model: { ...item }, rowKey: key } })

        //,   ,    
        if (dgdData != null) {
            dgdData.instance.getDataSource().store()                                    //          .
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    filteredRows = result;                                                 //     filteredRows
                });

            if (filteredRows.length === 0) {
                this.setState({ isVisibleEditArea: false })
            }
            else {
                this.setState({ isVisibleEditArea: true })
            }
        }
    }
    onRowClick(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        };
    }

    applyChange(target: any) {
        let item = this.state.data.find(x => x.id === target.id)
        let currentItem: string = ""

        if (item != null) {
            currentItem = JSON.stringify(item)
        }

        let newItem = JSON.stringify(target)

        let hasChanged = currentItem != newItem

        this.setState({ currentItem: { model: { ...target }, rowKey: this.state.currentItem.rowKey } })             //  

        this.setState({ isNeedToSave: hasChanged })                                                                 //  " "
    }
    onChangeCreateIncidentForEvent = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.isCreate = e.value;

        this.applyChange(changedItem);
    }
    onChangeSendEmailToUser = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.isSendEmail = e.value;

        this.applyChange(changedItem);
    }
    onChangeConsiderDeviceAvailabilityMode = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.isTerminalAvailabilty = e.value;

        this.applyChange(changedItem);
    }
    onChangeIncidentCreationWaitTime = (e: any) => {
        let changedItem = { ...this.state.currentItem.model };

        changedItem.reactionParameter = e.value;

        this.applyChange(changedItem);

        this.checkReactionParameterForCorrect(changedItem)
    }
    onChangeDescription = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.description = e.value;

        this.applyChange(changedItem);
    }
    onChangeStateDeviceId = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.stateDeviceId = e.value;

        this.applyChange(changedItem);
    }
    onChangeEventName = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.causes = e.value;

        this.applyChange(changedItem);

        this.checkCausesForCorrect(changedItem);
    }
    //  
    handleButtonSaveClick() {
        if (this.state.currentItem.model.id == 0) {
            this.setState({ isNeedToSave: false })
            return
        }

        if (this.state.isNeedToSave) {
            //    .
            this.setState({
                confirmSaveFormVisible: true
            });
        };
    }

    //   
    resetDataForCorrect() {
        this.setState({ causesIsCorrect: { isValid: true, message: '' }, reactionParameterIsCorrect: { isValid: true, message: '' } })
    }
    //    ,    
    checkCausesForCorrect(model: Model): boolean {
        let causes = model.causes == null ? '' : model.causes.trim()

        let causesIsValid = causes != null && causes != '' && causes != undefined
        let causesMessage = causesIsValid ? '' : getLabelText('Causes_Incorrect')

        this.setState({ causesIsCorrect: { isValid: causesIsValid, message: causesMessage } })

        return causesIsValid
    }
    //    ,    
    checkReactionParameterForCorrect(model: Model): boolean {
        let reactionParameter = model.reactionParameter == null ? 0 : model.reactionParameter
        let reactionParameterMin = model.reactionParameterMin == null ? 0 : model.reactionParameterMin
        let reactionParameterMax = model.reactionParameterMax == null ? 0 : model.reactionParameterMax

        let reactionParameterIsValid: boolean = true
        let reactionParameterMessage: string = ''

        if (!(reactionParameter >= reactionParameterMin && reactionParameter <= reactionParameterMax)) {
            reactionParameterIsValid = false
            reactionParameterMessage = getLabelText('ReactionParameter_Incorrect') + ' (' + getLabelText('Message_MinValue') + ': ' + reactionParameterMin + ', ' + getLabelText('Message_MaxValue') + ': ' + reactionParameterMax + ')'
        }

        this.setState({ reactionParameterIsCorrect: { isValid: reactionParameterIsValid, message: reactionParameterMessage } })

        return reactionParameterIsValid
    }
    //    ,    
    checkDataForCorrect(model: Model): boolean {
        let causesIsValid = this.checkCausesForCorrect(model)
        let reactionParameterIsValid = this.checkReactionParameterForCorrect(model)

        return causesIsValid && reactionParameterIsValid    // 
    }
    //     .
    confirmFormClick() {
        var saveItem = this.state.currentItem.model

        //       
        if (this.checkDataForCorrect(saveItem) === false) {
            this.setState({ confirmSaveFormVisible: false });

            return
        }

        var issuesCause: IssuesCause[] = [];

        issuesCause.push({
            id: saveItem.id,
            causes: saveItem.causes == null ? '' : saveItem.causes.trim(),
            description: saveItem.description == null ? '' : saveItem.description.trim(),
            groupCauseId: saveItem.groupCauseId == null ? 0 : saveItem.groupCauseId,
            isCreate: saveItem.isCreate == null ? false : saveItem.isCreate,
            isHeightening: saveItem.isHeightening == null ? false : saveItem.isHeightening,
            isPercent: saveItem.isPercent == null ? false : saveItem.isPercent,
            isSendEmail: saveItem.isSendEmail == null ? false : saveItem.isSendEmail,
            isTerminalAvailabilty: saveItem.isTerminalAvailabilty == null ? false : saveItem.isTerminalAvailabilty,
            reactionParameter: saveItem.reactionParameter == null ? 0 : saveItem.reactionParameter,
            stateDeviceId: saveItem.stateDeviceId == null ? 0 : saveItem.stateDeviceId,

            deviceIdleStatesId: saveItem.deviceIdleStatesId == null ? 0 : saveItem.deviceIdleStatesId,
            enable: saveItem.enable == null ? 0 : saveItem.enable
        })

        dbwrap.UpdateRow("IssuesCauses", issuesCause, null).then((data: dbwrap.responseResult) => {
            if (data.success) {
                this.handleButtonRefreshClick();
            }
            else {
                let errorMessage: string = ''

                if (data.data.parsedErrObject == null) {
                    errorMessage = data.data.request.responseText
                }
                else {
                    errorMessage = data.data.parsedErrObject.message
                }

                this.setState({
                    popupTitle: getLabelText('PopupTitle_Error'),
                    popupMessage: errorMessage,
                    popupVisible: true
                });
            }
        })

        this.closeConfirmFormClick('saved');
    }
    //    .
    closeConfirmFormClick(action: string = 'cancel') {
        this.setState({
            confirmSaveFormVisible: false,
            isNeedToSave: false
        });

        if (this.state.currentItem != null && action == 'cancel') {
            let item = this.state.data.find(x => x.id === this.state.currentItem.model.id)

            if (item != null) {
                this.setState({ currentItem: { model: { ...item }, rowKey: this.state.currentItem.rowKey } })
                this.setState({ isNeedToSave: false });
            }
        }

        this.resetDataForCorrect()          //    

        this.handleButtonRefreshClick()
    }
    setToNextRow() {
        let item = this.state.data.find(x => x.id === this.state.nextRow)

        if (item != null) {
            this.setState({ currentItem: { model: { ...item }, rowKey: this.state.nextRow } })
        }

        this.setState({ nextRow: 0 })           //   ,    
    }
    setToPrevRow() {
        this.setState({ isVisibleToastForEdit: true })

        let dgdData = this.state.refComponents.dgdData

        if (dgdData != null) {
            if (this.state.prevCurrentItem != null) {
                dgdData.instance.navigateToRow(this.state.prevCurrentItem.model.id)
            }
            if (this.state.currentItem != null && this.state.currentItem.model.id != 0) {
                dgdData.instance.navigateToRow(this.state.currentItem.model.id)
            }
        }
    }
    onToastHiding() {
        this.setState({ isVisibleToastForEdit: false })
    }

    //  grid.
    handleButtonRefreshClick() {
        this.componentDidMount()
    }
    //   
    handleButtonRevertClick() {
        this.closeConfirmFormClick()
    }
    //  popup.
    hidePopup() {
        this.setState({ popupVisible: false });
    }
    //  popup.    
    hidePopupForCriticalError() {
        this.setState({ popupVisibleForCriticalError: false });
        this.setState({ hasCriticalError: true });                      //True -       
    }

    render() {
        return (
            <div>
                <h3 style={{ width: "auto", textAlign: "center" }}>
                    {getLabelText('PageName')}
                </h3>

                <div hidden={this.state.hasCriticalError === false}>
                    <table width='100%' style={{ height: '80vh' }}>
                        <tr>
                            <td align='center'>
                                <h1>
                                    {getLabelText('Message_DirectoryNotAvailable')}
                                </h1>
                            </td>
                        </tr>
                    </table>
                </div>

                <div hidden={this.state.hasCriticalError}>
                    <Button icon="save" disabled={this.state.isNeedToSave === false} onClick={this.handleButtonSaveClick} hint={getLabelText('Hint_Button_Save')} className='mb-2 mr-2' />
                    <Button icon="refresh" visible={this.state.isNeedToSave === false} onClick={this.handleButtonRefreshClick} hint={getLabelText('Hint_Button_Refresh')} className='mb-2 mr-2' />
                    <Button icon="revert" visible={this.state.isNeedToSave} onClick={this.handleButtonRevertClick} hint={getLabelText('Hint_Button_Cancel')} className='mb-2 mr-2' />

                    <div hidden={this.state.isVisibleEditArea === false}>
                        <table width={"100%"}>
                            <tr>
                                <td width={"16%"} /><td width={"22%"} /><td width={"1%"} /><td width={"30%"} /><td width={"1%"} /><td width={"30%"} />
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('EventName') + ":"}
                                </td>
                                <td>
                                    <TextBox maxLength={1000} value={this.state.currentItem.model.causes} valueChangeEvent="input" onValueChanged={this.onChangeEventName}
                                        isValid={this.state.causesIsCorrect.isValid} hint={this.state.causesIsCorrect.message}
                                    />
                                </td>
                                <td />
                                <td>
                                    {getLabelText('Description') + ":"}
                                </td>
                                <td />
                                <td>
                                    <CheckBox text={getLabelText('CreateIncidentForEvent')} value={this.state.currentItem.model.isCreate} onValueChanged={this.onChangeCreateIncidentForEvent} />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('EquipmentEvent') + ":"}
                                </td>
                                <td>
                                    <TextBox readOnly={true} value={this.state.currentItem.model.originalName} />
                                </td>
                                <td />
                                <td rowSpan={5}>
                                    <div>
                                        <TextArea maxLength={2000} width={"100%"} height={"160px"} value={this.state.currentItem.model.description} valueChangeEvent="input" onValueChanged={this.onChangeDescription} />
                                    </div>
                                </td>
                                <td />
                                <td>
                                    <CheckBox text={getLabelText('SendEmailToUser')} value={this.state.currentItem.model.isSendEmail} onValueChanged={this.onChangeSendEmailToUser} />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('GroupName') + ":"}
                                </td>
                                <td>
                                    <TextBox readOnly={true} value={this.state.currentItem.model.groupName} />
                                </td>
                                <td />
                                <td />
                                <td>
                                    <CheckBox text={getLabelText('ConsiderDeviceAvailabilityMode')} value={this.state.currentItem.model.isTerminalAvailabilty} onValueChanged={this.onChangeConsiderDeviceAvailabilityMode} />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {this.state.currentItem.model.isPercent === true ? getLabelText('IncidentCreationPercentage') + ":" : getLabelText('IncidentCreationWaitTime') + ":"}                                </td>
                                <td>
                                    <NumberBox
                                        label={'min: ' + this.state.currentItem.model.reactionParameterMin + ', max: ' + this.state.currentItem.model.reactionParameterMax}
                                        width={"100%"}
                                        showSpinButtons={true} value={this.state.currentItem.model.reactionParameter} max={this.state.currentItem.model.reactionParameterMax}
                                        valueChangeEvent="input" onValueChanged={this.onChangeIncidentCreationWaitTime}
                                        isValid={this.state.reactionParameterIsCorrect.isValid} hint={this.state.reactionParameterIsCorrect.message}
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('EquipmentStatus') + ":"}
                                </td>
                                <td>
                                    <SelectBox width={"100%"}
                                        dataSource={this.state.equipmentStatusList}
                                        valueExpr="id"
                                        displayExpr="description"
                                        placeholder={getLabelText('NeedSelect')}
                                        value={this.state.currentItem.model.stateDeviceId}
                                        fieldRender={Field}
                                        itemRender={Item}
                                        onValueChanged={this.onChangeStateDeviceId}
                                    />
                                </td>
                            </tr>
                        </table>
                    </div>

                    <ConfirmForm title={getLabelText('Message_Confirm')} message={getLabelText('Message_ConfirmSave')}
                        visible={this.state.confirmSaveFormVisible} confirmButtonText={getLabelText('Button_Save')} confirmForm={this.confirmFormClick}
                        cancelButtonText={getLabelText('Button_Cancel')} cancelForm={this.closeConfirmFormClick} />

                    <MessagePopup
                        title={this.state.popupTitle}
                        hidePopup={this.hidePopup}
                        message={this.state.popupMessage}
                        visible={this.state.popupVisible}
                    />
                    <MessagePopup
                        visible={this.state.popupVisibleForCriticalError}
                        title={this.state.popupTitle}
                        hidePopup={this.hidePopupForCriticalError}
                        message={this.state.popupMessage}
                    />

                    <br />

                    <DataGrid
                        dataSource={this.state.data}
                        keyExpr="id"
                        showBorders={true}
                        focusedRowEnabled={true}
                        allowColumnResizing={true}
                        allowColumnReordering={true}
                        onFocusedRowChanged={this.onFocusedRowChanged}
                        onRowClick={this.onRowClick}
                        focusedRowKey={this.state.currentItem.rowKey}
                        onFocusedRowChanging={this.onFocusedRowChanging}

                        autoNavigateToFocusedRow={true}
                        onContentReady={this.onContentReady}
                        ref={(ref) => { this.state.refComponents["dgdData"] = ref }}
                    >

                        <Paging defaultPageSize={10} />
                        <Pager
                            visible={true}
                            allowedPageSizes={allowedPageSizes}
                            displayMode={'full'}
                            showPageSizeSelector={true}
                            showInfo={true}
                            showNavigationButtons={true} />

                        <FilterRow visible={true} />
                        <HeaderFilter visible={true} />
                        <ColumnChooser enabled={true} mode="select" />
                        <Sorting mode="multiple" />
                        <SearchPanel visible={true} />

                        <Column dataField="causes" dataType="string" caption={getLabelText('EventName')} />
                        <Column dataField="originalName" dataType="string" caption={getLabelText('EquipmentEvent')} />
                        <Column dataField="groupName" dataType="string" caption={getLabelText('GroupName')} />
                        <Column dataField="description" dataType="string" caption={getLabelText('Description')} />

                        <Column dataField="stateDeviceId" caption={getLabelText('EquipmentStatus')} cellRender={StateDeviceImage} >
                            <Lookup dataSource={this.state.equipmentStatusList} valueExpr="id" displayExpr="description" />
                        </Column>

                        <Column dataField="reactionParameter" dataType="number" caption={getLabelText('IncidentCreationWaitTimeOrPercentage')} />
                        <Column dataField="isCreate" dataType="boolean" caption={getLabelText('CreateIncidentForEvent')} />
                        <Column dataField="isSendEmail" dataType="boolean" caption={getLabelText('SendEmailToUser')} />
                        <Column dataField="isTerminalAvailabilty" dataType="boolean" caption={getLabelText('ConsiderDeviceAvailabilityMode')} />
                    </DataGrid>

                    <Toast
                        visible={this.state.isVisibleToastForEdit}
                        message={getLabelText('Message_EditMode')}
                        type={'warning'}
                        onHiding={this.onToastHiding}
                        displayTime={3000}
                        width={400}
                        position={'center center'}
                    />
                </div >
            </div >
        );
    }
}

function mapStateToProps(storePar: any, ownProps?: any) { return {} }

function mapDispatchToProps(dispatch: any) { return { actions: {} } }

export const IssueCausesDict = connect(mapStateToProps, mapDispatchToProps)(IssueCausesDictClass);