import React, { Dispatch } from "react";
import { Table } from 'react-bootstrap';
import { BoolFormatterSimple } from '../ReusableComponents/ReactDataGrid/ColumnFormatters';
import getLocMsg, { LocMsgId } from '../localization';
import { Column, Lookup, FormItem } from 'devextreme-react/data-grid';
import { GetLookupRows } from '../DbWrapper';
import { SimpleItem } from 'devextreme-react/form';
import { store } from '../index';
import { TableStoreItemType } from '../reducers/tables';
import CustomStore from 'devextreme/data/custom_store';
import { StaticRouter } from "react-router";
import axios from 'axios';


export enum Tables {
    Cities = "Cities",
    Countries = "Countries",
    Areas = "Areas",
    Roles = "Roles"
}


export interface CityRow {
    id: number,
    name: string,
    region: string,
    date: Date,
    bool: boolean
}

enum columnDataType {
    number = "number",
    string = "string",
    date = "date",
    bool = "boolean",
}

interface ILookup {
    dataSource: Tables,
    valueExpr: string,
    displayExpr: string
}

interface IColumn {
    allowEditing?: boolean,
    dataField: string,
    dataType: columnDataType,
    caption?: string,
    captionId?: LocMsgId,
    lookup?: ILookup
}

const CountriesColumns: IColumn[] = [
    { dataField: 'id', dataType: columnDataType.number, caption: "Id", allowEditing: false },
    { dataField: 'name', dataType: columnDataType.string, captionId: LocMsgId.Column_Country },
];

const AreasColumns: IColumn[] = [
    { dataField: 'id', dataType: columnDataType.number, caption: "Id", allowEditing: false },
    { dataField: 'name', dataType: columnDataType.string, captionId: LocMsgId.Column_Area },
    { dataField: 'countryId', dataType: columnDataType.number, captionId: LocMsgId.Column_Country, lookup: { dataSource: Tables.Countries, valueExpr: "id", displayExpr: "name" }, allowEditing: true },
];

const CitiesColumns: IColumn[] = [
    { dataField: 'id', dataType: columnDataType.number, caption: "Id", allowEditing: false },
    { dataField: 'name', dataType: columnDataType.string, captionId: LocMsgId.Column_City, },
    { dataField: 'areaId', dataType: columnDataType.number, captionId: LocMsgId.Column_Area, lookup: { dataSource: Tables.Areas, valueExpr: "id", displayExpr: "name" }, allowEditing: true },
];

export function customDataSource(tableName: Tables) {
    let dispatch: any = store.dispatch;
    let regionTable = store.getState().Table.find((itm) => itm.tableName === tableName);
    let regionRows: any[] = [];
    if (regionTable) {
        regionRows = regionTable.rows;
    }
    if (regionRows && regionRows.length > 0) {
        return regionRows;
    }
    return new CustomStore({
        key: 'id',
        load: (): any => {
            return GetLookupRows(tableName,dispatch)
                .then((data: any) => {
                    return data;
                });

        }
    });
}

function getColumns(cols: IColumn[]): Column[] {
    let result: Column[] = [];
    for (let i: number = 0; i < cols.length; i++) {
        let col: IColumn = cols[i];
        let column: Column;
        let allowEditingProp: boolean = true;
        if (col.allowEditing !== undefined) {
            allowEditingProp = col.allowEditing;
        }
        let colKey = col.dataField;
        let captionValue = col.caption;
        if (col.captionId) {
            captionValue = getLocMsg(col.captionId);
        }
        //@ts-ignore
        column = (<Column key={colKey} dataField={col.dataField} dataType={col.dataType} caption={captionValue} allowEditing={allowEditingProp} >
        </Column>
        );

        if (col.lookup) {
            //@ts-ignore
            column = (
                <Column key={colKey} dataField={col.dataField} dataType={col.dataType} caption={captionValue} allowEditing={allowEditingProp} >
                    <Lookup dataSource={customDataSource(col.lookup.dataSource)} valueExpr={col.lookup.valueExpr} displayExpr={col.lookup.displayExpr} />
                </Column>
            );
        }
        result.push(column);
    }
    return result;
}

export function GetColumns(table: Tables): IColumn[] {
    let columns: IColumn[] = eval(table + "Columns");
    return columns;
}

export function GetDGColumns(table: Tables): Column[] {
    let columns: IColumn[] = GetColumns(table);
    return getColumns(columns);
}