export default function getLocMsg(msgId: LocMsgId): string {
    let currLang: LangEnum = LangEnum.ru;
    let st = store;
    if (st) {
        let appSet: AppSettingsState = st.getState().AppSettings;
        currLang = appSet.Language;
    }
    let msgObj: LocMsg = locMessages.find(itm => itm.id === msgId) as LocMsg;
    if (!msgObj) {
        throw new Error("Error: message Id:" + msgId + "is not found");
    }
    if (msgObj.hasOwnProperty(currLang)) {
        let result: string = "";
        switch (currLang) {
            case LangEnum.ru:
                result = msgObj.ru!;
                break;
            case LangEnum.kz:
                result = msgObj.kz!;
                break;
            case LangEnum.en:
                result = msgObj.en!;
                break;
            default:
                result = msgObj.ru!;
        }
        return result;
    }
    return msgObj.ru;
}