import React from 'react';
import TreeView from 'devextreme-react/tree-view';
import { withRouter } from 'react-router-dom';
import { RouteComponentProps } from 'react-router';
import { SidebarItems} from '../Routes';
import { Route } from 'devextreme-react/map';
import {GetSidebarItems} from '../Routes';
import { connect } from 'react-redux';

type PathParamsType = {
    param1:string;
}

type LinkListProps=RouteComponentProps<PathParamsType> &{
    sidebarItems:object[],
}

interface LinkListState {
    currentItem:object
}

class LinkList extends React.PureComponent<LinkListProps,LinkListState> {
    items:object[]=[];
    constructor(props:any) {
        super(props);
        this.items = SidebarItems;
        this.state = {
          currentItem: { ...this.items[0] },
        };
        this.selectItem = this.selectItem.bind(this);
      }
      
      render() {
        const currentItem  = this.state.currentItem;
        return (
          <div className="form">
            <TreeView id="simple-treeview" 
              items={(()=>GetSidebarItems())()}
              dataStructure="plain"
              displayExpr="name"
              parentIdExpr="categoryId"
              keyExpr="ID"
              width={300}
              onItemClick={(e)=>this.selectItem(e)} />
          </div>
        );
      }

       
      selectItem(e:any) {
        //this.setState({
        //  currentItem: { ...e.itemData },
        //});
        this.props.history.push(e.itemData.path);
      }
}
 
function mapStateToProps(store:any,ownProps?:any) {
  let result = {...store.AppSettings};
  return result
}
function mapDispatchToProps(dispatch:any) {
  return {
  }
}

const wr = withRouter(LinkList);

const result = connect(mapStateToProps, mapDispatchToProps)(wr);

export default result;