import * as React from "react";
import axios, { AxiosPromise } from 'axios';
import { connect } from 'react-redux';
import Drawer from 'devextreme-react/drawer';
import Toolbar, { Item } from "devextreme-react/toolbar";
import LinkList from '../components/LinkList';
import { Route } from "react-router-dom";
import {ACT_JWT_TESTRESULT_CHANGED} from '../reducers/appSettings';

//import 'devextreme/dist/css/dx.light.css';

import Button from 'devextreme-react/button';
import Icon from "../Images/AdminService";
import { getRoutes } from "../Routes";
import { SelectLang } from '../ReusableComponents/SelectLang';

//@ts-ignore
import ruMessages from 'devextreme/localization/messages/ru.json';
import { loadMessages, locale } from 'devextreme/localization';

import { LoginDialog } from '../components/Login';
import * as dbwrap from '../DbWrapper';


interface MainPageProps {
    hist?: object,
    sidebarItems?: object[],
    JWTTestResult:boolean,
    actions:{
        SetJWTTestResult(newResult?:boolean):void        
    }
}

interface MainPageState {
    opened: boolean,
    openedStateMode: any,
    revealMode: any,
    position: any
}


class Root extends React.Component<MainPageProps, MainPageState> {
    constructor(props: MainPageProps) {
        super(props);

        this.initMessages();
        locale('ru');                   //  

        this.state = {
            opened: true,
            openedStateMode: "shrink",
            revealMode: "slide",
            position: "left"
        };
        this.onOutsideClick = this.onOutsideClick.bind(this);

    }

    componentDidUpdate(){
    }

    onOutsideClick(e: any): boolean {
        this.setState({ opened: false });
        return true;
    }

    handleLogoutBtnClick(){
        this.Logout();
        this.props.actions.SetJWTTestResult(false);
    }

    Logout() {
        axios({
            url: "/api/Account/logout",
            method: "POST"
        })
            .then(response => {
                return response.data;
            })
            .then((dataObj) => {
                this.props.actions.SetJWTTestResult(false);
            })
            .catch(err => {
                console.log(err);
            }
            );
    }  
    initMessages() {                        //     
        loadMessages(ruMessages);
    }
   

    render() {
        const { opened, openedStateMode, position, revealMode } = this.state;
        return (
            <React.Fragment>
                <Toolbar >
                    <Item widget="dxButton" location="before" options={{ icon: "menu", onClick: () => this.setState({ opened: !this.state.opened }) }} />
                    <Item widget="dxButton" location="before" options={{ icon: "export", onClick: () => this.handleLogoutBtnClick()}} />
                    <Item location="before" render={() => <SelectLang />} />
                </Toolbar>
                <Drawer id="drawer"
                    opened={opened}
                    openedStateMode={openedStateMode}
                    position={position}
                    revealMode={revealMode}
                    component={LinkList}
                    closeOnOutsideClick={this.onOutsideClick}
                >
                    <div id="content" className="dx-theme-background-color">
                        {getRoutes()}
                    </div>
                </Drawer>
                <LoginDialog />
            </React.Fragment>
        );
    }


}

function mapStateToProps(storePar: any, ownProps?: any) {
    let result = { ...storePar.AppSettings };
    return result
}

function mapDispatchToProps(dispatch: any) {
    return {
        actions: {
            SetJWTTestResult(newResult?:boolean){
                dispatch({
                    type: ACT_JWT_TESTRESULT_CHANGED,
                    payload: newResult
                });

            }
        }
    }
}

export const RootPage = connect(mapStateToProps, mapDispatchToProps)(Root);