import React from 'react';
import getLocMsg, { LocMsgId } from '../localization';
import Button from 'devextreme-react/button';
import { ButtonGroup } from 'devextreme-react/button-group';

import * as dbwrap from '../DbWrapper';
import { GuiElementsId, GetGuiElementName } from '../Routes';
import { ErrorMsg, ErrorMsgProps, ErrorMsgFromDict } from '../ReusableComponents/DxStyleSimple';
import { MessageType, errorParser, ErrorEnum, getProp } from '../Errors';

import DataGrid, {
    Column,
    Item,
    Editing,
    Selection,
} from 'devextreme-react/data-grid';

import Form, {
    Item as FormItem,
    SimpleItem,
    GroupItem,
    Label,
} from 'devextreme-react/form';

import { Popup } from 'devextreme-react/popup';
import { IColProps } from 'devextreme-react/responsive-box';
import { TableEditorMode } from '../ReusableComponents/TableEditorPage';
import { FormEditor } from '../ReusableComponents/FormEditor';
import { TEControllerBase, ICurrentRow } from '../ReusableComponents/TEControllerBase';
import TreeView from 'devextreme-react/tree-view';
import { GetSidebarItems } from '../Routes';
import { TreeListHeaderFilterTexts } from 'devextreme-react/tree-list';
import { TreeViewSelector, ITVSelectorData } from "../ReusableComponents/TreeViewSelector";


export interface RoleRow {
    id?: number,
    roleName: string,
    roleDescription: string,
    superAdmimn: boolean
}

interface ApplicationMenuObjectsRow {
    id?: number,
    uid: string,
    name: string,
}


interface IAttrType {
    id: number,
    name: string
}

interface IAttribute {
    treeId: number,
    id: number,
    parentId?: number,
    name: string,
    expanded: boolean,
    selected: boolean
}

interface ICurrRowTVSelection {
    oldAttrItems: IAttribute[],
    newAttrItems: IAttribute[],
    oldGuiItems: any[],
    newGuiItems: any[]
}


interface RoleConfigState {
    width: number;
    height: number;
    teController: TEControllerBase,
    currentRowIndex: number,
    currentRow: ICurrentRow,
    roleRows: RoleRow[],
    error: ErrorEnum,
    tvGuiData: ITVSelectorData,
    tvAttrData: ITVSelectorData
}

export class RoleConfigPage extends React.Component<any, RoleConfigState> {
    tableName: string = "Roles";
    gridRef: object;
    formRef: any;
    constructor(props: any) {
        super(props);
        this.state = {
            width: window.innerWidth,
            height: window.innerHeight,
            teController: new TEControllerBase(this),
            currentRowIndex: -1,
            currentRow: { newRowValue: {}, oldRowValue: {} },
            roleRows: [],
            error: ErrorEnum.NoError,
            tvGuiData: { allItems: [], newSelectedIds: [], oldSelectedIds: [], hasChanges: false },
            tvAttrData: { allItems: [], newSelectedIds: [], oldSelectedIds: [], hasChanges: false },
        }
        this.gridRef = React.createRef();
        this.formRef = React.createRef();
        this.updateWindowSize = this.updateWindowSize.bind(this);
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateWindowSize);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowSize);
    }

    updateWindowSize(): void {
        this.setState({ width: window.innerWidth, height: window.innerHeight })
    }

    loadTvGuiData() {
        let appMenuObjectsRows: any[];
        let allGuiItems = [...GetSidebarItems()];
        return dbwrap.GetRows("ApplicationMenuObjects", null)
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    return result;
                }
                appMenuObjectsRows = result.data;
                return dbwrap.GetRows("RoleMenuObjects", null)
            })
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    return result;
                }
                let currGuiItems = result.data.filter((itm: any) => itm.roleId === this.state.currentRow.oldRowValue.id);
                allGuiItems.forEach((guiElmNode: any) => {
                    let menuItem = appMenuObjectsRows.find(m => m.uid === guiElmNode.ID);
                    if (menuItem) {
                        guiElmNode.id = menuItem.id;
                    }
                    if (guiElmNode.component) {
                        if (!menuItem) {
                            return;
                        }
                        let menuId = menuItem.id;
                        let ci = currGuiItems.find((cr: any) => cr.menuId === menuId);
                        if (ci) {
                            guiElmNode.selected = true;
                        } else {
                            guiElmNode.selected = false;
                        }
                    }
                });
                let selIds = allGuiItems.filter((r: any) => r.selected).map((r: any) => r.id) as number[];
                this.setState({ tvGuiData: { allItems: allGuiItems, newSelectedIds: [...selIds], oldSelectedIds: selIds, hasChanges: false } });
                return result;
            });
    }

    loadTvAttrData() {
        let attrTypeRows: any[] = [];
        let attributeRows: any[] = [];
        return dbwrap.GetRows("AttributeTypes", null)
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    return result;
                }
                attrTypeRows = result.data;
                return dbwrap.GetRows("Attributes", null)
            })
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    return result;
                }
                attributeRows = result.data;
                return dbwrap.GetRows("RoleAttributes", null)
            })
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    return result;
                }
                let attrs: any[] = [];
                attributeRows.forEach((itm) => {
                    attrs.push({ treeId: itm.id * -1, id: itm.id, parentId: itm.typeId, name: itm.name });
                });
                attrTypeRows.forEach((itm) => {
                    attrs.push({ treeId: itm.id, id: itm.id, parentId: undefined, name: itm.name, expanded: true });
                });
                result.data.filter((roleAttr:any)=>roleAttr.roleId === this.state.currentRow.oldRowValue.id)
                            .forEach((roleAttr:any)=>{
                                let attr = attrs.find((itm:any)=> itm.id === roleAttr.attributeId);
                                if(attr){
                                    attr.selected = true;
                                }
                });
                let selIds = attrs.filter((r: any) => r.selected).map((r: any) => r.id) as number[];
                this.setState({ tvAttrData: { allItems: attrs, newSelectedIds: [...selIds], oldSelectedIds: selIds, hasChanges: false } });
                return result;
            });
    }

    loadTvData() {
        this.loadTvGuiData()
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    this.handleError(result);
                }
                return this.loadTvAttrData();
            })
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    this.handleError(result);
                    return;
                }
                if(this.formRef && this.formRef.instance){
                    let editor = this.formRef.instance.getEditor("roleName");
                    editor.focus();    
                }        
            });
    }

    handleAddBtnClick() {
        this.state.teController.AddRow();
    }

    handleDeleteBtnClick() {
        this.state.teController.DeleteRow();
    }

    saveGuiTvSelection() {
        if (!this.state.tvGuiData.hasChanges) {
            return dbwrap.returnOkResult();
        }
        let nodesForAdding = this.state.tvGuiData.allItems.filter(itm => this.state.tvGuiData.newSelectedIds.includes(itm.id));
        let addNestedNodes = ((parentNode:any)=>{
            let childNodes = this.state.tvGuiData.allItems.filter(itm=>itm.categoryId === parentNode.ID);
            childNodes.forEach((nestNode:any)=>{
                if(!nodesForAdding.find(itm=>itm.ID === nestNode.ID)){
                    nodesForAdding.push(nestNode);
                }
                addNestedNodes(nestNode);
            });
        });
        nodesForAdding.forEach(node=>addNestedNodes(node));
        let currRoleId = this.state.currentRow.oldRowValue.id;
        return dbwrap.GetRows("RoleMenuObjects", null)
            .then((result: dbwrap.responseResult) => {
                //  RoleMenuObjects  ,    id    ( )
                if (!result.success) {
                    throw result;
                }
                let RoleMenuObjIdsForDeleting: any[] = [];
                result.data.forEach((elm: any) => {
                    if (elm.roleId === currRoleId) {
                        RoleMenuObjIdsForDeleting.push(elm.id);
                    }
                });
                //     RoleMenuObjects
                return dbwrap.DeleteRow("RoleMenuObjects", RoleMenuObjIdsForDeleting, null);
            })
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    throw result;
                }
                //       RoleMenuObjects
                let roleMenuObjRowsForAdding: any[] = [];
                nodesForAdding.forEach((node: any) => {
                    let newRow = { roleId: currRoleId, menuId: node.id };
                    roleMenuObjRowsForAdding.push(newRow);
                });
                return dbwrap.AddRowRange("RoleMenuObjects", roleMenuObjRowsForAdding, null);
            })
            .then((result: dbwrap.responseResult) => {
                return result;
            })
            .catch((result: dbwrap.responseResult) => {
                this.handleError(result);
                return result;
            });
    }

    saveAttrTvSelection() {
        if (!this.state.tvAttrData.hasChanges) {
            return dbwrap.returnOkResult();
        }
        let nodesForAdding = this.state.tvAttrData.allItems.filter(itm => this.state.tvAttrData.newSelectedIds.includes(itm.id));
        let currRoleId = this.state.currentRow.oldRowValue.id;

        return dbwrap.GetRows("RoleAttributes", null)
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    throw result;
                }
                let rowsForDeleting: any[] = [];
                result.data.forEach((elm: any) => {
                    if (elm.roleId === currRoleId) {
                        rowsForDeleting.push(elm.id);
                    }
                });
                //     RoleMenuObjects
                return dbwrap.DeleteRow("RoleAttributes", rowsForDeleting, null);
            })
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    throw result;
                }
                let rowsForAdding:any[] = [];
                nodesForAdding.forEach((node: any) => {
                    let newRow = { roleId: currRoleId, attributeId: node.id };
                    rowsForAdding.push(newRow);
                });
                return dbwrap.AddRowRange("RoleAttributes", rowsForAdding, null);
            })
            .catch((result: dbwrap.responseResult) => {
                this.handleError(result);
                return result;
            });
    }

    saveTvSelection() {
        return this.saveGuiTvSelection()
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    return result;
                }
                return this.saveAttrTvSelection();
            })
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    return result;
                }
                let newTvGuiData = { ...this.state.tvGuiData };
                newTvGuiData.oldSelectedIds = this.state.tvGuiData.newSelectedIds;
                newTvGuiData.hasChanges = false;
                let newTvAttrData = { ...this.state.tvAttrData };
                newTvAttrData.oldSelectedIds = this.state.tvAttrData.newSelectedIds;
                newTvAttrData.hasChanges = false;
                this.setState({ tvGuiData: newTvGuiData, tvAttrData: newTvAttrData });
            })
    }


    handleSaveBtnClick() {
        this.state.teController.SaveChanges()
            .then((result: dbwrap.responseResult) => {
                if (!result.success) {
                    throw result;
                }
                return this.saveTvSelection()
            })
    }


    handleCancelBtnClick() {
        this.state.teController.CancelChanges();
        this.loadTvData();
    }

    handleFieldChange(e: any, dataField: string) {
        this.state.teController.HandleFieldChange(e, dataField)
    }


    handleError(result: dbwrap.responseResult) {
        let parsedErr = errorParser(result.data);
        if (parsedErr.hasOwnProperty("parsedErrId")) {
            let errId: ErrorEnum = getProp(parsedErr, "parsedErrId");
            this.onError(errId);
            return;
        }
    }

    hasChanges() {
        let rv = this.state.teController.HasChanges() || this.state.tvGuiData.hasChanges || this.state.tvAttrData.hasChanges;
        return rv;
    }

    onRowListChanged(rows: []) {
        let currInd = this.state.currentRowIndex;
        if (currInd === -1 && rows.length > 0) {
            currInd = 0;
            this.setState({ roleRows: rows as RoleRow[], currentRowIndex: currInd });
            return;
        }
        let newRows = rows as RoleRow[];
        this.setState({ roleRows: newRows });
        //@ts-ignore
        this.getGrid().refresh();
    }

    onError(errorId: ErrorEnum) {
        this.setState({ error: errorId })
    }

    renderDataGrid() {
        if (this.state.currentRowIndex === -1) {
            return (<div />);
        }
        return (
            <DataGrid
                ref={(gref) => {
                    if (gref) {
                        this.gridRef = gref;
                    }
                }}
                style={{ height: this.getHeight("grid") }}
                dataSource={this.state.roleRows}
                focusedRowEnabled={true}
                focusedRowIndex={this.state.currentRowIndex}
                keyExpr="id"
                onContentReady={() => this.loadTvData()}
                onFocusedRowChanging={(e) => {
                    let newRow = this.state.roleRows[e.newRowIndex];
                    if (this.hasChanges()) {
                        this.onError(ErrorEnum.NotSavedRowChanges);
                        return;
                    }
                    let bresult = this.state.teController.HandleSelectNewCurrentRow(newRow);
                    if (bresult) {
                        this.setState({ currentRowIndex: e.newRowIndex }, () => this.loadTvData());
                    }
                }}
            >
                <Selection mode="single" />
                <Column dataField="id" caption="id"></Column>
                <Column dataField="roleName" caption={getLocMsg(LocMsgId.Column_Name)} ></Column>
                <Column dataField="roleDescription" caption={getLocMsg(LocMsgId.Column_Description)}></Column>
                <Column dataField="superAdmin" caption={getLocMsg(LocMsgId.Column_SuperAdmin)}></Column>
            </DataGrid>
        );
    }

    renderFormFld(dataField: string, caption: string = "", disabled: boolean = false, width: number = 0, editorType: string = "dxTextBox") {
        if (!caption) {
            caption = dataField;
        }
        if (disabled) {
            return (
                <SimpleItem dataField={dataField} editorType={editorType} editorOptions={{ disabled: true, width: width }}>
                    <Label text={caption} />
                </SimpleItem>
            );
        } else {
            let edOpt = { onInput: (e: any) => this.handleFieldChange(e, dataField), };
            if (width > 0) {
                //@ts-ignore
                edOpt.width = width;
            }
            return (
                <SimpleItem dataField={dataField} editorType={editorType}
                    editorOptions={edOpt} >
                    <Label text={caption} />
                </SimpleItem>
            );
        }
    }

    renderForm() {
        return (
            <Form id="FormEditor" formData={this.state.currentRow.newRowValue} minColWidth={100}
                ref={(frmRef:any)=>{ this.formRef = frmRef}}
                style={{ marginTop: "20px", marginBottom: "20px" }} >
                <GroupItem colCount={"auto"}>
                    {this.renderFormFld("id", "", true, 100)}
                    {this.renderFormFld("roleName", getLocMsg(LocMsgId.Column_Name), false, 300)}
                </GroupItem>
                <GroupItem colCount={"auto"}>
                    {this.renderFormFld("roleDescription", getLocMsg(LocMsgId.Column_Description), false, 380)}
                    {this.renderFormFld("superAdmin", getLocMsg(LocMsgId.Column_SuperAdmin), false, 100, "dxCheckBox")}
                </GroupItem>
            </Form>
        );
    }

    renderGuiElmTree() {
        return (
            <TreeViewSelector
                parent={this}
                dataPropName="tvGuiData"
                displayExpr="name"
                parentIdExpr="categoryId"
                keyExpr="ID"
            />
        );
    }

    renderAttrTree() {
        return (
            <TreeViewSelector
                parent={this}
                dataPropName="tvAttrData"
                displayExpr="name"
                parentIdExpr="parentId"
                keyExpr="treeId"
            />
        );
    }


    render() {
        let rowSelected = (this.state.currentRowIndex != -1);
        let btnAddStyle = this.state.teController.editorMode === TableEditorMode.AddRow ? { borderColor: "lightGreen", borderWidth: "4px" } : {};
        let btnDeleteStyle = rowSelected && this.state.teController.editorMode !== TableEditorMode.AddRow ? { visibility: "visible" } : { visibility: "hidden" };
        let disableSaveBtn = !this.hasChanges();
        let disableCancelBtn = !this.hasChanges() && this.state.teController.editorMode !== TableEditorMode.AddRow;
        return (
            <div id="rolePage">
                <h3 style={{ display: "inline-block", textAlign: "left" }}>{getLocMsg(LocMsgId.Menu_AccessManagement_Role)}</h3>
                <ButtonGroup style={{ marginLeft: "20px" }}>
                    <Item key="btnAdd" location="before" disabled={this.state.teController.editorMode === TableEditorMode.AddRow} >
                        <Button style={btnAddStyle} icon="add" onClick={() => this.handleAddBtnClick()} />
                    </Item>
                    <Item key="btnDelete" location="before">
                        <Button style={btnDeleteStyle} icon="remove" onClick={() => this.handleDeleteBtnClick()} />
                    </Item>
                </ButtonGroup>
                {this.renderForm()}
                <div id="tViewsDiv" style={{ height: this.getHeight("tview"), display: "flex", flexDirection: "row" }} >
                    <div id="tvGuiDiv" className="dx-theme-border-color" style={{ width: "50%", height: "100%", border: "solid 1px" }}>
                        <div style={{ fontWeight: "bold", marginLeft: "10px", height: "20px" }}>GUI </div>
                        <div style={{ height: this.getHeight("tview") - 20 - 5 }}>
                            {this.renderGuiElmTree()}
                        </div>
                    </div>
                    <div id="tvAttrDiv" className="dx-theme-border-color" style={{ width: "48%", height: "100%", border: "solid 1px", marginLeft: "10px" }}>
                        <div style={{ fontWeight: "bold", marginLeft: "10px", height: "20px" }}></div>
                        <div style={{ height: this.getHeight("tview") - 20 - 5 }}>
                            {this.renderAttrTree()}
                        </div>
                    </div>
                </div>
                <div style={{ display: "flex", justifyContent: "flex-end", marginBottom: "20px", marginTop: "10px" }}>
                    <Button icon="save" text={getLocMsg(LocMsgId.Button_Save)} disabled={disableSaveBtn} onClick={() => this.handleSaveBtnClick()} style={{ marginLeft: "10px" }} />
                    <Button icon="clear" text={getLocMsg(LocMsgId.Button_Cancel)} disabled={disableCancelBtn} onClick={() => this.handleCancelBtnClick()} style={{ marginLeft: "10px" }} />
                </div>
                {ErrorMsgFromDict(this.state.error)}
                {this.renderDataGrid()}
            </div>
        );
    }

    getCurrentRow(): ICurrentRow {
        return this.state.currentRow;
    }

    setCurrentRow(currRow: ICurrentRow) {
        this.setState({ currentRow: currRow });
    }


    getCurrentRowIndex(): number {
        return this.state.currentRowIndex;
    }

    setCurrentRowIndex(idx: number) {
        this.setState({ currentRowIndex: idx });
    }
    getGrid(): DataGrid<any, any> {
        //@ts-ignore
        return this.gridRef.instance;
    }

    getHeight(elm: string) {
        let heightBeforeGrid = 280;
        let heightForTviewsAndGrid = this.state.height - heightBeforeGrid - 50;
        let tViewsKoef = 0.4;//        Treeview ("GUI "  "")
        let gridKoef = 1 - tViewsKoef;  //        
        let tViewsHeight = heightForTviewsAndGrid * tViewsKoef;
        let gridHeight = heightForTviewsAndGrid * gridKoef;
        if (elm === "tview") {
            return tViewsHeight;
        }
        if (elm === "grid") {
            return gridHeight;
        }
        throw "wrong elm " + elm;
    }

}
