import * as React from 'react';
import { connect } from 'react-redux';
import * as dbwrap from '../../DbWrapper';
import DataGrid, {
    Column,
    ColumnChooser,
    FilterRow,
    HeaderFilter,
    SearchPanel,
    Sorting,
    Pager,
    Paging
} from 'devextreme-react/data-grid';
import Button from 'devextreme-react/button';
import { MessagePopup } from '../../components/CommonComponents/MessagePopup';
import TextBox from 'devextreme-react/text-box';
import { CheckBox, TextArea, Toast } from 'devextreme-react';
import { Popup, ToolbarItem } from 'devextreme-react/popup';

import { Model } from './Model';
import getLabelText from './Labels';
import { MailingListUser } from '../../Models/MailingListUser';
import { IncorrectData } from './IncorrectData';

interface Props { }

type State = {
    data: Model[],
    currentItem: { model: Model, rowKey: number },                          //       Grid,   ,      
    isNeedToSave: boolean,                                                  //,      .   True,        .
    isNew: boolean,                                                         //   
    prevCurrentItem?: { model: Model, rowKey: number },                     //        Grid,            

    //        
    confirmFormVisible: boolean
    confirmFormTitle: string,
    confirmFormMessage: string,
    confirmFormOkButtonText: string,
    confirmFormOkButton?: any,
    confirmFormCancelButtonText: string,
    confirmFormCancelButton?: any,

    popupTitle: string,                                           //   PopUp ,  
    popupMessage: string,                                         //   PopUp ,   
    popupVisible: boolean,                                        //    PopUp 
    popupVisibleForCriticalError: boolean,                        //    PopUp .    
    hasCriticalError: boolean,                                    //         
    nextRow: number,                                              //   

    dataIsCorrect: { userName: IncorrectData, eMails: IncorrectData },                       //   .         

    isVisibleEditArea: boolean,

    refComponents: any,                                                                     //    
    isVisibleToastForEdit: boolean                                                          //    ,      
}

const allowedPageSizes = [5, 10, 15, 20];

export class MailingListUsersDictClass extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);

        this.state = {
            data: [],
            currentItem: {
                model: { id: 0, userName: '', isGroupEmail: false },
                rowKey: 0
            },
            isNeedToSave: false,
            isNew: false,

            confirmFormVisible: false,
            confirmFormTitle: '',
            confirmFormMessage: '',
            confirmFormOkButtonText: '',
            confirmFormCancelButtonText: '',

            popupTitle: "",
            popupMessage: "",
            popupVisible: false,
            popupVisibleForCriticalError: false,
            hasCriticalError: false,
            nextRow: 0,

            dataIsCorrect: {
                userName: { isValid: true },
                eMails: { isValid: true }
            },

            isVisibleEditArea: false,
            isVisibleToastForEdit: false,

            refComponents: new Map()
        };

        this.handleButtonSaveClick = this.handleButtonSaveClick.bind(this);
        this.handleButtonRefreshClick = this.handleButtonRefreshClick.bind(this);
        this.handleButtonRevertClick = this.handleButtonRevertClick.bind(this);

        this.onChangeIsGroupEmail = this.onChangeIsGroupEmail.bind(this);
        this.onChangeDescription = this.onChangeDescription.bind(this);

        this.closeConfirmSaveFormClick = this.closeConfirmSaveFormClick.bind(this);
        this.confirmSaveFormClick = this.confirmSaveFormClick.bind(this);

        this.closeConfirmDeleteFormClick = this.closeConfirmDeleteFormClick.bind(this);
        this.confirmDeleteFormClick = this.confirmDeleteFormClick.bind(this);

        this.applyChange = this.applyChange.bind(this);
        this.changeCurrentRow = this.changeCurrentRow.bind(this);
        this.onChangeUserName = this.onChangeUserName.bind(this);
        this.onChangeEmailList = this.onChangeEmailList.bind(this);

        this.hidePopup = this.hidePopup.bind(this);
        this.hidePopupForCriticalError = this.hidePopupForCriticalError.bind(this);

        this.handleButtonNewClick = this.handleButtonNewClick.bind(this);
        this.handleButtonDeleteClick = this.handleButtonDeleteClick.bind(this);
        this.onRowClick = this.onRowClick.bind(this);

        this.onFocusedRowChanged = this.onFocusedRowChanged.bind(this);
        this.onContentReady = this.onContentReady.bind(this);
        this.onFocusedRowChanging = this.onFocusedRowChanging.bind(this);

        this.onToastHiding = this.onToastHiding.bind(this)
    }

    //      
    confirmSaveFormVisible() {
        this.setState({
            confirmFormVisible: true,
            confirmFormTitle: getLabelText('Message_Confirm'),
            confirmFormMessage: getLabelText('Message_ConfirmSave'),
            confirmFormOkButtonText: getLabelText('Button_Save'),
            confirmFormOkButton: this.confirmSaveFormClick,
            confirmFormCancelButtonText: getLabelText('Button_Cancel'),
            confirmFormCancelButton: this.closeConfirmSaveFormClick
        })
    }
    //      
    confirmDeleteFormVisible() {
        this.setState({
            confirmFormVisible: true,
            confirmFormTitle: getLabelText('Message_Confirm'),
            confirmFormMessage: getLabelText('Message_ConfirmDelete') + "\n\r" + getLabelText('UserName') + ': ' + this.state.currentItem.model.userName,
            confirmFormOkButtonText: getLabelText('Button_Yes'),
            confirmFormOkButton: this.confirmDeleteFormClick,
            confirmFormCancelButtonText: getLabelText('Button_No'),
            confirmFormCancelButton: this.closeConfirmDeleteFormClick
        })
    }

    componentDidMount() {
        (async () => {
            await dbwrap.GetRows("MailingListUsers", null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.setState({ data: data.data })

                    if (this.state.nextRow != 0) {
                        this.setToNextRow()
                    }
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisibleForCriticalError: true
                    });
                }
            });
        })();
    }
    changeCurrentRow(e: Model) {
        this.setState({ isNew: false })             //  ,     False  isNew

        if (e == null) { return }

        const dataRow = e;

        this.setState({
            currentItem: {
                model: { ...dataRow },
                rowKey: dataRow.id
            },
        })
        this.setState({ isNeedToSave: false })

        this.resetDataForCorrect()          //    
    }
    onFocusedRowChanged(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }

        this.changeCurrentRow(e.row && e.row.data)
    }
    onFocusedRowChanging(e: any) {
        if (this.state.isNeedToSave) {
            e.cancel = true;

            return
        }
    }
    onContentReady(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }

        var item = this.state.currentItem.model
        var key = item.id
        var keyExistsInFilteredRows = -1
        var filteredRows: any

        this.onFocusedRowChanged(item)                                                     //       , ,     
        if (this.state.isNeedToSave) { return }                                            //  isNeedToSave     

        this.setState({ currentItem: { model: { ...item }, rowKey: key } })                //      

        let dgdData = this.state.refComponents.dgdData

        if (item != null && dgdData != null) {
            dgdData.instance.getDataSource().store()                                       //          .
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    filteredRows = result;                                                 //     filteredRows
                });

            keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)          //        
        }

        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {            //            Id,    
            if (dgdData != null) {
                key = dgdData.instance.getKeyByRowIndex(0)                                 // Key    (  -    )
            }
        }

        if (key != null && dgdData != null) {
            dgdData.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    filteredRows = result;
                });
            item = filteredRows.find((x: any) => x.id === key)
        }

        this.setState({ currentItem: { model: { ...item }, rowKey: key } })

        //,   ,    
        if (dgdData != null) {
            dgdData.instance.getDataSource().store()                                    //          .
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    filteredRows = result;                                                 //     filteredRows
                });

            if (filteredRows.length === 0) {
                this.setState({ isVisibleEditArea: false })
            }
            else {
                this.setState({ isVisibleEditArea: true })
            }
        }
    }

    onRowClick(e: any) {
        if (this.state.currentItem.model.id === e.data.id) { return }

        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        };

        if (this.state.currentItem.model.id === 0 || this.state.currentItem.model.id == null) {
            this.changeCurrentRow(e.data)
        }
    }

    applyChange(target: any) {
        let item = this.state.data.find(x => x.id === target.id)
        let currentItem: string = ""

        if (item != null) {
            currentItem = JSON.stringify(item)
        }

        let newItem = JSON.stringify(target)

        let hasChanged = currentItem != newItem

        this.setState({ currentItem: { model: target, rowKey: this.state.currentItem.rowKey } })       //  

        this.setState({ isNeedToSave: hasChanged })                                                    //  " "
    }
    onChangeIsGroupEmail = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.isGroupEmail = e.value;

        this.applyChange(changedItem);
    }
    onChangeDescription = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.description = e.value;

        this.applyChange(changedItem);
    }
    onChangeUserName = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.userName = e.value;

        this.applyChange(changedItem);
    }
    onChangeEmailList = (e: any) => {
        var changedItem = { ...this.state.currentItem.model };

        changedItem.emailList = e.value;

        this.applyChange(changedItem);
    }

    //  
    handleButtonNewClick() {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        };

        this.setState({ prevCurrentItem: { ...this.state.currentItem } })              //   

        var newItem: Model = { id: 0, userName: '', isGroupEmail: false }

        this.setState({ currentItem: { model: newItem, rowKey: newItem.id } })
        this.setState({ isNew: true })

        this.resetDataForCorrect()

        let first = this.state.refComponents.txtUserName

        if (first != null) {
            first.instance.focus()        //     
        }
    }
    // 
    handleButtonDeleteClick() {
        if (this.state.data.find(x => x.id === this.state.currentItem.model.id) == null) { return }

        //    .
        this.confirmDeleteFormVisible()
    }
    //  
    handleButtonSaveClick() {
        if (this.state.currentItem.model.id === 0 && this.state.isNew === false) {
            this.setState({ isNeedToSave: false })
            return
        }

        if (this.state.isNeedToSave) {
            //    .
            this.confirmSaveFormVisible()
        };
    }
    //   
    resetDataForCorrect() {
        this.setState({ dataIsCorrect: { userName: { isValid: true, message: '' }, eMails: { isValid: true, message: '' } } })
    }
    //     E-Mails    
    checkEMailsForCorrect(value?: string): boolean {
        let regexp = new RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/)

        let checkValue = value != null && value != undefined ? value : ''

        let splitted = checkValue.split(",");

        let result = true

        splitted.forEach(function (val) {
            result = result && regexp.test(val.trim())
        });

        return result
    }
    //        
    checkDataForCorrect(model: Model): boolean {
        let userName = model.userName == null ? '' : model.userName.trim()

        var userNameIsValid = userName != null && userName != '' && userName != undefined
        var userNameMessage = userNameIsValid ? '' : getLabelText('UserName_Incorrect')

        if (userNameIsValid) {
            userNameIsValid = this.state.data.find(x => x.userName.toUpperCase() === userName.toUpperCase() && x.id != model.id) === undefined
            userNameMessage = userNameIsValid ? '' : getLabelText('UserName_IsDublicate')
        }

        var eMailsIsValid = this.checkEMailsForCorrect(model.emailList)
        var eMailsMessage = eMailsIsValid ? '' : getLabelText('EMails_Incorrect')

        this.setState({ dataIsCorrect: { userName: { isValid: userNameIsValid, message: userNameMessage }, eMails: { isValid: eMailsIsValid, message: eMailsMessage } } })

        if (userNameIsValid === false) {
            let comp = this.state.refComponents.txtUserName

            if (comp != null) {
                comp.instance.focus()
            }
        }
        else if (eMailsIsValid === false) {
            let comp = this.state.refComponents.txtEmailList

            if (comp != null) {
                comp.instance.focus()
            }
        }

        return userNameIsValid && eMailsIsValid     // 
    }
    //     .
    confirmSaveFormClick() {
        var item = this.state.currentItem.model

        //       
        if (this.checkDataForCorrect(item) === false) {
            this.setState({ confirmFormVisible: false });

            return
        }

        var users: MailingListUser[] = [];

        users.push({
            id: item.id,
            userName: item.userName == null ? '' : item.userName.trim(),
            description: item.description == null ? '' : item.description.trim(),
            emailList: item.emailList == null ? '' : item.emailList.trim(),
            isGroupEmail: item.isGroupEmail == null ? false : item.isGroupEmail
        })

        if (this.state.currentItem.model.id === 0) {
            var newItem = users[0]

            dbwrap.AddRow("MailingListUsers", newItem, null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    var currentItem = this.state.currentItem.model
                    currentItem.id = data.data.id

                    this.handleButtonRefreshClick();
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisible: true
                    });
                }
            })
        }
        else {
            dbwrap.UpdateRow("MailingListUsers", users, null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    this.handleButtonRefreshClick();
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: getLabelText('PopupTitle_Error'),
                        popupMessage: errorMessage,
                        popupVisible: true
                    });
                }
            })
        }

        this.closeConfirmSaveFormClick('saved');
    }
    //    .
    closeConfirmSaveFormClick(action: string) {
        if (action != 'saved') { action = 'cancel' }

        this.setState({
            confirmFormVisible: false,
            isNeedToSave: false
        });

        if (this.state.currentItem != null && this.state.currentItem != undefined && action == 'cancel') {
            var item = this.state.data.find(x => x.id === this.state.currentItem.model.id)

            if (item != null && item != undefined) {
                this.setState({ currentItem: { model: { ...item }, rowKey: this.state.currentItem.rowKey } })
            }
        }

        //         ,   ,         (  )
        if (this.state.currentItem.model.id === 0) {
            this.setState({ currentItem: { model: { id: 0, userName: '', isGroupEmail: false }, rowKey: 0 } })

            if (this.state.isNew && this.state.prevCurrentItem != null) {
                this.setState({ currentItem: { ...this.state.prevCurrentItem } })
            }

            this.setState({ isNew: false, isNeedToSave: false })
        }

        this.setState({ prevCurrentItem: undefined })               //       ,     

        this.resetDataForCorrect()                                 //    

        this.handleButtonRefreshClick()
    }
    //     .
    confirmDeleteFormClick() {
        var item = this.state.currentItem.model

        dbwrap.DeleteRow("MailingListUsers", item.id, null).then((data: dbwrap.responseResult) => {
            if (data.success) {
                this.handleButtonRefreshClick();
            }
            else {
                let errorMessage: string = ''

                if (data.data.parsedErrObject == null) {
                    errorMessage = data.data.request.responseText
                }
                else {
                    errorMessage = data.data.parsedErrObject.message
                }

                this.setState({
                    popupTitle: getLabelText('PopupTitle_Error'),
                    popupMessage: errorMessage,
                    popupVisible: true
                });
            }
        })

        this.resetDataForCorrect()          //    

        this.closeConfirmDeleteFormClick();
    }
    //    .
    closeConfirmDeleteFormClick() {

        this.setState({
            confirmFormVisible: false,
            isNeedToSave: false
        });

        this.handleButtonRefreshClick()

        //    ,    
        if (this.state.data.length <= 1) {
            this.setState(
                {
                    currentItem: { model: { id: 0, userName: '', isGroupEmail: false }, rowKey: 0 },
                    isNew: false,
                    isNeedToSave: false
                }
            )
        }

        this.resetDataForCorrect()          //    
    }
    setToNextRow() {
        let item = this.state.data.find(x => x.id === this.state.nextRow)

        if (item != null) {
            this.setState({ currentItem: { model: { ...item }, rowKey: this.state.nextRow } })
        }

        this.setState({ nextRow: 0 })           //   ,    
    }
    setToPrevRow() {
        this.setState({ isVisibleToastForEdit: true })

        let dgdData = this.state.refComponents.dgdData

        if (dgdData != null) {
            if (this.state.prevCurrentItem != null) {
                dgdData.instance.navigateToRow(this.state.prevCurrentItem.model.id)
            }
            if (this.state.currentItem != null && this.state.currentItem.model.id != 0) {
                dgdData.instance.navigateToRow(this.state.currentItem.model.id)
            }
        }
    }
    onToastHiding() {
        this.setState({ isVisibleToastForEdit: false })
    }

    //  grid.
    handleButtonRefreshClick() {
        this.componentDidMount()
    }
    //   
    handleButtonRevertClick() {
        this.closeConfirmSaveFormClick('cancel')
    }
    //  popup.
    hidePopup() {
        this.setState({ popupVisible: false });
    }
    //  popup.    
    hidePopupForCriticalError() {
        this.setState({ popupVisibleForCriticalError: false });
        this.setState({ hasCriticalError: true });                      //True -       
    }

    render() {
        return (
            <div>
                <h3 style={{ width: "auto", textAlign: "center" }}>
                    {getLabelText('PageName')}
                </h3>

                <div hidden={this.state.hasCriticalError === false}>
                    <table width='100%' style={{ height: '80vh' }}>
                        <tr>
                            <td align='center'>
                                <h1>
                                    {getLabelText('Message_DirectoryNotAvailable')}
                                </h1>
                            </td>
                        </tr>
                    </table>
                </div>

                <div hidden={this.state.hasCriticalError}>
                    <Button icon="add" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNew || this.state.isNeedToSave} onClick={this.handleButtonNewClick} hint={getLabelText('Hint_Button_Add')} className='mb-2 mr-2' />
                    <Button icon="trash" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNew || this.state.isVisibleEditArea === false || this.state.isNeedToSave} onClick={this.handleButtonDeleteClick} hint={getLabelText('Hint_Button_Delete')} className='mb-2 mr-2' />
                    <Button icon="save" activeStateEnabled={false} hoverStateEnabled={false} disabled={this.state.isNeedToSave === false} onClick={this.handleButtonSaveClick} hint={getLabelText('Hint_Button_Save')} className='mb-2 mr-2' />
                    <Button icon="refresh" activeStateEnabled={false} hoverStateEnabled={false} visible={this.state.isNeedToSave === false && this.state.isNew === false} disabled={this.state.isNew} onClick={this.handleButtonRefreshClick} hint={getLabelText('Hint_Button_Refresh')} className='mb-2 mr-2' />
                    <Button icon="revert" activeStateEnabled={false} hoverStateEnabled={false} visible={this.state.isNeedToSave || this.state.isNew} onClick={this.handleButtonRevertClick} hint={getLabelText('Hint_Button_Cancel')} className='mb-2 mr-2' />

                    <div hidden={this.state.isVisibleEditArea === false && this.state.isNew === false}>
                        <table width={"100%"}>
                            <tr>
                                <td width={"16%"} /><td width={"52%"} /><td width={"2%"} /><td width={"30%"} />
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('UserName') + ":"}
                                </td>
                                <td>
                                    <TextBox maxLength={255} value={this.state.currentItem.model.userName} valueChangeEvent="input" onValueChanged={this.onChangeUserName}
                                        isValid={this.state.dataIsCorrect.userName.isValid} hint={this.state.dataIsCorrect.userName.message}
                                        ref={(ref) => { this.state.refComponents["txtUserName"] = ref }}                //    
                                    />
                                </td>
                                <td />
                                <td>
                                    <CheckBox text={getLabelText('IsGroupEmail')} value={this.state.currentItem.model.isGroupEmail} onValueChanged={this.onChangeIsGroupEmail} />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('EmailList') + ":"}
                                </td>
                                <td>
                                    <TextBox maxLength={500} value={this.state.currentItem.model.emailList} valueChangeEvent="input" onValueChanged={this.onChangeEmailList}
                                        isValid={this.state.dataIsCorrect.eMails.isValid} hint={this.state.dataIsCorrect.eMails.message}
                                        ref={(ref) => { this.state.refComponents["txtEmailList"] = ref }}               //    
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {getLabelText('Description') + ":"}
                                </td>
                                <td>
                                    <TextBox maxLength={500} value={this.state.currentItem.model.description} valueChangeEvent="input" onValueChanged={this.onChangeDescription} />
                                </td>
                            </tr>
                        </table>
                    </div>

                    <Popup
                        visible={this.state.confirmFormVisible}
                        dragEnabled={false}
                        closeOnOutsideClick={false}
                        showCloseButton={false}
                        showTitle={true}
                        width={500}
                        height={300}
                        title={this.state.confirmFormTitle}
                        container=".dx-viewport">

                        <TextArea
                            height={150}
                            value={this.state.confirmFormMessage}
                            readOnly={true}
                        />

                        <ToolbarItem location="after" toolbar="bottom" widget="dxButton" options={{ text: this.state.confirmFormOkButtonText, onClick: this.state.confirmFormOkButton }}></ToolbarItem>
                        <ToolbarItem location="after" toolbar="bottom" widget="dxButton" options={{ text: this.state.confirmFormCancelButtonText, onClick: this.state.confirmFormCancelButton }}></ToolbarItem>
                    </Popup>

                    <MessagePopup
                        visible={this.state.popupVisible}
                        title={this.state.popupTitle}
                        hidePopup={this.hidePopup}
                        message={this.state.popupMessage}
                    />
                    <MessagePopup
                        visible={this.state.popupVisibleForCriticalError}
                        title={this.state.popupTitle}
                        hidePopup={this.hidePopupForCriticalError}
                        message={this.state.popupMessage}
                    />

                    <br />

                    <DataGrid
                        dataSource={this.state.data}
                        keyExpr="id"
                        showBorders={true}
                        focusedRowEnabled={true}
                        allowColumnResizing={true}
                        allowColumnReordering={true}
                        onFocusedRowChanged={this.onFocusedRowChanged}
                        onRowClick={this.onRowClick}
                        focusedRowKey={this.state.currentItem.rowKey}
                        onFocusedRowChanging={this.onFocusedRowChanging}

                        autoNavigateToFocusedRow={true}
                        onContentReady={this.onContentReady}
                        ref={(ref) => { this.state.refComponents["dgdData"] = ref }}
                    >

                        <Paging defaultPageSize={10} />
                        <Pager
                            visible={true}
                            allowedPageSizes={allowedPageSizes}
                            displayMode={'full'}
                            showPageSizeSelector={true}
                            showInfo={true}
                            showNavigationButtons={true} />

                        <FilterRow visible={true} />
                        <HeaderFilter visible={true} />
                        <ColumnChooser enabled={true} mode="select" />
                        <Sorting mode="multiple" />
                        <SearchPanel visible={true} />

                        <Column dataField="userName" dataType="string" caption={getLabelText('UserName')} />
                        <Column dataField="emailList" dataType="string" caption={getLabelText('EmailList')} />
                        <Column dataField="description" dataType="string" caption={getLabelText('Description')} />
                        <Column dataField="isGroupEmail" dataType="boolean" caption={getLabelText('IsGroupEmail')} />
                    </DataGrid>

                    <Toast
                        visible={this.state.isVisibleToastForEdit}
                        message={getLabelText('Message_EditMode')}
                        type={'warning'}
                        onHiding={this.onToastHiding}
                        displayTime={3000}
                        width={400}
                        position={'center center'}
                    />
                </div >
            </div >
        );
    }
}

