import { LoadingStage } from '../commonTypes';
import * as actions from '../DbWrapper';
import { Tables } from '../DTO/Tables';

export interface TableStoreItemType {
    tableName: string;
    rows: Array<any>;
    loadingStage: LoadingStage;
    error: object;
    currentRowIndex: number
};

function getTableDflt(tblName: string): TableStoreItemType {
    return { tableName: tblName, rows: [], loadingStage: LoadingStage.undefined, error: {}, currentRowIndex: -1 };
}

const defaultState: TableStoreItemType[] = (() => {
    let result: TableStoreItemType[] = [];
    for (let tbl in Tables) {
        result.push(getTableDflt(tbl))
    }
    return result;
})();

type DBWrapperPayload = {
    tableName: string;
    rows: Array<any>;
    row: object;
    error: object;
    currentRowIndex: number;
    rowUid: number
}

interface TableAction {
    type: string,
    payload: DBWrapperPayload
}


function reducer(state: Array<TableStoreItemType> = defaultState, action: TableAction) {
    if (
        action.type != actions.ACT_TABLEROWS_REQUEST
        && action.type != actions.ACT_TABLEROWS_RECEIVE
        && action.type != actions.ACT_TABLEROWS_ERROR
        && action.type != actions.ACT_SETCURRENTROW_INDEX
        && action.type != actions.ACT_ROW_ADD
        && action.type != actions.ACT_ROW_UPDATE
        && action.type != actions.ACT_ROW_DELETE) {
        return state;
    }
    let tableName: string = "";
    let newTableObj: TableStoreItemType;
    tableName = action.payload.tableName;
    let currTableObjIndex: number = state.findIndex(item => item.tableName === action.payload.tableName);
    if (currTableObjIndex === -1) {
        let newTbl = getTableDflt(tableName);
        state.push(newTbl);
        currTableObjIndex = state.findIndex(item => item.tableName === action.payload.tableName);
    }
    let newState = [...state];
    newTableObj = { ...newState[currTableObjIndex] };
    switch (action.type) {
        case actions.ACT_TABLEROWS_REQUEST:
            newTableObj.loadingStage = LoadingStage.loading;
            newTableObj.currentRowIndex = -1;
            newState[currTableObjIndex] = newTableObj;
            return newState;
        case actions.ACT_TABLEROWS_RECEIVE:
            newTableObj.loadingStage = LoadingStage.loaded;
            newTableObj.rows = action.payload.rows;
            newTableObj.currentRowIndex = 0;
            newState[currTableObjIndex] = newTableObj;
            return newState;
        case actions.ACT_TABLEROWS_ERROR:
            newTableObj.loadingStage = LoadingStage.error;
            //newTableObj.rows = [];
            //newTableObj.currentRowIndex = -1;
            newTableObj.error = action.payload.error;
            newState[currTableObjIndex] = newTableObj;
            return newState;
        case actions.ACT_SETCURRENTROW_INDEX:
            newTableObj.currentRowIndex = action.payload.currentRowIndex;
            newState[currTableObjIndex] = newTableObj;
            return newState;
        case actions.ACT_ROW_ADD:
            newTableObj.rows.push(action.payload.row);
            newState[currTableObjIndex] = newTableObj;
            return newState;
        case actions.ACT_ROW_UPDATE:
            //@ts-ignore
            let rowIndex = newTableObj.rows.findIndex(itm => itm.id === action.payload.row.id);
            newTableObj.rows[rowIndex] = action.payload.row;
            newState[currTableObjIndex] = newTableObj;
            return newState;
        case actions.ACT_ROW_DELETE:
            newTableObj.rows = newTableObj.rows.filter(itm => itm.id != action.payload.rowUid);
            newState[currTableObjIndex] = newTableObj;
            return newState;
        default:
            return state;
    }
}

export default reducer;

