import React, { ReactNode } from 'react';
import Lookup from 'devextreme-react/lookup';
import { customDataSource } from '../DTO/Tables';
import getLocMsg, { LocMsgId } from '../localization';

import Form, {
    Item,
    SimpleItem,
    Label,
} from 'devextreme-react/form';


import { TableEditorPageClass, TableEditorMode } from './TableEditorPage';
import { GetColumns } from '../DTO/Tables';
import { ErrorMsgFromDict } from './DxStyleSimple';
import { GetCurrentColCountByScr } from '../commonTypes';
import Button from 'devextreme-react/button';

interface FEProps {
    tableEditorInstance: TableEditorPageClass
}

interface FEState {
}

const colCountByScr = {
    sm: 2,
    md: 4,
    lg: 5
}

export class FormEditor extends React.Component<FEProps, FEState> {
    constructor(props: FEProps) {
        super(props);
        this.state = {
        };
        this.handleSaveBtnClick = this.handleSaveBtnClick.bind(this);
        this.handleFieldChange = this.handleFieldChange.bind(this);
        this.handleCancelBtnClick = this.handleCancelBtnClick.bind(this);
    }

    handleFieldChange(e: any, fieldName: string) {
        let newValue = "";
        if (e.event && e.event.currentTarget) {
            newValue = e.event.currentTarget.value;
        }
        if (e.value) {
            newValue = e.value;
        }
        let hs = this.checkChanges(fieldName, newValue);
        //@ts-ignore
        let tblEditor = this.props.tableEditorInstance;
        tblEditor.setState({ hasChanges: hs });
        if(hs && this.props.tableEditorInstance.state.editorMode !== TableEditorMode.AddRow){
            this.props.tableEditorInstance.setState({editorMode:TableEditorMode.EditRow})
        }
    }

    handleSaveBtnClick() {
        let teInstance = this.props.tableEditorInstance;
        if (teInstance) {
            teInstance.props.actions.SaveRowChangesOnServer(teInstance, teInstance.state.newRowValue);
            teInstance.setState({ hasChanges: false });
        }
    }

    handleCancelBtnClick() {
        let teInstance = this.props.tableEditorInstance;
        if (teInstance) {
            if (teInstance.state.editorMode !== TableEditorMode.AddRow) {
                teInstance.props.actions.RollbackChangesInRedux(teInstance);
                teInstance.setState({ hasChanges: false });
                return;
            }
            let currRow = teInstance.props.rows[teInstance.props.currentRowIndex];
            teInstance.setState({ hasChanges:false,editorMode: TableEditorMode.None, oldRowValue: currRow, newRowValue: currRow });
        }
    }

    getTableName() {
        //@ts-ignore
        let tableName = this.props.tableEditorInstance.props.tableName;
        return tableName;
    }

    getFormData() {
        return this.props.tableEditorInstance.state.newRowValue;
    }


    checkChanges(fieldName: string, currFldValue: any): boolean {
        let currRowValue = { ...this.getFormData() }; //     (   tableEditor   Redux)
        let oldRowValue = this.props.tableEditorInstance.state.oldRowValue;//  (  )
        let prevFldValue = undefined;
        if (currRowValue) {
            prevFldValue = currRowValue[fieldName]; //     (   tableEditor   Redux)
        }
        else {
            return false;
        }
        let oldFldValue = undefined;
        if (oldRowValue) {
            //@ts-ignore
            oldFldValue = oldRowValue[fieldName]; //  
        }
        let fieldType = typeof (oldFldValue);
        if (fieldType === 'number') {
            currFldValue = Number(currFldValue);
        }
        //@ts-ignore
        let changesBeforeLostFocus = (oldFldValue !== currFldValue);
        let changesAfterLostFocus = false;
        for (let prop in oldRowValue) {
            if (prop === fieldName) {
                continue;
            }
            //@ts-ignore            
            if (currRowValue[prop] !== oldRowValue[prop]) {
                changesAfterLostFocus = true;
                break;
            }
        }
        currRowValue[fieldName] = currFldValue;
        let result = changesBeforeLostFocus || changesAfterLostFocus;
        let tblEd = this.props.tableEditorInstance;
        if (result) {
            tblEd.handleRowChange(currRowValue);
        }
        console.log(currRowValue, result);
        return result;
    }

    renderEmptyColumns() {
        let allColumnCount = GetCurrentColCountByScr(colCountByScr);
        let emptyColCount: number = allColumnCount - 2;
        let eCols = [];
        for (let i: number = 0; i < emptyColCount; i++) {
            eCols.push(<Item key={"emptyCol" + i} itemType="empty" />);
        }
        return eCols;
    }

    renderColumns(): any {
        let tableName = this.getTableName();
        let colDefinition = GetColumns(tableName);
        let result: any = [];
        colDefinition.forEach((itm) => {
            let disableEdit = false;
            let wdth = undefined;
            let ratio = undefined;
            if (itm.dataField === 'id') {
                disableEdit = true;
                wdth = 200;
                ratio = 1;
            }
            let caption = itm.dataField;
            if (itm.captionId) {
                let captId = itm.captionId;
                caption = getLocMsg(captId);
            }
            let newItem = (
                <SimpleItem key={itm.dataField} dataField={itm.dataField} editorType="dxTextBox"
                    editorOptions={{ onInput: (e: any) => this.handleFieldChange(e, itm.dataField), disabled: disableEdit, width: wdth }}>
                    <Label text={caption} />
                </SimpleItem>
            );
            if (itm.lookup) {
                newItem = (<SimpleItem key={itm.dataField} dataField={itm.dataField} editorType="dxSelectBox" editorOptions={{
                    dataSource: customDataSource(itm.lookup.dataSource),
                    displayExpr: itm.lookup.displayExpr,
                    valueExpr: itm.lookup.valueExpr,
                    onValueChanged: (e: any) => this.handleFieldChange(e, itm.dataField)
                }} >
                    <Label text={caption} />
                </SimpleItem>);
            }
            result.push(newItem);
        });
        return result;
    }

    render() {
        let ronly = !(this.props.tableEditorInstance.state.isRowSelected || this.props.tableEditorInstance.state.editorMode === TableEditorMode.AddRow);
        let disableSaveBtn = !this.props.tableEditorInstance.state.hasChanges;
        let disableCancelBtn = !this.props.tableEditorInstance.state.hasChanges && this.props.tableEditorInstance.state.editorMode!==TableEditorMode.AddRow;
        return (
            <div>
                <Form id="FormEditor" formData={this.getFormData()} minColWidth={350}
                    readOnly={ronly}
                    colCount={"auto"} >
                    {this.renderColumns()}
                </Form>
                {ErrorMsgFromDict(this.props.tableEditorInstance.state.error)}
                <div style={{ display: "flex", justifyContent: "flex-end", marginBottom: "10px", marginTop:"10px" }}>
                    <Button icon="save" text={getLocMsg(LocMsgId.Button_Save)} disabled={disableSaveBtn} onClick={this.handleSaveBtnClick} style={{ marginLeft: "10px" }} />
                    <Button icon="clear" text={getLocMsg(LocMsgId.Button_Cancel)} disabled={disableCancelBtn} onClick={this.handleCancelBtnClick} style={{ marginLeft: "10px" }} />
                </div>
            </div>
        );
    }

}
