    //#region Confirm functions
    //      
    confirmSaveFormVisible() {
        this.setState({
            confirmFormVisible: true,
            confirmFormTitle: labels.Message_Confirm(),
            confirmFormMessage: labels.Message_ConfirmSave(),
            confirmFormOkButtonText: labels.Button_Save(),
            confirmFormOkButton: this.confirmSaveFormClick,
            confirmFormCancelButtonText: labels.Button_Cancel(),
            confirmFormCancelButton: this.closeConfirmSaveFormClick
        })
    }

    //     .
    confirmSaveFormClick() {
        var item = this.state.currentItem.model

        //       
        if (this.checkDataForCorrect(item) === false) {
            this.setState({ confirmFormVisible: false });

            return
        }

        // Need validate if saving new terminal.
        if (this.state.isNew) {
            this.setState({ confirmFormVisible: false });

            let value = item.apiKey == null ? '' : item.apiKey.trim()

            let isValid = value != null && value != '' && value != undefined
            if (!isValid) {
                this.setState({
                    isVisibleToast: true,
                    toastMessage: labels.ApiKeyRequired()
                })

                return;
            }
        }

        var terminals: Terminal[] = [];

        terminals.push({
            id: item.id,
            name: item.name.trim(),
            address: item.address.trim(),
            terminalKey: item.terminalKey.trim(),
            dealerId: item.dealerId,
            properties: item.properties,
            stackerCapacity: item.stackerCapacity,
            enabled: item.enabled,
            inServing: item.inServing,
            cityId: item.locationId == null ? 0 : item.locationId,
            rnm: item.rnm.trim(),
            serialNumber: item.serialNumber.trim(),
            deviceCode: item.deviceCode.trim(),
            account: item.account == null ? undefined : item.account.trim(),
            serviceNote: item.serviceNote == null ? undefined : item.serviceNote.trim(),
            profileId: item.profileId,
            typePointPlaceId: item.typePointPlaceId,
            netTypeId: item.netTypeId,
            servicesProfileDate: item.servicesProfileDate,
            terminalSerialNumber: item.terminalSerialNumber == null ? undefined : item.terminalSerialNumber.trim(),
            warrantyPeriod: item.warrantyPeriod,
            apiKey: item.apiKey == null ? undefined : item.apiKey.trim(),
            cashDeskCode: item.cashDeskCode == null ? undefined : item.cashDeskCode.trim(),
            timezoneId: item.timezoneId
        });

        var terminalAttributes: TerminalAttribute[] = item.terminalAttributes;
        var terminalSchedules: TerminalSchedule[] = item.terminalSchedules;

        // Convert UserModel[] to UserTerminal[].
        var userTerminals: UserTerminal[] = [];
        for (let i = 0; i < item.users.length; i++) {
            userTerminals.push({ id: item.users[i].id, userId: item.users[i].userId, terminalId: item.users[i].terminalId });
        }

        var newItem = terminals[0]
        let terminalDTO: TerminalDTO = { terminal: newItem, terminalSchedules: terminalSchedules, terminalAttributes: terminalAttributes, userTerminals: userTerminals };

        if (this.state.currentItem.model.id === 0) {
            dbwrap.AddRow("TerminalDicts", terminalDTO, null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    // Add new terminal to list.
                    let terminalDTOList = this.state.terminalDTOList;
                    terminalDTOList.push(data.data);

                    this.convertToModelArray();

                    // Set focus to new row.
                    let currentItem = this.state.currentItem;
                    currentItem.rowKey = data.data.terminal.id;

                    this.closeConfirmSaveFormClick('saved');
                }
                else if (data.data.response.status == 453) {
                    this.setUniqueViolationError();
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: labels.PopupTitle_Error(),
                        popupMessage: errorMessage,
                        popupVisible: true
                    });
                }
            })
        }
        else {
            dbwrap.UpdateRow("TerminalDicts", terminalDTO, null).then((data: dbwrap.responseResult) => {
                if (data.success) {
                    dbwrap.GetRows("TerminalDicts/" + terminalDTO.terminal.id, null).then((data: dbwrap.responseResult) => {
                        if (data.success) {
                            // Update terminal by Id.
                            let terminalDTOList = this.state.terminalDTOList;
                            let terminalIndex = terminalDTOList.findIndex(x => x.terminal.id == data.data.terminal.id);
                            if (terminalIndex != -1)
                                terminalDTOList[terminalIndex] = data.data;

                            this.convertToModelArray();

                            this.closeConfirmSaveFormClick('saved');
                        }
                    })
                }
                else if (data.data.response.status == 453) {
                    this.setUniqueViolationError();
                }
                else {
                    let errorMessage: string = ''

                    if (data.data.parsedErrObject == null) {
                        errorMessage = data.data.request.responseText
                    }
                    else {
                        errorMessage = data.data.parsedErrObject.message
                    }

                    this.setState({
                        popupTitle: labels.PopupTitle_Error(),
                        popupMessage: errorMessage,
                        popupVisible: true
                    });
                }
            })
        }

        this.setState({ confirmFormVisible: false });
    }

    //    .
    closeConfirmSaveFormClick(action: string) {
        if (action != 'saved') { action = 'cancel' }

        this.setState({
            confirmFormVisible: false,
            isNeedToSave: false
        });

        if (this.state.currentItem != null && this.state.currentItem != undefined && action == 'cancel') {
            // Reset inner collection.
            this.convertToModelArray();

            var item = this.state.data.find(x => x.id === this.state.currentItem.model.id)

            if (item != null && item != undefined) {
                this.setCurrentItem({ ...item }, this.state.currentItem.rowKey)
            }
        }

        //         ,   ,         (  )
        if (this.state.currentItem.model.id === 0) {
            this.setCurrentItem(this.createNewModel(), 0)

            if (this.state.isNew && this.state.prevCurrentItem != null) {
                this.setCurrentItem({ ...this.state.prevCurrentItem.model }, this.state.prevCurrentItem.rowKey)
            }

            this.setState({ isNew: false, isNeedToSave: false })
        }

        this.setState({ prevCurrentItem: undefined })               //       ,     

        this.resetDataForCorrect()                                 //    
    }
