    //#region Terminal DataGrid functions
    createNewModel(): Model {
        let model: Model = {
            id: 0,
            name: "",
            address: "",
            terminalKey: "",
            dealerId: 0,
            properties: "",
            stackerCapacity: 0,
            enabled: true,
            inServing: false,
            cityId: 0,
            rnm: "",
            serialNumber: "",
            deviceCode: "",
            account: "",
            serviceNote: "",
            profileId: 0,
            netTypeId: 0,
            terminalSerialNumber: "",
            apiKey: "",
            cashDeskCode: "",
            timezoneId: 0,
            terminalAttributes: [],
            terminalSchedules: [],
            userTerminals: [],
            users: [],
            technicalSupportNumbers: [],
            technicalSupportNumbersString: "",
            callCenterNumbers: [],
            callCenterNumbersString: ""
        }

        return model
    }

    changeCurrentRow(e: Model) {
        this.setState({ isNew: false })             //  ,     False  isNew

        if (e == null) { return }

        const dataRow = e;

        this.setCurrentItem({ ...dataRow }, dataRow.id)

        this.setState({ isNeedToSave: false })

        this.resetDataForCorrect()          //    

        this.setState({ userFilteredList: this.getListUser() })
    }

    onFocusedRowChanged(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }

        this.changeCurrentRow(e.row && e.row.data)
    }

    onFocusedRowChanging(e: any) {
        if (this.state.isNeedToSave) {
            e.cancel = true;

            return
        }
    }

    onContentReady(e: any) {
        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        }

        var item = this.state.currentItem.model
        var key = item.id
        var keyExistsInFilteredRows = -1
        var filteredRows: any

        this.onFocusedRowChanged(item)                                                     //       , ,     
        if (this.state.isNeedToSave) { return }                                            //  isNeedToSave     

        this.setCurrentItem({ ...item }, key)                                              //      

        let dgdData = this.state.refComponents.dgdData

        if (item != null && dgdData != null) {
            dgdData.instance.getDataSource().store()                                       //          .
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    filteredRows = result;                                                 //     filteredRows
                });

            keyExistsInFilteredRows = filteredRows.find((x: any) => x.id === key)          //        
        }

        if (item == null || item.id === 0 || keyExistsInFilteredRows == null) {            //            Id,    
            if (dgdData != null) {
                key = dgdData.instance.getKeyByRowIndex(0)                                 // Key    (  -    )
            }
        }

        if (key != null && dgdData != null) {
            dgdData.instance.getDataSource().store().load({ filter: ['id', '=', key] })
                .then((result: any) => {
                    filteredRows = result;
                });
            item = filteredRows.find((x: any) => x.id === key)
        }

        this.setCurrentItem({ ...item }, key)

        //,   ,    
        if (dgdData != null) {
            dgdData.instance.getDataSource().store()                                    //          .
                .load({ filter: dgdData.instance.getCombinedFilter() })
                .then((result: any) => {
                    filteredRows = result;                                                 //     filteredRows
                });

            if (filteredRows.length === 0) {
                this.setState({ isVisibleEditArea: false })
            }
            else {
                this.setState({ isVisibleEditArea: true })
            }
        }
    }

    setToPrevRow() {
        this.setState({
            isVisibleToast: true,
            toastMessage: labels.Message_EditMode()
        })

        let dgdData = this.state.refComponents.dgdData

        if (dgdData != null) {
            if (this.state.prevCurrentItem != null) {
                dgdData.instance.navigateToRow(this.state.prevCurrentItem.model.id)
            }
            if (this.state.currentItem != null && this.state.currentItem.model.id != 0) {
                dgdData.instance.navigateToRow(this.state.currentItem.model.id)
            }
        }
    }

    onRowClick(e: any) {
        if (this.state.currentItem.model.id === e.data.id) { return }

        if (this.state.isNeedToSave) {
            this.setToPrevRow()

            return
        };

        this.changeCurrentRow(e.data)
    }

    setCurrentItem(model: Model, rowKey: number) {
        let copy: Model = model;

        // Clone inner collections.
        let terminalAttributes: TerminalAttribute[] = [];
        for (let i = 0; i < model.terminalAttributes.length; i++) {
            terminalAttributes.push({ ...model.terminalAttributes[i] });
        }
        copy.terminalAttributes = terminalAttributes;

        let terminalSchedules: TerminalSchedule[] = [];
        for (let i = 0; i < model.terminalSchedules.length; i++) {
            terminalSchedules.push({ ...model.terminalSchedules[i] });
        }
        copy.terminalSchedules = terminalSchedules;

        let userTerminals: UserTerminal[] = [];
        for (let i = 0; i < model.userTerminals.length; i++) {
            userTerminals.push({ ...model.userTerminals[i] });
        }
        copy.userTerminals = userTerminals;

        this.setState({ currentItem: { model: copy, rowKey: rowKey } })
        this.setState({ userFilteredList: this.getListUser() })
    }
