    //#region Add/Remove buttons functions
    //  
    handleButtonAddUserClick() {
        if (
            this.state.currentItem_ListUser == null ||
            this.state.currentItem_ListUser.model == null ||
            this.state.currentItem_ListUser.model.id === 0
        ) { return }

        let userId = this.state.currentItem_ListUser.model.id
        let userName = this.state.currentItem_ListUser.model.username

        let terminal = { ...this.state.currentItem.model };

        if (terminal.users.findIndex(x => x.userId == userId) === -1) {
            let addedUserTerminalModel: UserTerminalModel = { id: 0, userId: userId, terminalId: terminal.id, userName: userName };
            terminal.users.push(addedUserTerminalModel);

            let dgdListUserSelected = this.state.refComponents.dgdListUserSelected
            dgdListUserSelected.instance.refresh();
            this.setState({ userFilteredList: this.getListUser() });

            this.applyChange_ListUser(terminal);
        }
    }

    //  
    handleButtonRemoveUserClick() {
        if (
            this.state.currentItem_ListUserSelected == null ||
            this.state.currentItem_ListUserSelected.model == null
        ) { return }

        let userId = this.state.currentItem_ListUserSelected.model.userId;

        let terminal = { ...this.state.currentItem.model };

        let index = terminal.users.findIndex(x => x.userId == userId);
        terminal.users.splice(index, 1);

        let dgdListUserSelected = this.state.refComponents.dgdListUserSelected
        dgdListUserSelected.instance.refresh();
        this.setState({ userFilteredList: this.getListUser() });

        this.applyChange_ListUser(terminal);
    }

    getListUser(): any {
        let listUsers: User[]

        listUsers = { ...this.state.userList }               //  ,  ,         "" 

        listUsers = Object.values(listUsers)                 //    

        if (listUsers != null && this.state.currentItem.model.users != null) {
            for (let i = 0; i < this.state.currentItem.model.users.length; i++) {
                let index = listUsers.findIndex(x => x.id === this.state.currentItem.model.users[i].userId)

                if (index >= 0) {
                    listUsers.splice(index, 1);
                }
            }
        }

        return listUsers
    }
