    //#region Change events handlers
    applyChange(target: any) {
        let item = this.state.data.find(x => x.id === target.id)
        let currentItem: string = ""

        if (item != null) {
            currentItem = JSON.stringify(item)
        }

        let newItem = JSON.stringify(target)

        let hasChanged = currentItem != newItem

        this.setCurrentItem(target, this.state.currentItem.rowKey)                                     //  

        this.setState({ isNeedToSave: hasChanged })                                                    //  " "
    }

    // Compare old and new UsersTerminalsModel arrays.
    equalListUsers(newItems: UserTerminalModel[], oldItems: UserTerminalModel[]): boolean {
        if (newItems == null || oldItems == null) { return false }

        let result: boolean = true;

        if (newItems.length != oldItems.length) {
            return false;
        }
        else {
            for (let i = 0; i < oldItems.length; i++) {
                if (newItems.findIndex(x => x.userId == oldItems[i].userId) === -1) {
                    return false;
                }
            }
        }

        return result;
    }

    applyChange_ListUser(target: Model) {
        if (this.state.isVisibleEditArea === false) { return }

        let hasChanged = !this.equalListUsers(target.users, Object.values(this.state.currentUsersTerminalModels));

        if (hasChanged) {
            this.applyChange(target);
        }
        this.setState({ isNeedToSave: hasChanged });
    }

