    onChangeTerminalType = (e: any) => {
        let changedItem = { ...this.state.currentItem.model };

        if (changedItem.terminalTypeId == null) {
            // If terminal attribute creating.
            changedItem.terminalTypeId = e.value;

            if (this.state.terminalTypeList != null && this.state.terminalTypeList.length > 0) {
                let foundItem = this.state.terminalTypeList.find((x: any) => x.id === changedItem.terminalTypeId)
                if (foundItem != null) {
                    let newAttribute: TerminalAttribute = { id: 0, terminalId: changedItem.id, attributeId: foundItem.id }
                    changedItem.terminalAttributes.push(newAttribute);
                }
            }
        }
        else {
            // If terminal attribute editing.
            changedItem.terminalTypeId = e.value;

            if (this.state.terminalTypeList != null && this.state.terminalTypeList.length > 0) {
                let previousAttribute = changedItem.terminalAttributes.find(x => x.attributeId == e.previousValue);

                let foundItem = this.state.terminalTypeList.find((x: any) => x.id === changedItem.terminalTypeId)
                if (foundItem != null && previousAttribute != null) {
                    previousAttribute.attributeId = foundItem.id
                }
            }
        }

        this.applyChange(changedItem);

        this.checkTerminalTypeForCorrect(changedItem);
    }
