    //#region Validation functions
    //    ,    
    checkTerminalNameForCorrect(model: Model): IncorrectData {
        let value = model.name == null ? '' : model.name.trim()

        let isValid = value != null && value != '' && value != undefined
        let message = isValid ? '' : labels.TerminalName_Required()

        if (isValid) {
            isValid = this.state.data.find(x => x.name.toUpperCase() === value.toUpperCase() && x.id != model.id) === undefined
            message = isValid ? '' : labels.TerminalName_Unique()
        }

        let result: IncorrectData = { isValid: isValid, message: message }

        this.setState({ terminalNameIsCorrect: { isValid: result.isValid, message: result.message } })

        return result
    }
    //    ,    
    checkAddressForCorrect(model: Model): IncorrectData {
        let value = model.address == null ? '' : model.address.trim()

        let isValid = value != null && value != '' && value != undefined
        let message = isValid ? '' : labels.TerminalAddress_Required()

        let result: IncorrectData = { isValid: isValid, message: message }

        this.setState({ addressIsCorrect: { isValid: result.isValid, message: result.message } })

        if (isValid) {
            result = this.checkAddressForCorrect_2(value)      // 
        }

        return result
    }
    //    ,    
    checkAddressForCorrect_2(value: string): IncorrectData {
        let regexp = new RegExp(/^[?!,.#@$^%&*()\-_+~`:;"'/\\|<>--0-9\s]+$/)

        let isValid = regexp.test(value.trim())
        let message = isValid ? '' : labels.AddressRegRus_Incorrect()

        let result: IncorrectData = { isValid: isValid, message: message }

        this.setState({ addressIsCorrect: { isValid: result.isValid, message: result.message } })

        return result
    }
