    //    ,    
    checkTechnicalSupportNumbersForCorrect(model: Model): IncorrectData {
        var result: IncorrectData = { isValid: true, message: '' }
        this.setState({ technicalSupportNumbersIsCorrect: { isValid: true, message: '' } });

        let value = model.properties;

        if (value == null || value == "") {
            result = { isValid: false, message: labels.TechnicalSupportNumbers_Required() }
            this.setState({ technicalSupportNumbersIsCorrect: { isValid: result.isValid, message: result.message } })
        }
        else {
            let properties: PropertyModel[] = [];

            properties = JSON.parse(value);
            let technicalSupport = properties.find(x => x.name == "SupportPhones");

            if (technicalSupport != null && technicalSupport.phones.length == 0) {
                result = { isValid: false, message: labels.TechnicalSupportNumbers_Required() }
                this.setState({ technicalSupportNumbersIsCorrect: { isValid: false, message: labels.TechnicalSupportNumbers_Required() } });
            }
        }

        return result
    }
