    //#region Drop down box contents
    //#region Location data grid functions
    getLocationGrid(): any {
        return (
            <DataGrid
                dataSource={this.state.locationList}
                hoverStateEnabled={true}
                onSelectionChanged={this.onChangeLocation}
                height="100%"

                ref={(ref) => { this.state.refComponents["dgdLocation"] = ref }}
            >
                <Selection mode="single" />
                <Scrolling mode="virtual" />
                <FilterRow visible={true} />

                <Column dataField="countryName" dataType="string" caption={labels.Country()} />
                <Column dataField="areaName" dataType="string" caption={labels.Area()} />
                <Column dataField="cityName" dataType="string" caption={labels.City()} />
            </DataGrid>
        );
    }

    onOptionChangedLocation = (e: any) => {
        if (e.name === 'opened') {
            this.setState({ isLocationGridBoxOpened: e.value });

            this.state.refComponents.dgdLocation.instance.clearFilter();            //  
            this.state.refComponents.dgdLocation.instance.clearGrouping();          //  
            this.state.refComponents.dgdLocation.instance.clearSelection();         //  
            this.state.refComponents.dgdLocation.instance.clearSorting();           //  
        }
    }
    //#endregion

    //#region Technical support numbers data grid functions
    // Render data grid for technical support numbers.
    getTechnicalSupportNumberGrid(): any {
        return (
            <DataGrid
                dataSource={this.state.currentItem.model.technicalSupportNumbers}
                showBorders={true}
                height="100%"
                onInitNewRow={this.onTechnicalSupportNumberInitNewRow}
                onEditingStart={this.onTechnicalSupportNumberEditingStart}
                onEditCanceled={this.onTechnicalSupportNumberEditCanceled}
                onSaving={this.onTechnicalSupportNumberSaving}
                onSaved={this.onTechnicalSupportNumberSaved}
                ref={(ref) => { this.state.refComponents["dgdTechnicalSupportNumbers"] = ref }}
            >
                <Editing
                    mode="row"
                    allowUpdating={true}
                    allowAdding={this.state.isTechnicalSupportNumberAllowAdding}
                    allowDeleting={true}
                    useIcons={true}
                    confirmDelete={false}
                    newRowPosition="last" />
                <Scrolling mode="virtual" />
                <Column dataField="number" dataType="string" caption={labels.TechnicalSupportNumbers() + ":"} />
            </DataGrid>
        );
    }

    onOptionChangedTechnicalSupportNumber = (e: any) => {
        if (this.state.isTechnicalSupportNumberEdited) {
            // Cancel edit data if data grid is closing.
            let dgdData = this.state.refComponents.dgdTechnicalSupportNumbers;
            dgdData.instance.cancelEditData();
        }

        if (e.name === 'opened') {
            let dropDownBox = this.state.refComponents.ddbTechnicalSupportNumbers;
            dropDownBox.instance.repaint();

            let technicalSupportNumbers = this.state.currentItem.model.technicalSupportNumbers;
            if (technicalSupportNumbers == null) {
                return;
            }

            // Disable adding button if numbers more than 10.
            if (technicalSupportNumbers.length >= 10) {
                this.setState({ isTechnicalSupportNumberAllowAdding: false });
            }
            else {
                this.setState({ isTechnicalSupportNumberAllowAdding: true });
            }

            // If drop down is closing.
            if (e.value == false) {
                let technicalSupportProperty: PropertyModel = { name: "SupportPhones", phones: this.getPhoneModels(technicalSupportNumbers) };
                let callCenterProperty: PropertyModel = { name: "CallCenter", phones: this.getPhoneModels(this.state.currentItem.model.callCenterNumbers) };

                let properties: PropertyModel[] = [];
                properties.push(technicalSupportProperty, callCenterProperty);

                this.onChangeTechnicalSupportNumbers(JSON.stringify(properties));
            }

            this.setState({ isTechnicalSupportNumberGridOpened: e.value });
        }
    }

    onTechnicalSupportNumberInitNewRow = (e: any) => {
        this.setTechnicalSupportNumberEditState(true);
    }

    onTechnicalSupportNumberEditingStart = (e: any) => {
        this.setTechnicalSupportNumberEditState(true);
    }

    onTechnicalSupportNumberEditCanceled = (e: any) => {
        this.setTechnicalSupportNumberEditState(false);
    }

    onTechnicalSupportNumberSaving = (e: any) => {
        if (e.changes.length != 0 && e.changes[0].type != 'remove') {
            if (e.changes[0].data.number != null) {
                e.changes[0].data.number = e.changes[0].data.number.trim();
            }

            let phoneNumber: string = e.changes[0].data.number;

            if (phoneNumber == null || phoneNumber.length == 0) {
                e.cancel = true;
                this.setState({
                    isVisibleToast: true,
                    toastMessage: labels.TechnicalSupportNumber_Required()
                });
            }
            else if (phoneNumber.length > 25) {
                e.cancel = true;
                this.setState({
                    isVisibleToast: true,
                    toastMessage: labels.TechnicalSupportNumber_MaxLength()
                });
            }
            else if (!this.checkUniqueNumber(this.state.currentItem.model.technicalSupportNumbers, phoneNumber)) {
                e.cancel = true;
                this.setState({
                    isVisibleToast: true,
                    toastMessage: labels.TechnicalSupportNumber_Unique()
                });
            }
        }
    }

    onTechnicalSupportNumberSaved = (e: any) => {
        this.setTechnicalSupportNumberEditState(false);

        let model: Model = this.state.currentItem.model;

        if (model.technicalSupportNumbers.length >= 10) {
            // Show toast once during adding 10th phone.
            if (this.state.isTechnicalSupportNumberAllowAdding)
                this.setState({
                    isTechnicalSupportNumberAllowAdding: false,
                    isVisibleToast: true,
                    toastMessage: labels.TechnicalSupportNumber_MaxCount()
                });
        }
        else {
            this.setState({ isTechnicalSupportNumberAllowAdding: true });
        }

        model.technicalSupportNumbersString = this.getPhonesString(model.technicalSupportNumbers);

        this.setCurrentItem(model, this.state.currentItem.rowKey);
    }

    // Define edit state for technical support number.
    setTechnicalSupportNumberEditState = (isEdited: boolean): void => {
        this.setState({ isTechnicalSupportNumberEdited: isEdited });
    }
    //#endregion

    //#region Call center numbers data grid functions
