                let technicalSupportProperty: PropertyModel = { name: "SupportPhones", phones: this.getPhoneModels(this.state.currentItem.model.technicalSupportNumbers) };
                let callCenterProperty: PropertyModel = { name: "CallCenter", phones: this.getPhoneModels(callCenterNumbers) };

