    // Define edit state for call center number.
    setCallCenterNumberEditState = (isEdited: boolean): void => {
        this.setState({ isCallCenterNumberEdited: isEdited });
    }
    //#endregion

    checkUniqueNumber(numbers: PhoneModel[], number: string): boolean {
        for (let i = 0; i < numbers.length; i++) {
            if (numbers[i].number == number)
                return false;
        }

        return true;
    }
