import * as React from 'react';
import getImage from './Images';

export default function StateDeviceImage(data: any) {
    return (
        <table>
            <tr>
                <td><img src={data && getImage(data.data.stateDeviceCode)} /></td>
                <td />
                <td>{data && data.displayValue}</td>
            </tr>
        </table>
    );

}
